<template>
  <div>
    <div v-loading="loading">
      <template v-if="form.conditions.length > 0">
        <Draggable
          v-model="dataToDrag"
          handle=".drag_indicator"
          :disabled="disabledUniqueAndMac"
          @change="sortingCondition">
          <div
            v-for="(condition, indexCondition) in dataToDrag"
            :class="[
              !main?.synced ? 'bg-opacity-10' : 'bg-opacity-50',
              'border rounded-lg mb-4',
              'border-grey-01 border-opacity-50 bg-grey-01',
              'py-3 px-5',
              'condition__draggable group',
              'hover:border-opacity-80 transition-all ease-in-out duration-300'
            ]"
            :key="indexCondition">
            <div v-if="condition" class="flex w-full gap-4">
              <span
                v-if="!main?.synced"
                :class="disabledUniqueAndMac && 'opacity-30 cursor-not-allowed'"
                class="material-icons text-grey-04 text-4xl cursor-move drag_indicator">
                drag_indicator
              </span>
              <div class="flex-1">
                <p class="font-semibold mb-2">
                  {{ condition.name }}
                </p>
                <div v-html="condition.term" />
                <ul class="list">
                  <template
                    v-for="(term, indexTerm) in (condition.terms || [])"
                    :key="indexTerm">
                    <li
                      v-if="!!term && !!term.length"
                      class="text-grey-04"
                      v-html="sanitize(term)"
                    />
                  </template>
                </ul>
              </div>
              <div v-if="!disabledUniqueAndMac" class="lg:opacity-0 group-hover:opacity-100 transition-opacity">
                <div class="flex items-center justify-end">
                  <Button
                    color="teal"
                    class="mr-3 text-sm"
                    dense
                    icon
                    @click="
                      openModalUpdate(
                        condition,
                        indexCondition
                      )
                    ">
                    <span class="material-icons text-sm">edit</span>
                  </Button>
                  <Button
                    v-if="!main?.synced"
                    color="red"
                    dense
                    @click="openModalDelete(condition, indexCondition)">
                    <span class="material-icons text-sm">
                      delete
                    </span>
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </Draggable>
      </template>
      <template v-else>
        <div class="text-center border-2 border-grey-01 border-dashed px-6 py-8 rounded-lg">
          <p class="text-grey-02 max-w-lg mx-auto w-full text-sm">
            Use the "Add New Condition" button below to create your item conditions from scratch, or save time and use our Preset Conditions
          </p>
        </div>
      </template>
      <div
        v-if="!main?.synced"
        class="my-4">
        <Button
          class="block w-full"
          color="teal"
          :disabled="!formCategory.categories || !formCategory.categories.length || !formCategory.group_name || disabledUniqueAndMac"
          @click="openModalAdd">
          <span class="flex items-center justify-center">
            <i class="material-icons icon-xxs mr-2">add</i> Add New Condition
          </span>
        </Button>
      </div>
    </div>
    <ConditionModalForm
      v-model="modal.condition"
      :condition="selected"
      :conditions="form.conditions"
      :is-edit="isEdit"
      @save="saveCondition"
      @cancel="closeModal"
    />
    <ConditionModalPreview
      v-model="modal.preview"
      :condition="selected"
    />
    <ConditionModalDelete
      v-model="modal.delete"
      @cancel="closeModal"
      @delete="deleteCondition"
    />
  </div>
  <div
    v-for="n in validation.conditions.$errors.length"
    :key="n"
    class="bg-red-lighten rounded-lg py-4 px-6 my-6">
    <span class="text-red-base block">
      Please add at least 1 condition
    </span>
  </div>
</template>

<script>
import { defineComponent, computed, ref } from 'vue'
import { useRoute } from 'vue-router'
import { sanitize } from 'utils/dom'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'

import Button from 'bbUi/components/button/button.vue'
import { VueDraggableNext as Draggable } from 'vue-draggable-next'
import ConditionModalPreview from '../Modal/Preview.vue'
import ConditionModalDelete from '../Modal/Delete.vue'
import ConditionModalForm from '../Modal/Condition.vue'
import { minLength } from '@vuelidate/validators'

export default defineComponent({
  name: 'ConditionMappingList',
  components: {
    ConditionModalPreview,
    ConditionModalDelete,
    ConditionModalForm,
    Draggable,
    Button
  },
  props: {
    mixin: {
      type: Object,
      default: undefined
    },
    preset: {
      type: Object,
      default: undefined
    },
    category: {
      type: Object,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    refSource: {
      type: Object,
      default: undefined
    },
    priceEditor: {
      type: Boolean,
      default: false
    },
    priceEditorCategory: {
      type: Object,
      default: undefined
    }
  },
  emits: [
    'update:modelValue',
    'update:modal',
    'update:condition',
    'openModal',
    'setCondition',
    'sortingCondition',
    'confirmSave',
    'confirmDelete'
  ],
  setup(props, ctx) {
    const main = props.main
    const {
      form,
      loading,
      modal,
      addCondition,
      editCondition,
      deleteCondition: _deleteCondition,
      sortingCondition: _sortingCondition
    } = props.mixin
    const { form: formPreset, isValidJSON } = props.preset
    const { form: formCategory } = props.category
    const dataToDrag = computed({
      get: () => [...form.value.conditions],
      set: (value) => {
        form.value.conditions = [...value]
      }
    })
    const isEdit = ref(false)
    const selected = ref(null)
    const rules = {
      conditions: { required, minLength: minLength(1) }
    }
    const route = useRoute()
    const disabledUniqueAndMac = computed(() => {
      const isMac = [...formCategory.categories].findIndex(category => category.is_mac)
      return !!route?.query?.unique_id && isMac >= 0
    })

    const validation = useVuelidate(rules, form, {
      $lazy: true
    })

    const submit = async () => {
      await validation.value.conditions.$validate()

      return validation.value.conditions.$error ? false : true
    }

    const openModalAdd = () => {
      isEdit.value = false
      selected.value = null
      modal.value.condition = true
    }
    const openModalUpdate = (item, index) => {
      isEdit.value = true
      selected.value = { ...item, index }
      modal.value.condition = true
    }
    const openModaPreview = (item, index) => {
      selected.value = { ...item, index }
      modal.preview = true
    }
    const openModalDelete = (item, index) => {
      isEdit.value = true
      selected.value = { ...item, index }
      modal.value.delete = true
    }
    const closeModal = () => {
      isEdit.value = false
      selected.value = null
      Object.keys(modal.value).forEach(item => {
        modal.value[item] = false
      })
    }
    const saveCondition = async (value, source = false) => {
      modal.value.condition = false
      if (!!isEdit.value) {
        if (!!main.id && !!value.id) {
          await editCondition({ id: value.id, ...value })
          await main.getDetail({
            id: main.id,
            priceSync: {
              uniqueId: main.uniqueId,
              sync: main.sync
            }
          })
          handleInitEdit()
        } else form.value.conditions[value.index] = {...form.value.conditions[value.index], ...value}
      } else {
        if (!!main.id) {
          await addCondition({ id: main.id, ...value })
          await main.getDetail({
            id: main.id,
            priceSync: {
              uniqueId: main.uniqueId,
              sync: main.sync
            }
          })
          handleInitEdit()
        } else form.value.conditions.push(value)
      }
      if (!source) {
        props.refSource.handleRelabeling()
        // refresh source list here
      }
      closeModal()
    }
    const deleteCondition = async () => {
      modal.value.delete = false
      props.refSource.loading = true
      props.refSource.deleteActiveCondtion(selected.value)
      if (!!main.id) {
        await _deleteCondition(selected.value.id)
        await main.getDetail({
          id: main.id,
          priceSync: {
            uniqueId: main.uniqueId,
            sync: main.sync
          }
        })
        handleInitEdit()
      } else {
        form.value.conditions.splice(selected.value.index, 1)
      }

      props.refSource.loading = false
      closeModal()
    }
    const sortingCondition = async (value) => {
      const idMoved = value.moved?.element?.id
      const sorted = [...form.value?.conditions].map((item, index) => ({
        id: item.id,
        sort: index
      }))

      if (!!main.id) await _sortingCondition(sorted)
    }
    const previewCondition = () => {

    }
    const handleInitEdit = () => {
      form.value.conditions = [...main.formEdit.conditions].map(item => {
        let terms = []
        try {
          if (isValidJSON(item.condition_term)) {
            const parsedTerms = JSON.parse(item.condition_term)
            terms = Array.isArray(parsedTerms) ? parsedTerms : []
          }
        } catch (error) {
          console.error('Error parsing condition terms:', error)
          terms = []
        }

        return ({
          ...item,
          terms: !!terms ? terms.splice(1, terms.length - 1) : [''],
          term: !!terms ? terms[0] : item.condition_term
        })
      })
    }

    if (!!main.id) {
      handleInitEdit()
    }

    return {
      main,
      form,
      modal,
      isEdit,
      loading,
      formPreset,
      dataToDrag,
      formCategory,
      sanitize,
      selected,
      validation,
      submit,
      openModalAdd,
      openModalUpdate,
      openModaPreview,
      openModalDelete,
      closeModal,
      saveCondition,
      deleteCondition,
      sortingCondition,
      previewCondition,
      disabledUniqueAndMac
    }
  }
})
</script>
