<template>
  <div :class="isOverlays ? 'hidden' : 'block'">
    <Modal
      v-model="modal"
      size="xl"
      hide-header
      no-close>
      <div class="border-b border-grey-01 px-10 pb-5 mb-5 -mx-12">
        <div
          v-if="nameTitle"
          class="flex items-center gap-x-2">
          <h6 class="font-semibold">
            Price Sync - {{ nameTitle }} Category
          </h6>
          <Tooltip placement="top">
            <template #content>
              <div class="max-w-sm text-sm">
                Price Sync is a tool that allows you to automatically price your items using one or more of the data sources below. You can create rules to match, mark up, or mark down buyback prices based on a set percentage or fixed amount.
              </div>
            </template>
            <i class="material-icons cursor-default">info</i>
          </Tooltip>
        </div>
        <h6 v-else class="font-semibold">Price Sync</h6>
      </div>
      <div
        v-if="messageCase && itemSelected?.children?.length > 0"
        class="bg-blue-01 text-blue-04 bg-opacity-50 rounded-md px-4 py-1.5 flex gap-x-4"
        :class="editing && noRules ? 'mb-4' : 'mb-10'"
        >
        <span class="material-icons">info</span>
        <p>{{ messageCase }}</p>
      </div>
      <div class="space-y-10">
        <div
          v-if="editing && noRules"
          v-loading="loading?.check || loading?.all || loadingPricing?.all"
          class="bg-teal-01 bg-opacity-50 rounded-md px-4 py-1.5 flex items-center gap-x-4">
          <span class="material-icons">info</span>
          <p>Price sync has been edited for some items in this category.</p>
        </div>
        <!-- Step 1 -->
        <div
          v-loading="loading?.check || loading?.all || loadingPricing?.all"
          class="space-y-6">
          <div class="flex items-center gap-x-4">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">1</div>
            <span class="font-medium text-lg">Select your data source</span>
          </div>
          <div class="space-y-4">
            <div class="space-y-6">
              <div class="flex flex-wrap gap-x-4 gap-y-2">
                <div class="text-green-04 flex items-center gap-x-1 text-sm bg-green-01 bg-opacity-80 rounded-full px-4">
                  <span class="material-icons">check_circle</span>
                  <p class="whitespace-pre-wrap">Support all countries</p>
                </div>
                <div class="text-green-04 flex items-center gap-x-1 text-sm bg-green-01 bg-opacity-80 rounded-full px-4">
                  <span class="material-icons">check_circle</span>
                  <p class="whitespace-pre-wrap">Automatically converts the currency</p>
                </div>
                <div
                  whitelabel="remove"
                  class="text-green-04 flex items-center gap-x-1 text-sm bg-green-01 bg-opacity-80 rounded-full px-4">
                  <span class="material-icons">check_circle</span>
                  <p class="whitespace-pre-wrap">Supports using up to 5 data sources at the same time</p>
                </div>
              </div>
              <div class="flex justify-end gap-x-4">
                <Tooltip placement="top">
                  <template #content>
                    <div class="max-w-xs text-sm">
                      If there are no sources available for your region, you can use sources from other regions, as the currency will automatically convert to your local currency, and you can adjust the pricing rules accordingly.
                    </div>
                  </template>
                  <div>
                      <div class="w-64">
                        <SelectCountry
                          :selected="selectedCountryFilter"
                          :countries="countryOptions"
                          placeholder="Select Region"
                          rounded
                          noBorder
                          @change="handleCountryFilterChange"
                        />
                      </div>
                  </div>
                </Tooltip>
                <Tooltip placement="top">
                  <template #content>
                    <div class="max-w-xs text-sm">
                      If there are no sources available for your region, you can use sources from other regions, as the currency will automatically convert to your local currency, and you can adjust the pricing rules accordingly.
                    </div>
                  </template>
                  <div class="w-64">
                    <SelectCurrency
                      :selected="selectedCurrencyFilter"
                      :currencies="currencyOptions"
                      placeholder="Select Currency"
                      rounded
                      @change="handleCurrencyFilterChange" />
                  </div>
                </Tooltip>
              </div>
              <div class="border border-grey-01 rounded-md relative">
                <span class="bg-white text-sm absolute rounded-full px-2 py-1 -top-4 left-3 text-grey-03" style="z-index: 1;">Data Source</span>
                <div class="max-h-80 overflow-y-auto overflow-x-hidden py-6 px-4">
                  <Tooltip
                    v-for="(source, n) in filteredSources"
                    :key="n"
                    placement="top-start"
                    :disabled="!source.disabled">
                    <template #content>
                      <p class="max-w-xl text-sm font-normal">As a price source on the platform, you cannot use another price source that is also using the platform; you are only permitted to use scraping sources like BuybackBoss, BuybackWorld, ItsWorthMore, and Atlas.</p>
                    </template>
                    <template #default>
                      <div
                        :class="source.disabled ? 'opacity-50' : ''"
                        class="hover:bg-grey-01 hover:bg-opacity-50 group flex flex-wrap md:flex-nowrap items-center justify-between px-4 md:py-0 py-4 md:h-16 gap-x-4 gap-y-2 md:gap-y-0">
                        <div class="flex gap-x-4">
                          <Checkbox
                            v-model="source.checked"
                            :value="true"
                            :value-off="false"
                            :disabled="(sourceSelected?.length >= 5 && !source.checked) || source.disabled"
                            :class="[
                              'font-medium',
                              !source.checked && !source.disabled && sourceSelected?.length < 5 && 'checkbox-blink'
                            ]"
                            color="teal"
                            view="smooth"
                            type="checkbox"
                            block
                            @change="handleChangeSource">
                            <span class="lowercase">{{ source.name }}</span>
                          </Checkbox>
                        </div>
                        <div class="flex flex-wrap md:flex-nowrap justify-end items-center gap-x-2 md:gap-y-0 gap-y-2">
                          <div
                            v-if="source.is_support_sell_instantly"
                            class="bg-green-01 text-green-04 text-sm rounded-full px-4 py-1 flex items-center">
                            <span class="material-icons">flash_on</span>
                            Support Sell Instantly
                          </div>
                          <div class="group-hover:bg-white bg-grey-01 bg-opacity-20 px-3 py-1 rounded-full">
                            Data Refresh Frequency:
                            <span class="font-semibold">{{ source.refresh_frequency }}</span>
                          </div>
                          <div
                            class="group-hover:bg-white bg-grey-01 bg-opacity-20 px-3 py-1 rounded-full">
                            Item Coverage:
                            <span
                              v-if="!source.loading"
                              class="font-semibold">
                              {{ `${source.price_coverage}%` }}
                            </span>
                            <span
                              v-else
                              v-loading="source.loading"
                              class="font-semibold">
                              {{ `...%` }}
                            </span>
                          </div>
                          <div class="flex items-center gap-4 mx-2 text-sm text-grey-03">
                            <tooltip placement="top">
                              <template #content>
                                {{ source.country_name }}
                              </template>
                              <img
                                :src="`/flag/${source.country_code?.toLowerCase()}.svg`"
                                width="20"
                                :alt="source.country_name"
                                class="rounded"
                              />
                            </tooltip placement="top">
                            <div>
                              <span class="text-xl font-medium">{{ source.currency_symbol }}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </template>
                  </Tooltip>
                </div>
              </div>
            </div>
            <!-- hide intercom panel trigger -->
            <div
              v-if="!!connectData"
              class="flex items-center justify-between">
              <span class="text-sm">Custom ({{ 'Connect Any Data'/* connectData.name */ }})</span>
              <button
                class="rounded-full bg-teal-04 px-6 py-0.5 text-white flex items-center justify-center gap-x-4 text-sm"
                @click="handleIntercomeMessage">
                Learn More
                <i class="material-icons">chevron_right</i>
              </button>
            </div>
            <!-- hide intercom panel trigger -->
          </div>
        </div>
        <!-- Step 1 -->

        <!-- Step 2 -->
        <div
          :class="!!sourceSelected?.length ? 'opacity-100' : 'opacity-30'"
          class="space-y-6">
          <div class="flex items-center gap-x-4">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">2</div>
            <span class="font-medium text-lg">Map The Condition</span>
          </div>
          <div>
            <BulkPriceEditorMappingMethod
              :key="keyForm"
              ref="refMappingMethod"
              v-model:type="mappingType"
              :category="itemSelected"
              :pricing-source="sourceSelected"
              :editing="editing"
              :rules="rules"
              :different-rules="isDifferentRules"
              :company-source="priceSharing"
              @update:type="handleSelectedMethod"
              @manual-mapping="handleManualMapping"
              @update:different="(e) => isDifferentRules = e"
              @selectedCondition="handleValidateSelected"
            />
          </div>
        </div>
        <!-- Step 2 -->

        <!-- Step 3 -->
        <div
          v-if="!isDifferentRules && !priceSharing && !isRemoveStepThree"
          :class="[
            !!sourceSelected?.length && !!mappingType ? 'opacity-100' : 'opacity-30',
            'space-y-2'
          ]">
          <div class="flex items-center gap-x-4 mb-5">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">3</div>
            <span class="font-medium text-lg">Set your pricing rules</span>
          </div>
          <div
            class="max-w-lg">
            <RadioGroup
              v-model="type"
              class="radio--full"
              :disabled="!sourceSelected?.length || !mappingType"
              @change="handleChangeType">
              <RadioButton label="3">Match</RadioButton>
              <RadioButton label="2">Markup</RadioButton>
              <RadioButton label="1">Markdown</RadioButton>
            </RadioGroup>
          </div>
          <div
            v-if="type != 3"
            class="grid grid-cols-2 max-w-lg gap-2">
            <RadioGroup
              v-model="amountType"
              class="radio--full mb-4"
              @change="handleChangeType">
              <RadioButton label="1">
                % Percentage
              </RadioButton>
              <RadioButton label="0">
                {{ me.currency_symbol }} Fixed
              </RadioButton>
            </RadioGroup>
            <Tooltip
              v-model="showTooltip"
              placement="top"
              manual>
              <template #content>
                <p class="max-w-xs text-sm">Items priced at {{ me.currency_prefix == 1 ? `${me.currency_symbol}0` : `0${me.currency_symbol}` }} are not subject to pricing rules.</p>
              </template>
              <Input
                v-model="amountPrice"
                :right="amountPrice == '0' ? false : true"
                :min="0"
                :max="amountType == 1 ? (type == 2 ? undefined : 100) : 'auto'"
                type="number"
                class="w-full"
                icon-block
                icon
                @focus-in="showTooltip = true"
                @focus-out="showTooltip = false">
                <template #icon>
                  <span
                    v-if="amountType == '0'"
                    class="text-grey-03">
                    {{ me.currency_symbol }}
                  </span>
                  <span
                    v-else
                    class="text-grey-03">
                    %
                  </span>
                </template>
              </Input>
            </Tooltip>
          </div>
        </div>
        <!-- step 3 -->
      </div>

      <template #footer>
        <div class="flex justify-between items-center">
          <Button
            :loading="loadingRule || loading.check || loadingPricing.all"
            view="outlined"
            color="red"
            @click="handleCancel">
            Cancel
          </Button>
          <Button
            :loading="loadingRule || loading.check || loadingPricing.all"
            :disabled="editing ? false : isUnavailable || editing || !sourceSelected?.length || sourceSelected?.length > 5"
            color="teal"
            class="ml-auto small"
            @click="handleApply">
            Apply
          </Button>
        </div>
      </template>
    </Modal>
  </div>
  <BulkPriceEditorMappingCondition
    v-model="modalFormCondition"
    :category="itemSelected"
    :pricing-source="sourceSelected"
    @back="handleBackMappingCondition"
    @save="handleSaveMappingCondition"
  />
</template>

<script>
import { defineComponent, computed, watch, ref } from 'vue'
import { useCompanyPricingSources, useCategories, useDevice } from 'composable/pricing'
import { useFeatureFlag } from 'composable/whitelabel'
import { useUser } from 'composable/user'
import { deepClone } from 'utils/deep-clone'
import SelectCountry from '../../SelectCountry.vue'
import SelectCurrency from '../../SelectCurrency.vue'
// import { ElMessage } from 'element-plus'

import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import BulkPriceEditorMappingMethod from 'components/BulkPriceEditor/Mapping/FormMethod/index.vue'
import BulkPriceEditorMappingCondition from 'components/BulkPriceEditor/Mapping/FormCondition.vue'
import { Tooltip } from 'chart.js'

export default defineComponent({
  name: 'BulkPriceEditorSources',
  components: {
    Modal,
    Input,
    Button,
    Checkbox,
    SelectCountry,
    SelectCurrency,
    BulkPriceEditorMappingMethod,
    BulkPriceEditorMappingCondition
  },
  props: {
    modal: {
      type: Boolean,
      default: false
    },
    editing: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    item: {
      type: Object,
      default: undefined
    },
    overlayModals: {
      type: [Boolean, Array],
      default: false
    },
    priceSharing: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modal', 'update:editing', 'update:item', 'cancel', 'apply'],
  setup(props, ctx) {
    const showTooltip = ref(false)
    const refMappingMethod = ref()
    const type = ref('3')
    const amountType = ref('1')
    const amountPrice = ref(0)
    const keyForm = ref('')
    
    // Filter states
    const selectedCountryFilter = ref('')
    const selectedCurrencyFilter = ref('')
    
    const sourceSelected = computed(() => [...filteredSources.value].filter(item => item.checked))
    const typeSelectedSource = ref()
    const modal = computed({
      get: () => props.modal,
      set: (val) => ctx.emit('update:modal', val)
    })
    const editing = computed({
      get: () => props.editing,
      set: (val) => ctx.emit('update:editing', val)
    })
    const itemSelected = computed({
      get: () => props.item,
      set: (val) => ctx.emit('update:item', val)
    })
    const nameTitle = computed(() => {
      const names = props?.item?.level_names?.length ? [...props.item.level_names] : []
      return names?.length ? names?.shift() : ''
    })
    const mappingType = ref(null)
    const rules = ref({})
    const modalFormCondition = ref(false)
    const isOverlays = computed(() => {
      if (!!modalFormCondition?.value) return true

      if (Array.isArray(props.overlayModals)) {
        return props.overlayModals.includes(true)
      }

      return props.overlayModals
    })
    const loadingRule = computed(() => props.loading)

    const { me } = useUser()
    const {
      state,
      action,
      loading,
      actionCoverage,
      actionCheckDevicePricingSource
    } = useCompanyPricingSources()
    const { features } = useFeatureFlag()
    const {
      loading: loadingPricing,
      actionAll: fetchPricing,
      actionConditionUpdate,
      getRule
    } = useCategories()
    const { actionSetLevel } = useDevice()
    const sources = ref([])
    const connectData = ref(undefined)
    const noRules = ref(false)
    const flagDefault = computed(() => {
      const flag = me.value.currency.split('')
      flag.pop()
 
     return flag.join('').toLowerCase()
    })
    const isUnavailable = ref(true)
    const isDifferentRules = ref(false)
    const isRemoveStepThree = ref(false)
    
    const countryOptions = computed(() => {
      const uniqueCountries = []
      const countryMap = new Map()
      
      sources.value.forEach(source => {
        if (source.country_code && source.country_name && !countryMap.has(source.country_code)) {
          countryMap.set(source.country_code, {
            country_code: source.country_code,
            nicename: source.country_name,
            iso3: source.country_code
          })
        }
      })
      
      return Array.from(countryMap.values()).sort((a, b) => a.nicename.localeCompare(b.nicename))
    })
    
    const currencyOptions = computed(() => {
      const uniqueCurrencies = []
      const currencyMap = new Map()
      
      sources.value.forEach(source => {
        if (source.currency && !currencyMap.has(source.currency)) {
          currencyMap.set(source.currency, {
            currency_code: source.currency,
            country_code: source.country_code,
            name: source.currency
          })
        }
      })
      
      return Array.from(currencyMap.values()).sort((a, b) => a.currency_code.localeCompare(b.currency_code))
    })
    
    const filteredSources = computed(() => {
      let filtered = [...sources.value]
      
      if (selectedCountryFilter.value) {
        filtered = filtered.filter(source => 
          source.country_code?.toLowerCase() === selectedCountryFilter.value.toLowerCase()
        )
      }
      
      if (selectedCurrencyFilter.value) {
        filtered = filtered.filter(source => 
          source.currency?.toLowerCase() === selectedCurrencyFilter.value.toLowerCase()
        )
      }
      
      return filtered
    })
    
    const handleCountryFilterChange = (country) => {
      const selectedCode = country ? country.country_code?.toLowerCase() : ''
      const currentCode = selectedCountryFilter.value?.toLowerCase()
      if (currentCode && selectedCode === currentCode) {
        selectedCountryFilter.value = ''
      } else {
        selectedCountryFilter.value = selectedCode
      }
    }
    let currencyProcessing = false

    const handleCurrencyFilterChange = (currency) => {
      if (currencyProcessing) return
      currencyProcessing = true
      const selectedCode = currency ? currency.currency_code?.toLowerCase() : ''
      const currentCode = selectedCurrencyFilter.value?.toLowerCase()
      if (currentCode && selectedCode === currentCode) {
        selectedCurrencyFilter.value = ''
      } else {
        selectedCurrencyFilter.value = selectedCode
      }
      setTimeout(() => {
        currencyProcessing = false
      }, 50)
    }
    
    const clearFilters = () => {
      selectedCountryFilter.value = ''
      selectedCurrencyFilter.value = ''
    }
    
    const handleManualMapping = () => {
      modalFormCondition.value = true
    }
    const handleValidateSelected = (e) => {
      isRemoveStepThree.value = e?.isAllDuplicate || false

      if (!!e.selected.length) isUnavailable.value = false
        else isUnavailable.value = true
    }
    const handleReset = () => {
      type.value = '3'
      amountType.value = '1'
      amountPrice.value = 0
      mappingType.value = null
      sources.value = []
      clearFilters()
      modal.value = false
      isRemoveStepThree.value = false
    }
    const handleCancel = () => {
      if (loadingRule.value || loading.check || loadingPricing.all) return
      handleReset()
      ctx.emit('cancel')
    }
    const handleSetLevel = () => actionSetLevel(itemSelected.value)
    const handleGetRule = async () => {
      loadingPricing.all = true
      noRules.value = false
      try {
        const response = await getRule({
          category_id: itemSelected.value?.id,
          level: handleSetLevel()
        })

        const {
          rules: _rules,
          selected_sources,
          sources_conditions: sourcesConditions,
          device_mapping_type: mappingType
        } = response

        if (selected_sources){
          typeSelectedSource.value = selected_sources?.length > 1 ? 'multiple' : 'single'
        }

        const selectedSources = _rules ? selected_sources : null

        if (
          !selectedSources?.length ||
          !Object.keys(sourcesConditions).length ||
          !_rules?.length
        ) {
          noRules.value = true
          // return ElMessage({
          //   message: 'The rules data is incomplete!',
          //   type: 'warning'
          // })
        }

        rules.value = {
          rules: _rules,
          selectedSources,
          sourcesConditions,
          mappingType
        }

        if (rules.value?.selectedSources?.length) {
          rules.value.selectedSources.forEach(source => {
            const key = sources.value.findIndex(item => item.sub_domain === source)
            if (sources.value[key]) sources.value[key] = {
              ...sources.value[key],
              checked: true
            }
          })
        }

        if (mappingType === 'auto' || rules?.value?.selectedSources?.length < 2) {
          const mapRule = [...rules.value.rules].shift()
          type.value = mapRule.type
          amountType.value = mapRule.amount_type,
          amountPrice.value = mapRule.amount_price
        }
      } catch (error) {
        console.error('Error fetching rule:', error)
        noRules.value = true
      } finally {
        loadingPricing.all = false
      }
    }
    const validateNewRules = (newRules = [], existingRules = []) => {
      const compareKeys = [
        "source_condition_id",
        "member_condition_id",
        "source",
        "type",
        "amount_type",
        "amount_price"
      ]

      // Fallback error if existingRules from api is not Array
      if (!Array.isArray(existingRules)) return true
      if (!Array.isArray(newRules)) return true

      if (newRules?.length !== existingRules?.length) return true

      // 1. Normoalize: take key wiil be comapares
      const normalize = (arr) => {
        return arr.map(item => {
          const normalized = {}
          for (let key of compareKeys) {
            normalized[key] = item[key] ?? null
          }
          return normalized
        }).sort((a, b) => {
          // 2. Sort by all keys to ensure position-based comparison
          return compareKeys.map(k => {
            if (a[k] < b[k]) return -1
            if (a[k] > b[k]) return 1
            return 0
          }).find(res => res !== 0) || 0
        })
      }

      const normNew = normalize(newRules)
      const normExist = normalize(existingRules)

      // 3. Compare item per item
      for (let i = 0; i < normNew.length; i++) {
        for (let key of compareKeys) {
          if (normNew[i][key] != normExist[i][key]) {
            return true // different
          }
        }
      }

      return false // no diff
    }
    const handleApply = async () => {
      const isValid = await refMappingMethod?.value?.onSubmit()
      if (!isValid) return undefined
      const mappingType = refMappingMethod?.value?.data?.method === 1 ? 'auto' : 'manual'

      const conditions = Array.isArray(refMappingMethod?.value?.data?.conditions) 
        ? [...refMappingMethod.value.data.conditions] 
        : []

      const mapping = conditions.map(item => {
        if (!(mappingType === 'manual' ? !!item.source_condition_id : !!item.id)) return null

        const sourceConditionId = mappingType === 'manual' 
          ? Array.isArray(item?.source_condition_id) 
            ? item.source_condition_id 
            : [item.source_condition_id] 
          : [item.id]
        
        const sources = sourceSelected.value?.length > 1
          ? Array.isArray(item?.source) 
            ? item.source 
            : [item?.source?.sub_domain] 
          : [deepClone(sourceSelected.value)?.shift()?.sub_domain]

        const result = sourceConditionId.map((conditionId, index) => ({
          source_condition_id: conditionId?.id || conditionId,
          member_condition_id: mappingType === 'manual' ? item.id : null,
          source: sources[index]?.sub_domain || sources[0],
          type: sourceSelected.value?.length > 1 || props.priceSharing || item?.is_duplicate ? Number(item.pricingRuleType) : Number(type.value),
          amount_type: sourceSelected.value?.length > 1 || props.priceSharing || item?.is_duplicate ? item.amountType : amountType.value,
          amount_price: sourceSelected.value?.length > 1 || props.priceSharing || item?.is_duplicate ? item.amountPrice : amountPrice.value
        }))

        return result
      }).flat().filter(Boolean)

      const data = {
        category_id: itemSelected.value.id,
        mapping_type: mappingType,
        level: handleSetLevel(),
        mapping,
      }

      const isNewRules = props?.editing 
        ? validateNewRules(data.mapping, rules?.value?.rules) 
          : true

      await Promise.all([
        ctx.emit('apply', {
          data,
          isNew: isNewRules
        }, handleReset)
      ])
    }
    const handleBackMappingCondition = () => {}
    const handleSaveMappingCondition = () => {
      refMappingMethod.value.handleSelectMethod(2)
    }
    const handleIntercomeMessage = () => {
      window.CustomerSupport.use('showNewMessage', 'Hi, I would like to learn more about your custom data services')
    }
    const handleInitValue = async () => {
      sources.value = [...state.value].map(item => ({
        ...item,
        loading: true,
        checked: false,
        disabled: props.priceSharing && !item.is_scraper
      }))
      await Promise.all([
        sources.value.forEach(async (item) => {
          item = await actionCoverage({
            category_id: itemSelected?.value.id,
            level: handleSetLevel(),
            source: item.sub_domain
          }, item, (res) => {
            item.price_coverage = parseFloat((res?.price_coverage || 0).toFixed(2))
          })
        })
      ])
    }
    const handleSelectedMethod = (e) => {
      if (e === 1) isUnavailable.value = false
        else isUnavailable.value = true
    }
    const handleChangeSource = () => {
      mappingType.value = null
      refMappingMethod.value?.handleReset()
    }

    const messageCase = computed(() => {
      // Case 1: One Source, Same Rules
      if (typeSelectedSource.value === 'single' && (editing.value && !noRules.value)) {
        return `You're using the same data source and pricing rules for all your items. To use different data sources for different items, start by selecting more than one data source from the list below, map the conditions, set pricing rules, and click Apply. Then, click the arrow next to the brand name on the left to expand the list. Hover over an item with active price sync to reveal the Edit button, and click it to select your
          preferred data source for that item.`
      }
      // Case 2: Multiple Source, Same Rules
      if (typeSelectedSource.value === 'multiple' && (editing.value && !noRules.value)) {
        return "You're using multiple data sources for various items. To view them, click 'Cancel,' then click the arrow next to the brand name on the left to expand the list. Next, hover over an item with an active price sync to reveal the edit button, then click it to view its data source and pricing rule. If you want to change the data source and pricing rule for all items at once, use the menu below."
      } 
      // Case 3: One Source, Different Rules
      if (typeSelectedSource.value === 'single' && (editing.value && noRules.value)) {
        const level = handleSetLevel()
        if (level === 'device') {
          return "You're using multiple data sources and different pricing rules for various items. To view them, click 'Cancel,' then click the arrow next to the brand name on the left to expand the list. Next, hover over an item with an active price sync to reveal the edit button, then click it to view its data source and pricing rule. If you want to change the data source and pricing rule for all items at once, use the menu below."
        } else {
          return "You're using different pricing rules for this item. To view it, click 'Cancel, then click the arrow next to the brand name on the left to expand the list. Next, hover over the item to reveal the edit button, then click it to view its data source and pricing rule. If you want to change the data source and pricing rule for all item variants at once, use the menu below."
        }
      }
      // Case 4: Multiple Source, Different Rules
      if (typeSelectedSource.value === 'multiple' && (editing.value && noRules.value)) {
        const level = handleSetLevel()
        if (level === 'device') {
          return "You're using multiple data sources and different pricing rules for various items. To view them, click 'Cancel', then click the arrow next to the brand name on the left to expand the list. Next, hover over an item with an active price sync to reveal the edit button, then click it to view its data source and pricing rule. If you want to change the data source and pricing rule for all items at once, use the menu below."
        } else {
          return "You're using different pricing rules for this item. To view it, click 'Cancel, then click the arrow next to the brand name on the left to expand the list. Next, hover over the item to reveal the edit button, then click it to view its data source and pricing rule. If you want to change the data source and pricing rule for all item variants at once, use the menu below."
        }
      }
    })
    const handleChangeType = (e) => {
      // Always reset amountPrice to 0 when type is Match (3)
      if (type.value == 3) {
        amountPrice.value = 0
        return // No need to proceed with other validations
      }

      // Switching from Fixed to Percentage (Markdown only), normalize large value to 100
      if (amountType.value == 1 && type.value == 1 && Number(amountPrice.value)> 100) {
        amountPrice.value = 100
      }
    }

    watch(() => modal.value, async () => {
      if (!!modal.value) {
        isUnavailable.value = true
        await action({
          category_id: itemSelected.value?.id
        })
        await handleInitValue()
        if (!!editing.value) {
          isUnavailable.value = false
          await handleGetRule()
        }
      }
    })

    watch(sourceSelected, (newVal) => {
      const newKey = newVal.map(i => i.sub_domain).join('-')
      keyForm.value = newKey
    }, { immediate: true })

    return {
      me,
      type,
      rules,
      modal,
      state,
      action,
      editing,
      loading,
      sources,
      noRules,
      nameTitle,
      showTooltip,
      modalFormCondition,
      mappingType,
      connectData,
      loadingRule,
      amountType,
      isOverlays,
      flagDefault,
      amountPrice,
      itemSelected,
      sourceSelected,
      isUnavailable,
      handleCancel,
      handleApply,
      loadingPricing,
      refMappingMethod,
      isDifferentRules,
      handleInitValue,
      handleManualMapping,
      handleValidateSelected,
      handleIntercomeMessage,
      handleSelectedMethod,
      handleBackMappingCondition,
      handleSaveMappingCondition,
      messageCase,
      isRemoveStepThree,
      typeSelectedSource,
      handleChangeSource,
      handleCountryFilterChange,
      handleCurrencyFilterChange,
      currencyOptions,
      countryOptions,
      selectedCountryFilter,
      selectedCurrencyFilter,
      filteredSources,
      countryOptions,
      currencyOptions,
      clearFilters,
      handleChangeType,
      keyForm
    }
  }
})
</script>

