  animation: checkboxBlink;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
@keyframes checkboxBlink {
  from {
    box-shadow:  0px 0px 0px 3px rgba(221, 221, 221, 0.015);
  }
  to {
    box-shadow:  0px 0px 0px 0px;
  }
}
.price-coverage {
  animation: loadingCoverage;
  animation-duration: 1s;
  animation-delay: .5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
@keyframes loadingCoverage {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 0.8;
  }
}
</style>