<template>
  <Modal
    v-model="show"
    hide-header
    hide-footer
    size="xs">
    <div class="space-y-8 text-center my-8">
      <span
        :style="{
          fontSize: '8rem !important',
          lineHeight: '1'
        }"
        class="material-icons text-orange-03">
        error_outline
      </span>
      <div class="font-normal text-grey-03">
        <p>
          You have conditions with the same title already in the active conditions. If you continue, it will replace the existing condition.
        </p>
        <p>
          Are you sure you want to proceed?
        </p>
      </div>
      <div class="flex items-center justify-center gap-x-4">
        <Button
          color="grey"
          view="outlined"
          @click="handleCancel">
          Cancel
        </Button>
        <Button
          color="teal"
          @click="handleConfirm">
          Continue
        </Button>
      </div>
    </div>
  </Modal>
</template>
<script>
import { computed, defineComponent } from 'vue'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'ConditionModalCautionSource',
  components: {
    Modal,
    Button
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'cancel', 'continue'],
  setup(props, ctx) {
    const show = computed({
      get: () => props.modelValue,
      set: (val) => {
        ctx.emit('update:modelValue', val)
      }
    })
    const handleCancel = () => {
      show.value = false
      ctx.emit('cancel')
    }
    const handleConfirm = () => {
      show.value = false
      ctx.emit('continue')
    }

    return {
      show,
      handleCancel,
      handleConfirm
    }
  }
})
</script>
