<template>
  <div
    class="bulkprice-editor-mapping">
    <Methods
      v-model="data.method"
      :sources="pricingSource"
      :is-child="category.is_child"
      :editing="editing"
      :company-source="companySource"
      @select="handleSelectMethod"
    />
    <div v-if="!!data.method && !!pricingSource?.length">
      <div
        v-loading="data.loading"
        class="flex items-start justify-center"
        :style="{
          minHeight: !conditions?.length || data.loading ? '280px' : ''
        }">
        <EmptyCondition
          :conditions="conditions.length"
          :loading="data.loading"
          :available="isAvailableDevice"
          @add="handleAddConditionGroup"
        />
        <Conditions
          :key="`${data.method}-${keyCondition}`"
          ref="formConditions"
          :conditions="conditions"
          :loading="data.loading"
          :method="data.method"
          :sources="pricingSource"
          :source-conditions="sourceConditions"
          :category="category"
          :company-source="companySource"
          :currency-symbol="me.currency_symbol"
          :currency-prefix="me.currency_prefix"
          :differentRules="differentRules"
          @update:different="handleUpdateDifferent"
          @update:conditions="handleUpdateConditions"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, reactive, ref, watch } from 'vue'
import { useConditionAll } from 'composable/setting'
import { deepClone } from 'utils/deep-clone'
import { usePriceMappingMethod } from 'composable/pricing'
import { useUser } from 'composable/user'
import { useRoute } from 'vue-router'
import debounce from 'utils/debounce'

import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'components/Select.vue'
import Methods from './Methods.vue'
import EmptyCondition from './EmptyCondition.vue'
import Conditions from './Conditions.vue'

export default defineComponent({
  name: "BulkPriceEditorMappingMethod",
  components: {
    Button,
    Select,
    Input,
    Methods,
    EmptyCondition,
    Conditions
  },
  props: {
    pricingSource: {
      type: Array,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    editing: {
      type: Boolean,
      default: false
    },
    category: {
      type: Object,
      default: undefined
    },
    type: {
      type: [String, Number],
      default: undefined
    },
    rules: {
      type: Object,
      default: undefined
    },
    companySource: {
      type: Boolean,
      default: false
    },
    differentRules: {
      type: Boolean,
      default: false
    }
  },
  emits: ['manual-mapping', 'update:type', 'update:different', 'selectedCondition'],
  async setup(props, ctx) {
    const route = useRoute()
    const { me } = useUser()
    const selectCondtion = ref()
    const formConditions = ref()
    const keyCondition = ref()
    const {
      isAvailableDevice,
      actionConditionDeviceAvailability
    } = useConditionAll()
    const {
      data,
      conditions,
      sourceConditions,
      getConditions: _getConditions,
      handleExisting,
      handleWithLoading,
      handleParsingJSON
    } = usePriceMappingMethod()

    const handleAddConditionGroup = () => ctx.emit('manual-mapping')
    const updateConditions = (conditions) => {
      const flatSelected = deepClone(conditions).map(entry => entry.source).flat()

      data.conditions = deepClone(conditions).map(entry => {
        const isSelectedRow = flatSelected.some(flat => flat.member_id === entry.id)
        if (isSelectedRow) {
          entry.options = entry.options.map(option => {
            if (flatSelected.some(flat => flat.sub_domain === option.sub_domain)) {
              option.condition = option.condition.map(cond => ({
                ...cond,
                disabled: false, // !flatSelected.some(flat => flat.id === cond.id),
                already_selected_other: false
              }))
            }
            return option
          })
        } else {
          entry.options = entry.options.map(option => {
            if (flatSelected.some(flat => flat.sub_domain === option.sub_domain)) {
              option.condition = option.condition.map(cond => ({
                ...cond,
                already_selected_other: false, // flatSelected.some(flat => flat.id === cond.id),
                disabled: false // flatSelected.some(flat => flat.id === cond.id)
              }))
            }
            return option
          })
        }
        
        return entry
      })
    }
    const setSourceConditions = async (sources = undefined) => {
      const results = await Promise.all(
        props.pricingSource?.map(async source => {
          try {
            const res = props.editing && sources
              ? sources[source.sub_domain]
              : await _getConditions({
                  category_id: props.category.id,
                  sub_domain: source.sub_domain
                })
            const conditionList = Array.isArray(res) ? res : []
    
            return ({
              ...source, 
              condition: conditionList.map(i => {
                const { term, terms } = handleParsingJSON(i.condition_term)
    
                return ({
                  ...i,
                  term: term || null,
                  condition_term: terms || [],
                  sub_domain: source.sub_domain,
                  checked: false,
                  disabled: false,
                })
              })
            })
          } catch(error) {
            console.error('Error fetching source conditions:', error)
            return null
          }
        })
      )

      data.sourceConditions = (props.pricingSource?.length ?? 0) > 1
        ? results.filter(Boolean)
        : results.shift()?.condition || []
    }
    const setConditions = async (conditions, existing) => {
      const rules = props.rules?.rules || []
      const isMultipleSources = props?.pricingSource?.length > 1
      const isPossibleSameRulesOneSource = !isMultipleSources && props.pricingSource?.length == 1

      data.conditions = conditions?.map(condition => {
        const { term, terms } = handleParsingJSON(condition.condition_term)

        const rule = isMultipleSources && isPossibleSameRulesOneSource
          ? rules.filter(item => item.member_condition_id == condition.id)
          : rules.find(item => item.member_condition_id == condition.id)

        const options = isMultipleSources
          ? [...data.sourceConditions].map(i => ({
              ...deepClone(i),
              condition: i.condition.map(cond => {
                const findingRule = rules.find(e => e.member_condition_id === condition.id && e.source_condition_id === cond.id && i.sub_domain === e.source)
                const checked = !!findingRule ? true : false

                return ({
                  ...cond,
                  member_id: findingRule?.member_condition_id || undefined,
                  sub_domain: i.sub_domain,
                  already_selected_other: false,
                  ...(existing && { checked }),
                  ...(existing && { disabled: false } )
                })
              })
            }))
          : undefined
        const optionsConditions = options ? options.map(option => option.condition).flat() : undefined
        const sourceConditionId = optionsConditions ? deepClone(optionsConditions).filter(option => 
          option.member_id === condition.id && deepClone(rules).find(i => i.source === option.sub_domain) && deepClone(rules).find(i => i.source_condition_id === option.id)
        ) : undefined
        const sources = sourceConditionId ? sourceConditionId.map(i => ({
          id: i.id,
          name: i.name,
          sub_domain: i.sub_domain,
          member_id: i.member_id
        })) : null

        return {
          ...condition,
          term: term || null,
          condition_term: terms || [],
          pricingRuleType: (!!existing && (Array.isArray(rule) ? rule[0]?.type : rule?.type)) || '3',
          amountType: (!!existing && (Array.isArray(rule) ? rule[0]?.amount_type : rule?.amount_type)) || '1',
          amountPrice: (!!existing && (Array.isArray(rule) ? rule[0]?.amount_price : rule?.amount_price)) || 0,
          source_condition_id:
          (
            isMultipleSources
              ? (existing ? sourceConditionId : null)
              : null
          ) || (existing ? Array.isArray(rule) ? rule[0].source_condition_id : rule?.source_condition_id : null),
          ...({
            showOption: false,

            // Handle For Get Rule In Phase 3
            source: isMultipleSources
              ? existing
                ? sources.filter(i => i.member_id === condition.id)
                : []
              : {},
            // # Handle For Get Rule In Phase 3

            ...(isMultipleSources && { options })
          } || {
            source: (!!existing && {
              sub_domain: rule?.source,
              name: rule?.source_name,
              id: rule?.source_condition_id
            }) || {} 
          })
        }
      })
      if (existing && isPossibleSameRulesOneSource) handleUpdateConditions(data.conditions)

      if (existing && isMultipleSources) updateConditions(data.conditions)
    }
    const handleSelectMethodAuto = async () => {
      data.loading = true
      const [ source ] = props.pricingSource
      await _getConditions({
        category_id: props.category.id,
        sub_domain: source.sub_domain
      }).then(res => {
        data.conditions = res
        data.loading = false
      }).catch(() => {
        data.conditions = []
        data.loading = false
      })
    }
    const handleGetRule = async (isExisting) => {
      data.loading = true
      await setSourceConditions(props?.rules?.sourcesConditions)

      await setConditions(
        props?.category?.conditions,
        isExisting
      )

      data.loading = false
    }
    const handleSelectMethodManual = async () => {
      data.conditionIDS = []
      data.sourceConditions = []
      const method = data.method === 1 ? 'auto' : 'manual'
      const isSameMethod = props.rules?.mappingType === method
      const isExisting = handleExisting(props?.pricingSource, props.rules?.rules)

      if (
        !!props.editing &&
        !!props.rules?.selectedSources?.length &&
        !!isExisting &&
        !!isSameMethod
      ) return await handleGetRule(isExisting)

      data.loading = true
      await setSourceConditions()
      const conditions = await _getConditions({ category_id: props.category.id })
      await setConditions(conditions, isSameMethod ? isExisting : false)
      data.loading = false
    }
    const handleSelectMethod = async (type = undefined) => {
      keyCondition.value = Date.now()
      if (!props.pricingSource?.length) {
        data.loading = false
        return
      }
      if (type === 1 && props.category.is_child && !props.editing) return

      data.method = type
      ctx.emit('update:type', type || null)

      if (!type) return

      
      return type === 1 ? handleSelectMethodAuto() : handleSelectMethodManual()
    }
    const setDuplicateRuleOneSource = (data) => {
      try {
        const raw = data
        const countMap = raw.reduce((map, item) => {
          if (item.source_condition_id !== null) {
            map[item.source_condition_id] = (map[item.source_condition_id] || 0) + 1
          }
          return map
        }, {})
  
        return raw.map(item => ({
          ...item,
          is_duplicate:
            item.source_condition_id !== null &&
            countMap[item.source_condition_id] > 1
        })) ?? []
      } catch (error) {
        return data?.map(i => ({
          ...i,
          is_duplicate: false
        })) ?? []
      }
    }
    const handleUpdateConditions = (val) => {
      if (data.method == 2 && props.pricingSource.length == 1) {
        conditions.value = setDuplicateRuleOneSource(val)
      } else conditions.value = val

      const selected = deepClone(conditions.value)
        .filter(i => i.source_condition_id && 
          (Array.isArray(i.source_condition_id)
            ? i.source_condition_id.length > 0
            : ![null, undefined, {}, ''].includes(i.source_condition_id))
        ).map(i => ({ id: i.id, name: i.name, ...(i.is_duplicate && { is_duplicate: i.is_duplicate }) }));
      ctx.emit('selectedCondition', {
        selected,
        ...(data.method == 2 && props.pricingSource.length == 1 && {
          isAllDuplicate: selected.filter(i => i.is_duplicate)?.length === conditions.value?.length
        })
      })
    }
    const handleUpdateDifferent = (val) => ctx.emit('update:different', val)
    const validationSelectMethod = debounce(async (val) => {
      if (!!val) {
        if (val > 1 || props?.category?.is_child || props?.companySource) {
          await handleSelectMethod(2)
        } else {
          data.loading = false
          if (!!props?.editing) await handleSelectMethod(1)
        }
      } else {
        data.loading = false
        data.conditions = []
        data.sourceConditions = []
      }
    }, 100)
    const handleReset = () => {
      data.method = null
      data.loading = false
      data.conditions = []
      data.sourceConditions = []
      data.conditionIDS = []
    }
    const onSubmit = async () => {
      return formConditions.value?.onSubmit()
    }
    const isSameSourceList = (a, b) => {
      if (!Array.isArray(a) || !Array.isArray(b)) return false
      if (a.length !== b.length) return false

      const sortBySub = (arr) => [...arr].map(i => i?.sub_domain ?? i).sort()
      return JSON.stringify(sortBySub(a)) === JSON.stringify(sortBySub(b))
    }

    onMounted(async () => {
      await actionConditionDeviceAvailability()
  
      const isSameSource = isSameSourceList(props.pricingSource, props.rules?.selectedSources)

      if (props.editing && isSameSource) {
        await handleSelectMethod(props.rules?.mappingType === 'manual' ? 2 : 1)
      } else await validationSelectMethod(props?.pricingSource?.length)
    })

    watch(() => ({
      source: props.pricingSource, editing: props.editing
    }), async (val, oldVal) => {
      data.loading = true
      data.conditions = []
      data.sourceConditions = []
      await validationSelectMethod(val.source.length)

      if (val.editing) {
        props.category.condition_source = []
        props.category.condition = []
      }
    }, {
      deep: true
    })

    return {
      me,
      data,
      selectCondtion,
      conditions,
      sourceConditions,
      isAvailableDevice,
      formConditions,
      keyCondition,
      handleSelectMethod,
      handleAddConditionGroup,
      handleUpdateConditions,
      handleUpdateDifferent,
      handleReset,
      onSubmit
    }
  }
})
</script>

<style lang="scss">
.el-table__empty-block {
  span {
    display: block;
  }
  &::after {
    content: '';
  }
}
</style>
