import { ref } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'

const preset = () => {
  const form = ref({
    source: null,
    conditions: [],
    sources: [],
    condition: []
  })
  const modal = ref(false)
  const loading = ref(false)
  const getSources = async (payload = {}, callback = () => {}) => {
    if (payload.length !== 1) return

    try {
      loading.value = true
      const response = await store.dispatch('setting/condition/showSourceCompanies', payload)
      form.value.sources = response
      isFunction(callback) && callback(response)
    } catch (error) {
      form.value.sources = []
      isFunction(callback) && callback(error)
    } finally {
      loading.value = false
    }
  }
  const getConditions = async (payload = {}, callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch('setting/condition/showSourceConditions', {
        sub_domain: payload.sub_domain,
        data: {
          category_id: payload.category_id,
          is_mac: payload.is_mac,
          sub_domain: payload.sub_domain
        }
      })
      form.value.conditions = response
      isFunction(callback) && callback(response)
    } catch (error) {
      form.value.conditions = []
      isFunction(callback) && callback(error)
    } finally {
      loading.value = false
    }
  }

  return {
    form,
    modal,
    loading,
    isValidJSON,
    getSources,
    getConditions,
  }
}

const active = () => {
  const form = ref({
    conditions: []
  })
  const loading = ref(false)
  const modal = ref({
    delete: false,
    condition: false,
    preview: false
  })

  const addCondition = async (
    payload = {
      id: undefined,
      term: undefined,
      terms: undefined
    },
    callback = () => {}
  ) => {
    try {
      loading.value = true
      let terms = []
      terms = [payload.term, ...payload.terms]
      const response = await store.dispatch(`setting/condition/conditionCreate`, {
        id: payload.id,
        data: {
          ...payload,
          terms: JSON.stringify(terms)
        }
      })
      isFunction(callback) && callback()
      return response
    } catch {
      loading.value = false
    } finally {
      loading.value = false
    }
  }
  const editCondition = async(
    payload = {
      id: undefined,
      term: undefined,
      terms: undefined
    },
    callback = () => {}
  ) => {
    try {
      loading.value = true
      let terms = []
      terms = [payload.term, ...payload.terms]
      await store.dispatch(`setting/condition/conditionUpdate`, {
        id: payload.id,
        data: {
          terms: JSON.stringify(terms),
          name: payload?.name
        }
      })
      isFunction(callback) && callback()
    } catch {
      loading.value = false
    } finally {
      loading.value = false
    }
  }
  const deleteCondition = async(id, callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch(`setting/condition/conditionDelete`, id)
      isFunction(callback) && callback()
      return response
    } catch {
      loading.value = false
    } finally {
      loading.value = false
    }
  }
  const sortingCondition = async(payload, callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch(`setting/condition/sort`, {sorting: JSON.stringify(payload)})
      isFunction(callback) && callback()
      return response
    } catch {
      loading.value = false
    } finally {
      loading.value = false
    }
  }

  return {
    form,
    modal,
    loading,
    addCondition,
    editCondition,
    deleteCondition,
    sortingCondition
  }
}

const isValidJSON = (jsonString) => {
  try {
    JSON.parse(jsonString)
    return true
  } catch (e) {
    return false
  }
}

export default function Condition() {
  return {
    preset: preset(),
    active: active()
  }
}
