import { reactive, ref } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'

export default function Category () {
  const form = reactive({
    group_name: null,
    categories: null
  })
  const devices = ref([])
  const loading = ref(false)

  const getDevices = async (locationId, callback = () => {}) => {
    loading.value = true
    devices.value = await store.dispatch(`setting/condition/devices`)
    isFunction(callback) && callback()
    loading.value = false
  }

  return {
    form,
    devices,
    loading,
    getDevices,
  }
}
