<template>
  <div class="bulk-price-editor overflow-x-auto">
    <table
      v-loading="loadingTable || loadingPricing?.all"
      class="table-price-editor w-full table-fixed overflow-x-auto">
      <tr class="table-price-editor__row">
        <th class="table-price-editor__colhead"></th>
        <th class="table-price-editor__colhead md:w-96">Brand</th>
        <th class="table-price-editor__colhead md:w-96">Category</th>
        <th class="table-price-editor__colhead w-40">Price Sync</th>
        <th v-if="!priceScraper && !disabledAction.availabilityCondition" class="table-price-editor__colhead w-40">
          Pricing Status
        </th>
      </tr>
      <template v-for="(item, n) in items" :key="n">
        <tr
          :id="`row_${n}`"
          class="table-price-editor__row">
          <td class="table-price-editor__col">
            <div
              v-if="!disabledAction.priceUploadByProgress && !disabledAction.availabilityCondition"
              :class="{
                'el-table__expand-icon el-table__expand-icon--expanded': item.collapse,
                'el-table__expand-icon': !item.collapse
              }"
              @click="onCollapse(item)">
              <i v-loading="item.loading" class="el-icon-arrow-right" />
            </div>
          </td>
          <td class="table-price-editor__col">
            <div class="flex items-center gap-x-2">
            {{ item.brand }}
            <Tooltip
              v-if="!item.is_price_sync && (item.conditions && !item.conditions.length)"
              placement="top">
              <span class="text-orange-04 material-icons leading-4" style="font-size: 1.25rem !important;">warning</span>
              <template #content>
                <div class="w-72">
                  To manage the pricing for this item, you can either set up manual conditions using the "Setup Condition" or enable price sync for automatic pricing.
                </div>
              </template>
            </Tooltip>
            <Tooltip
              v-if="item?.missing_price_flags"
              placement="right">
              <span class="text-red-04 material-icons leading-4" style="font-size: 1.25rem !important;">flag</span>
              <template #content>
                <div class="max-w-lg">
                  One or more items are flagged due to unavailable prices for certain conditions. Expand the submenu to view the flagged items and manually price them.
                </div>
              </template>
            </Tooltip>
            </div>
          </td>
          <td class="table-price-editor__col">{{ item.name }}</td>
          <td class="table-price-editor__col">
            <div
              v-if="!priceScraper"
              class="flex items-center gap-x-4">
              <div>
                <div
                  v-if="subDomainLinked || item.is_syncable"
                  @click="onCollapse(item, true)">
                  <SyncPricing
                    v-model="item.is_price_sync"
                    :is-syncable="item.is_syncable"
                    :loading="loading.rules.includes(item.id)"
                    tooltip
                    :item="item"
                    :disabled-action="disabledAction"
                    @update:modelValue="changeAutomatedPricing(item)"
                  />
                </div>
                <SyncPricing
                  v-else
                  v-model="item.is_price_sync"
                  :item="item"
                  :disabled-action="disabledAction"
                />
              </div>
              <div class="action">
                <Button
                  v-if="item.is_price_sync == 0 && (item?.conditions && !item.conditions?.length)"
                  :loading="item.loading"
                  color="teal"
                  dense
                  class="action-btn"
                  @click="$router.push({
                    name: 'settingCondition',
                  })">
                  <div class="flex items-center justify-center">
                    Set item conditions
                    <span class="material-icons text-sm">chevron_right</span>
                  </div>
                </Button>
                <Button
                  v-if="item.is_price_sync != 0 && item.is_syncable"
                  :loading="item.loading"
                  color="teal"
                  dense
                  class="action-btn"
                  :disabled="disabledAction.priceUploadByProgress"
                  @click="editAutomate(item)">
                  <span class="material-icons text-sm">edit</span>
                </Button>
              </div>
            </div>
            <div v-if="priceScraper" class="flex items-center gap-x-4">
              <div>
                <SyncPricing
                  :item="item"
                  :price-scraper="priceScraper"
                  :disabled-action="disabledAction"
                />
              </div>
            </div>
          </td>
          <td v-if="!priceScraper && !disabledAction.availabilityCondition" class="table-price-editor__col">
            <p v-if="item.total_device_manual && !item.total_device_sync">
              All Items Manually Priced
            </p>
            <p v-else-if="!item.total_device_manual && item.total_device_sync">
              {{ item.mapping_type === 'manual' ? 'All Items Prices Synced' : 'All Items Prices Synced (Auto Mapped)' }}
            </p>
            <p v-else-if="item.total_device_manual && item.total_device_sync">
              {{ item.total_device_sync }}
              {{ item.total_device_sync > 1 ? 'Items' : 'Item' }} price synced,
              {{ item.total_device_manual }}
              {{ item.total_device_manual > 1 ? 'Items' : 'Item' }} manually priced
            </p>
          </td>
        </tr>
        <tr
          v-if="item.collapse"
          class="table-price-editor__row expanded">
          <td class="table-price-editor__col"></td>
          <td
            :colspan="!priceScraper && !disabledAction.availabilityCondition ? 4 : 3"
            class="table-price-editor__col">
            <div class="py-6 md:pr-16">
              <div
                v-if="item.is_price_sync == 0"
                class="condition-group">
                <transition name="fade">
                  <div
                    v-if="item.alert && !disabledAction.priceUploadByProgress"
                    class="condition-group__container"
                    style="z-index: 10; background-color: rgba(235,0,27,0.5)">
                    <div class="condition-group__modal-container">
                      <div class="condition-group__modal-content text-red-base">
                        <div class="flex">
                          <i class="material-icons mr-4">error</i>
                          <div class="w-full">
                            <p>
                              <span class="font-semibold">Warning!</span>
                              Changing the pricing rules on this window <br/>
                              will affect all of the following device:
                            </p>
                            <BulkPriceEditorConditionGroupList
                              v-if="item?.conditions_group?.length"
                              :conditions="item.conditions_group"
                            />
                            <div class="flex flex-wrap">
                              <Button
                                color="teal"
                                view="outlined"
                                class="mr-4 my-2"
                                @click="$router.push({
                                  name: 'settingConditionEdit',
                                  params: {
                                    conditionId: item.conditions_group_id || 0
                                  }
                                })">
                                Go to Conditions
                              </Button>
                              <Button
                                color="teal"
                                view="outlined"
                                class="my-2"
                                @click="item.alert = false">
                                Continue Pricing
                              </Button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </transition>
                <Alert
                  v-if="item?.conditions_group?.length !== 0 && item.alert == 0 && !disabledAction.priceUploadByProgress"
                  color="red"
                  view="smooth"
                  class="mb-5"
                  dense>
                  <div class="flex space-x-4">
                    <div>
                      <i class="material-icons">error</i>
                    </div>
                    <div>
                      Warning! Changing the pricing rules on this window will affect all of the following devices:
                      <BulkPriceEditorConditionGroupList
                        v-if="item.conditions_group?.length"
                        :conditions="item.conditions_group"
                      />
                      <Button
                        color="teal"
                        view="outlined"
                        @click="$router.push({
                          name: 'settingConditionEdit',
                          params: {
                            conditionId: item.conditions_group_id || 0
                          }
                        })">
                        Go to Conditions
                      </Button>
                    </div>
                  </div>
                </Alert>
                <BulkPriceEditorPricingManual
                  v-if="item?.conditions?.length"
                  v-loading="listLoading"
                  v-model:conditions="item.conditions"
                  v-model:options="item.condition_options"
                  v-model:disabled="item.disabled"
                  :item="item"
                  :key-component="n"
                  :disabled-action="disabledAction"
                  @change:condition="changeCondition"
                  @apply-pricing="updateCategoryCondition"
                />
              </div>
              <BulkPriceEditorList
                :key="`${item.id}-${item.renderKey || 0}`"
                :id="item.id"
                :items="item"
                :round-up="roundUp"
                :sycnable-price="
                  item.is_syncable &&
                  item.mapping_type !== 'auto'
                    ? 1
                    : 0
                "
                :price-scraper="priceScraper"
                :price-sharing="priceSharing"
                :search="search"
                :disabled-action="disabledAction"
                :refetch="refetch"
                @update:refetch="handleSetRefetch"
                @show-prices="onShowPrices"
                @refetch-device="refetchDevice"
                @pricing-flag-updated="handlePricingFlagUpdate"
              />
            </div>
          </td>
        </tr>
      </template>
    </table>
    <BulkPriceEditorSources
      v-model:modal="modal"
      v-model:item="selected"
      v-model:editing="isEditAutomate"
      v-model:priceSharing="priceSharing"
      :loading="loading?.rule"
      @cancel="cancelAutomatedPricing"
      @apply="applyRule"
    />
  </div>
</template>

<script>
import { defineComponent, ref, watch, computed, onBeforeMount, onUnmounted, nextTick } from 'vue'
import { useRoute } from 'vue-router'
import { useDevice, useCategories } from 'composable/pricing'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import SyncPricing from 'components/BulkPriceEditor/SyncPricing.vue'
import BulkPriceEditorList from 'components/BulkPriceEditor/Device/List.vue'
import BulkPriceEditorSources from 'components/BulkPriceEditor/Device/Sources.vue'
import BulkPriceEditorPricingManual from 'components/BulkPriceEditor/Device/Pricing.vue'
import BulkPriceEditorConditionGroupList from 'components/BulkPriceEditor/Device/ConditionGroupList.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'BulkPriceEditor',
  components: {
    Button,
    Alert,
    SyncPricing,
    BulkPriceEditorList,
    BulkPriceEditorSources,
    BulkPriceEditorPricingManual,
    BulkPriceEditorConditionGroupList
  },
  props: {
    items: {
      type: Array,
      default: () => ([])
    },
    conditions: {
      type: Array,
      default: () => ([])
     },
    pathId: {
      type: String,
      default: null
    },
    loadingTable: {
      type: Boolean
    },
    priceSharing: {
      type: Boolean,
      default: false
    },
    priceScraper: {
      type: Boolean,
      default: false
    },
    roundUp: {
      type: [String, Number],
      default: undefined
    },
    disallowSync: {
      type: Boolean,
      default: false,
    },
    subDomainLinked: {
      type: Boolean,
      default: false
    },
    search: {
      type: String,
      default: null
    },
    disabledAction: {
      type: Object,
      default: undefined
    }
  },
  emits: ['refetch-pricing', 'pricing-flag-updated'],
  setup(props, ctx) {
    const keyList = ref()
    const isEditAutomate = ref(false)
    const isProcessing = ref(false)
    const modal = ref(false)
    const route = useRoute()
    const { loading, actionAll, actionUpdate, actionSetRule, actionUnsetRule, actionSetLevel, actionGetChildren } = useDevice(route.params.tab)
    const {
      loading: loadingPricing,
      actionSpecificV2: fetchSpecificV2,
      actionConditionUpdate
    } = useCategories()
    const selected = ref(null)
    const items = ref(props.items)
    const tableElement = ref(null)
    const listLoading = ref(false)
    const refetch = ref(false)

    const expands = computed(() => {
      const filtered = []
      if (route.query.ref === 'true') {
        items.value.map(item => {
          if (item.brand === route.params.manufacturer) {
            filtered.push(item.id)
          }
        })
      }
      items.value.forEach((item) => {
        if (item.expand) {
          filtered.push(item.id)
          return
        }
        if (route.query.ref === 'true') {
          if (item.brand === route.params.manufacturer) filtered.push(item.id)
        }
      })
      return filtered
    })

    const deviceAll = async (item) => {
      if(item.children.length === 0 && (!item.devices)) {
        await actionAll(item.id, response => {
          item.devices = response.devices.devices
          item.child_conditions = response.conditions
        })
      }
    }

    const handlePricingFlagUpdate = (pricingStatusData) => {
      const itemIndex = items.value.findIndex(item => item.id === pricingStatusData.itemId)
      if (itemIndex !== -1) {
        items.value[itemIndex].missing_price_flags = pricingStatusData.deviceIdHasNoPrice
        
        if (pricingStatusData.children) {
          items.value[itemIndex].children = pricingStatusData.children
        }
      }

      ctx.emit('pricing-flag-updated', pricingStatusData)
    }

    const deviceUpdate = async (item, index) => {
      actionUpdate({
        pricing: JSON.stringify([{
          product_id: item.id,
          pricing: [
            {
              condition_id: item.price[index].id,
              price: item.price[index].price
            }
          ]
        }])
      })
    }

    const updateCategoryCondition = async(data) => {
      const { condition, item } = data
      const indexItem = items.value.findIndex(i => i.id === item.id)
      const conditionIndex = items.value[indexItem].conditions.findIndex(i => i.id === condition.id)
      items.value[indexItem].conditions[conditionIndex].loading = true
      const { id, parent_id, condition_sub_value } = items.value[indexItem]?.conditions[conditionIndex] ?? condition
      try {
        await actionConditionUpdate({
          route: route.params,
          data: {
            id: id,
            data: {
              parent_id: parent_id,
              sub_value: condition_sub_value
            }
          }
        })
        items.value[indexItem].children = resetChildren(items.value[indexItem].children, true)
        items.value[indexItem].renderKey = Date.now()
        // ctx.emit('refetch-pricing', callback)
      } finally {
        items.value[indexItem].conditions[conditionIndex].loading = false
      }
    }

    const validateData = ({ mapping_type, mapping }) => {
      return mapping.map((item, index) => {
        let errors = []

        // Validation by mapping_type
        if (mapping_type === "manual" && item.member_condition_id == null) {
          const msg = 'Member condition required'
          if (!errors.includes(msg)) errors.push(msg)
        }
        if (!item.source_condition_id) {
          const msg = 'Source condition required'
          if (!errors.includes(msg)) errors.push(msg)
        }

        // validation by type
        if (item.type !== 3 && (!item.amount_price || item.amount_price.trim() === "")) {
          const msg = 'Amount price required'
          if (!errors.includes(msg)) errors.push(msg)
        }

        return errors.length ? { index, errors } : null
      }).filter(Boolean)
    }

    const applyRule = async(payload, callback = () => {}) => {
      if (!payload.isNew) {
        callback()
        return resetModal()
      }

      loading.rule = true

      const result = validateData({
        mapping_type: payload.data.mapping_type,
        mapping: payload.data.mapping
      })
      if (result.length) {
        loading.rule = false
        return ElMessage({
          message: 'Please fill in the required information before proceeding',
          type: 'warning'
        })
      }

      await actionSetRule(payload.data, (res) => {
        if (!!res) {
          const callBackRefetch = () => {
            callback()
            resetModal()
            loading.rule = false
          }
    
          ctx.emit('refetch-pricing', callBackRefetch)
        }
      })
    }

    const resetChildren = (children = [], resetDevices = false) => {
      return children.map(child => ({
        ...child,
        collapse: false,
        ...(resetDevices && { devices: {} }),
        children: resetChildren(child.children || [])
      }))
    }
    const onCollapse = async (row, keepClose = false) => {
      if (row.loading) return
      if (row.isFetched) {
        row.collapse = keepClose ? false : !row.collapse
        return
      }

      row.loading = true
      await actionGetChildren({ id: row.id }, (res) => {
        row.children = res?.children ?? []
        row.conditions = res?.conditions ?? []
        row.conditions_group = res?.conditions_group ?? []
        row.conditions_group_id = res?.conditions_group_id ?? null
        row.condition_options = res?.condition_options ?? []
        row.alert = res?.alert ?? false
        row.disabled = res?.disabled ?? []
        row.isFetched = true
      })
      row.collapse = keepClose ? false : !row.collapse
      row.loading = false
      row.renderKey = Date.now()

      return row
    }
    const onShowPrices = (item) => {
      const originIndex = items.value.findIndex(i => i.id === item.top_id)
      if (originIndex === -1) return

      const originItem = items.value[originIndex]
      if (!originItem?.collapse) return

      const childIndex = originItem.children?.findIndex(i => i.id === item.id) ?? -1
      if (childIndex === -1) return

      const updatedChild = {
        ...originItem.children[childIndex],
        ...item
      }

      originItem.children[childIndex] = updatedChild
      originItem.renderKey = Date.now()
      item = updatedChild

      return item // opsional
    }

    const resetCollapse = (row, childOnly = false) => {
      keyList.value = Date.now()
    }
    const resetChildItems = (childItems, parentItem) => childItems.map(childItem => 
      ({
        ...childItem,
        children: childItem.child ? resetChildItems(childItem.child, parentItem) : [],
        collapse: false,
        devices: null
      })
    )
    const resetAllCollapsedExpandable = () => {
      items.value = items.value.map(item => ({
        ...item,
        ...(item.children && { children: resetChildItems(item.children, item) })
      }))
    }

    const changeAutomatedPricing = async (item) => {
      isProcessing.value = true
      loading.rules.push(item.id)
      listLoading.value = true
      if (item.is_price_sync == 1) {
        modal.value = true, selected.value = item
      } else {
        await actionUnsetRule({
          category_id: item.id,
          level: actionSetLevel(item)
        })
        ctx.emit('refetch-pricing')
      }
      resetAllCollapsedExpandable()
      listLoading.value = false
      const matchedIndex = loading.rules.findIndex(loadingItem => loadingItem === item.id)
      if(matchedIndex > -1) loading.rules.splice(matchedIndex, 1)
      isProcessing.value = false
    }

    const cancelAutomatedPricing = () => {
      let indexItem = items.value.findIndex(item => Number(item.id) === Number(selected.value.id))
      if (indexItem !== -1) items.value[indexItem].loading = false  

      if (!isEditAutomate.value) {
        items.value[indexItem].is_price_sync = 0
        resetAllCollapsedExpandable()
      }
      resetModal()
    }

    const changeCondition = async ({ row, item }) => {
      if(row.parent_id == null){
        row.condition_sub_value = 0
        await updateCategoryCondition({condition: row, item: item})
      }
      if(row.id != row.parent_id){
        item.disabled.push(row)
        item.disabled.forEach(selected => {
          let existingIndex = item.conditions.findIndex(condition => selected.parent_id == condition.parent_id)
          let selectedIndex = item.conditions.findIndex(condition => selected.parent_id == condition.id)
          if(existingIndex == -1) {
            let result = item.disabled.filter(value => value.parent_id != item.conditions[selectedIndex].id)
            item.disabled = result
          }
        })
      }
    }

    const resetModal = (data) => {
      isEditAutomate.value = false
      modal.value = false
      resetCollapse(selected.value)
    }

    const editAutomate = async (item) => {
      item.loading = true
      isEditAutomate.value = true
      modal.value = true
      selected.value = item
    }
    const handleSetRefetch = (val) => {
      refetch.value = val
    }

    watch(() => props.items, newValue => {
      items.value = newValue?.length ? [...newValue] : []
    }, {deep: true})

    const filterFecthOption = (conditions, item) => {
      if (!!conditions && !!conditions.length) {
        return [...conditions].filter(condition =>
          condition.name !== item?.name && condition.id !== item?.id
        )
      }
      return conditions
    }

    onBeforeMount(() => {
      window.addEventListener('beforeunload', (ev) => {
        if(!isProcessing.value) return
        ev.preventDefault()
        ev.returnValue = ''
      })
    })

    onUnmounted(() => {
      window.removeEventListener('beforeunload', ()=>{})
    })
    const refetchDevice = async (e = {
      item: undefined,
      callback: () => {}
    }) => {
      const { item, callback } = e
      await fetchSpecificV2({
        manufacturer: item.brand,
        categories: route?.params?.tab
      }, items.value, (succeed) => {
        if (succeed) {
          typeof callback === 'function' && callback()
          refetch.value = true
        }
      })
    }


    if (
      route.query.model &&
      route.query.ref === 'true' &&
      !props.disabledAction.priceUploadByProgress &&
      !props.disabledAction.availabilityCondition) {
      const itemRef = items.value?.find(item => item.brand === route.params?.manufacturer && item.name === route.query?.model) ?? undefined
      if (itemRef && !Array.isArray(itemRef?.children)) {
        onCollapse(itemRef)
      }
    }

    return {
      keyList,
      items,
      modal,
      isEditAutomate,
      deviceAll,
      listLoading,
      deviceUpdate,
      route,
      changeAutomatedPricing,
      cancelAutomatedPricing,
      selected,
      loading,
      applyRule,
      tableElement,
      updateCategoryCondition,
      changeCondition,
      editAutomate,
      filterFecthOption,
      loadingPricing,
      expands,
      refetchDevice,
      handlePricingFlagUpdate,
      onCollapse,
      onShowPrices,
      refetch,
      handleSetRefetch
    }
  }
})
</script>
