

<script setup>
import { ref, computed, watch } from 'vue'
import Button from 'bbUi/components/button/button.vue'

const props = defineProps({
  conditions: {
    type: Array,
    required: true
  },
  limit: {
    type: Number,
    default: 50
  }
})

const page = ref(1)

watch(() => props.conditions, () => {
  page.value = 1
})

const paginatedConditions = computed(() => {
  return props.conditions.slice(0, page.value * props.limit)
})

const loadMore = () => {
  page.value++
}
</script>

<template>
  <div v-if="conditions?.length" class="py-6">
    <ul class="list-disc ml-5">
      <li
        v-for="(condition, index) in paginatedConditions"
        :key="`condition-${index}`"
        class="font-semibold">
        {{ condition }}
      </li>
      <li class="list-none">
        <div v-if="paginatedConditions.length < conditions.length">
          <Button
            size="sm"
            color="red"
            view="outlined"
            dense
            @click="loadMore">
            Show More + {{ (conditions.length - paginatedConditions.length) }}
          </Button>
        </div>
      </li>
    </ul>

  </div>
</template>
