<template>
  <div v-if="!!data.row?.price?.length" class="flex items-stretch">
    <div class="relative flex-grow">
      <Tooltip
        v-model="tooltipShow"
        placement="top"
        manual>
        <template #content>
          <p class="max-w-md text-sm">Items priced at {{ me.currency_prefix == 1 ? `${me.currency_symbol}0` : `0${me.currency_symbol}` }} are not subject to pricing rules.</p>
        </template>
        <Input
          tabindex="0"
          :modelValue="data.row.price[data.index].price"
          @update:modelValue="
            (value) => {
              data.row.price[data.index].price = value
              $emit('setCustomPrice')
            }
          "
          type="number"
          :class="[
            'w-full',
            isPriceSyncCustomPrice ? 'input--custom-priced' : ''
          ]"
          :disabled="
            disabled ||
            // (data.condition.is_disabled && !hasPricingSource) ||
            data.row.price[data.index].loading
          "
          :view="data.row.price[data.index].success ? 'success' : 'default'"
          :loading="data.row.price[data.index].loading"
          :currency="true"
          @mouseenter="isVisible = disabled"
          @mouseleave="isVisible = false"
          @focus-out="deviceUpdate(data.row, data.index, data.items)"
          @focus-in="tooltipShow = true"
          icon
        >
          <template #icon>
            {{ me.currency_symbol }}
          </template>
        </Input>
      </Tooltip>
      <transition name="slide-fade">
        <span
          v-if="data.row.price[data.index].success"
          class="material-icons text-green-04 absolute left-10 top-1/2 text-2xl transform -translate-y-1/2"
          >done</span
        >
      </transition>
      <Tooltip v-if="isPriceSyncCustomPrice && !!data.row.price[data.index].last_update" placement="top">
        <template #content>
          <div class="max-w-sm">
            Manually updated on {{ data.row.price[data.index].last_update }}
          </div>
        </template>
        <template #default>
          <i
            class="material-icons absolute right-4 top-1/2 transform -translate-y-1/2 text-teal-04"
            >info</i
          >
        </template>
      </Tooltip>
    </div>
    <div v-if="isAutomatedPrice" class="flex items-stretch">
      <template v-if="data.row.price[data.index].is_custom_price === 0">
        <transition name="slide-fade">
          <div
            v-if="!data.row.price[data.index].is_editable_custom_price"
            class="flex item-stretch"
          >
            <Button
              v-if="data.row.price[data.index].price > 0"
              @click="$emit('setCustomPrice')"
              class="mx-2"
              color="teal"
              dense
              view="outlined"
              style="border-color: #dddddd !important;"
            >
              <div class="p-1">
                <i class="material-icons">edit</i>
              </div>
            </Button>
            <Tooltip
              v-else
              placement="left">
              <Button
                @click="$emit('setCustomPrice')"
                class="mx-2"
                color="red"
                dense
                view="outlined"
              >
                <div class="p-1">
                  <i class="material-icons">edit</i>
                </div>
              </Button>
              <template #content>
                <div class="max-w-lg">
                  Manual pricing required: Conditions priced at $0 must be manually priced to be visible to your customers.
                </div>
              </template>
            </Tooltip>
          </div>
        </transition>
      </template>
      <template v-else>
        <transition name="slide-fade">
          <div
            v-if="!data.row.price[data.index].is_editable_custom_price"
            class="flex items-stretch"
          >
            <Button
              v-if="!!isAutomatedPrice"
              @click="$emit('resetCustomPrice')"
              :loading="data.row.price[data.index].loading"
              color="red"
              class="mx-2"
              dense
            >
              <div class="p-1">
                <i class="material-icons">delete</i>
              </div>
            </Button>
          </div>
        </transition>
      </template>
      <transition name="slide-fade">
        <div
          v-if="data.row.price[data.index].is_editable_custom_price === 1"
          class="flex item-stretch"
        >
          <Button
            @click="$emit('saveCustomPrice')"
            :loading="data.row.price[data.index].loading"
            class="mx-2"
            color="teal"
            dense
          >
            <div class="p-1">
              <i class="material-icons">check</i>
            </div>
          </Button>
          <Button
            @click="$emit('unsetCustomPrice')"
            :disabled="data.row.price[data.index].loading"
            color="red"
            dense
          >
            <div class="p-1">
              <i class="material-icons">close</i>
            </div>
          </Button>
        </div>
      </transition>
    </div>
  </div>
  <div v-else class="flex items-stretch">
    <div class="relative flex-grow">
      <Tooltip
        v-model="tooltipShow"
        trigger="focus"
        placement="top"
        manual>
        <template #content>
          <p class="max-w-xs text-sm">Items priced at {{ me.currency_prefix == 1 ? `${me.currency_symbol}0` : `0${me.currency_symbol}` }} are not subject to pricing rules.</p>
        </template>
        <Input
          class="w-full"
          disabled
          :currency="true"
          icon>
          <template #icon>
            {{ me.currency_symbol }}
          </template>
        </Input>
      </Tooltip>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, ref, watch, onMounted, computed } from 'vue'
import { useUser } from 'composable/user'
import { currencies } from 'constants/currency'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'BulkPriceEditorInput',
  components: {
    Input,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    disabled: {
      type: Boolean,
      default: false
    },
    isAutomatedPrice: {
      type: [Boolean, Number],
      default: false
    },
    hasPricingSource: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'input',
    'focusOut',
    'setCustomPrice',
    'unsetCustomPrice',
    'resetCustomPrice',
    'saveCustomPrice',
    'setOriginalPrice'
  ],
  setup(props, { emit }) {
    const tooltipShow = ref(false)
    const isVisible = ref(null)
    const { me } = useUser()
    const data = reactive(props.data)

    const deviceUpdate = (row, index, item) => {
      tooltipShow.value = false
      if (props.isAutomatedPrice) return
      if (row?.price[index]?.original_price == row?.price[index]?.price) return

      emit('focusOut', { row, index, item, isSync: props.isAutomatedPrice })
    }

    const isPriceSyncCustomPrice = computed(() =>
      data.index > -1 && Array.isArray(data?.row?.price)
        ? props.isAutomatedPrice &&
          data.row.price[data.index].is_custom_price === 1
        : false
    )

    onMounted(() => {
      if (props.isAutomatedPrice) {
        emit('setOriginalPrice')
      }
    })

    watch(
      () => props.data,
      (newData) => {
        if(!data.row.price[data.index].original_price) emit('setOriginalPrice')
        Object.assign(data, newData)
      }
    )

    return {
      isVisible,
      tooltipShow,
      isPriceSyncCustomPrice,
      deviceUpdate,
      currencies,
      data,
      me
    }
  }
})
</script>
<style lang="scss">
.input {
  &.input--custom-priced {
    input {
      padding-right: 3rem !important;
    }
  }
}
</style>
