<template>
  <Modal v-model="modal" size="xl" hide-header>
    <div class="border-b border-grey-01 px-10 pb-5 mb-5 -mx-12">
      <div class="flex items-center">
        <h6 class="font-semibold">Add Manual Condition</h6>
        <Tooltip placement="top">
          <i class="material-icons mx-2 cursor-pointer">help</i>
          <template #content>
            <div class="w-80 text-sm">Price Sync is a tool that allows you to sync buyback pricing with a specific data source in real-time automatically. You can set rules to match, markup, or markdown the buyback price based on a percentage or fixed amount.</div>
          </template>
        </Tooltip>
      </div>
    </div>
    <div v-loading="loading">
      <ConditionForm
        ref="conditionForm"
        :price-editor-category="category"
        price-editor
      />
    </div>
    <template #footer>
      <div class="flex items-center justify-between">
        <a
          href="#"
          v-loading="loading"
          @click.prevent="handleBack">
          Go Back
        </a>
        <Button
          color="teal"
          :loading="loading"
          @click="handleSave">
          Save
        </Button>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import ConditionForm from 'components/Condition/Form.vue'

export default defineComponent({
  name: "BulkPriceEditorMappingCondition",
  components: {
    Modal,
    Button,
    ConditionForm
  },
  emits: ['back', 'save'],
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    pricingSource: {
      type: Object,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    category: {
      type: Object,
      default: undefined
    },
  },
  emits: ['update:modelValue', 'back', 'save'],
  async setup(props, ctx) {
    const conditionForm = ref()
    const loading = ref(false)
    const modal = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const categoryId = computed(() => props?.category)

    const handleSave = async () => {
      loading.value = true
      await conditionForm.value.saveCondition()
      loading.value = false
      modal.value = false
      ctx.emit('save')
    }
    const handleBack = () => {
      modal.value = false
      ctx.emit('back')
    }

    return {
      app,
      loading,
      modal,
      categoryId,
      conditionForm,
      handleSave,
      handleBack
    }
  }
})
</script>