import { ref, computed } from 'vue'
import store from 'store'

import Category from './category'
import Condition from './condition'

import { isFunction } from 'utils/is'

const useConditionFormNew = () => {
  const loading = ref(false)
  const formEdit = ref({
    groupName: null,
    conditions: [],
    categories: []
  })
  const companyName = computed(() => store.state.setting.condition.price_sync_company_source)

  const getDetail = async (payload, callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch(`setting/condition/show`, {
        id: payload.id,
        ...(payload.priceSync.uniqueId && payload.priceSync?.sync && {
            params: {
              unique_id: payload.priceSync.uniqueId,
              is_price_sync: payload.priceSync?.sync
            }
          }
        ),
        ...(payload.priceSync.uniqueId && {
          params: { unique_id: payload.priceSync.uniqueId }
        })
      })
      formEdit.value.groupName = response.group_name
      formEdit.value.conditions = response.conditions
      formEdit.value.categories = response.categories

      isFunction(callback) && callback(response)
    } catch (error) {
      isFunction(callback) && callback(error)
    } finally {
      loading.value = false
    }
  }

  const action = {
    add: async (payload) => await store.dispatch(`setting/condition/create`, {
      group_name: payload.group_name,
      categories: JSON.stringify(payload.categories),
      conditions: payload.conditions.map(item => ({
          ...item,
          terms: [...[item.term], ...item.terms]
        })
      )
    }),
    update: async (payload) => await store.dispatch(`setting/condition/update`, {
      id: payload.id,
      data: {
        group_name: payload.group_name,
        categories: JSON.stringify(payload.categories)
      }
    }),
    delete: async (id) => await store.dispatch(`setting/condition/delete`, id)
  }

  return {
    main: {
      loading,
      formEdit,
      action,
      companyName,
      getDetail
    },
    category: Category(),
    condition: Condition()
  }
}

export default useConditionFormNew
