<template>
  <div v-outside="closeDropdown" class="mass-price">
    <div class="relative">
      <div class="trigger">
        <div class="icon">
          <span class="material-icons">search</span>
        </div>
        <div class="input">
          <template v-if="selectedItemsModel.length">
            <div
              v-for="(item, n) in selectedItemsModel"
              :key="n"
              class="bg-grey-01 px-4 py-0.5 rounded-full text-sm text-grey-03 bg-opacity-30 flex flex-nowrap items-center gap-x-2">
              <span class="whitespace-normal">{{ item?.name || item?.product_name }}</span>
              <span
                class="cursor-pointer hover:opacity-50 material-icons"
                @click.stop="handleRemoveItem(n)">
                close
              </span>
            </div>
          </template>
          <input
            v-if="selectedItemsModel?.length < 5"
            v-model="searchQuery"
            :placeholder="placeholder"
            @input="handleSearch($event, isExpanded)"
          />
        </div>
      </div>
      <div
        v-if="showDropdown"
        class="z-10 absolute top-full max-h-80 overflow-y-auto left-0 right-0 transform translate-y-2 border border-grey-01 border-opacity-20 bg-white rounded-md shadow">
        <template v-if="
          itemOptions?.length > 0 ||
          modelOptions?.length > 0">
          <div class="space-y-2 py-4">
            <div
              v-if="itemOptions?.length > 0"
              class="space-y-3">
              <span
                class="px-4 py-2 font-semibold text-sm text-grey-03">
                Items
              </span>
              <div>
                <a
                  v-for="(item, n) in itemOptions"
                  :key="`${item.device_id}_${n}`"
                  tabindex="1"
                  class="p-4 flex items-center gap-x-4 hover:bg-grey-01 text-teal-04 hover:text-teal-04"
                  @click.prevent="handleSelectItem(item)">
                  <div
                    :style="{
                      width: '30px',
                      height: '30px',
                      backgroundImage: `url(${item.image})`,
                      backgroundSize: 'contain',
                      backgroundPosition: 'center',
                      backgroundRepeat: 'no-repeat'
                    }"
                  />
                  {{ item.name }}
                </a>
              </div>
            </div>
            <div
              v-if="modelOptions?.length > 0"
              class="space-y-3">
              <span
                class="px-4 py-2 font-semibold text-sm text-grey-03">
                Models
              </span>
              <div>
                <a
                  v-for="(model, m) in modelOptions"
                  :key="`${model.device_id}_${model.category_id}_${m}`"
                  tabindex="1"
                  class="p-4 flex items-center gap-x-4 hover:bg-grey-01 text-teal-04 hover:text-teal-04"
                  @click.prevent="handleSelectItem(model)">
                  <div
                    :style="{
                      width: '30px',
                      height: '30px',
                      backgroundImage: `url(${model.image})`,
                      backgroundSize: 'contain',
                      backgroundPosition: 'center',
                      backgroundRepeat: 'no-repeat'
                    }"
                  />
                  {{ model?.product_name || model?.name || '-' }}
                </a>
              </div>
            </div>
          </div>
        </template>
        <template v-else-if="
          searchQuery?.length >= 3 &&
          modelOptions?.length < 1 &&
          itemOptions?.length < 1">
          <div class="my-8">
            <p class="text-center text-grey-03">
              {{ loading ? 'Please wait...' : 'No data to show' }}
            </p>
          </div>
        </template>
        <template v-else>
          <div class="my-4">
            <div class="flex justify-center items-center py-4">
              <p class="text-left text-grey-03">Search for item or model</p>
            </div>
          </div>
        </template>
      </div>
    </div>    
  </div>
</template>
<script>
import { computed, defineComponent, ref } from 'vue'
import debounce from 'utils/debounce'
import { deepClone } from 'utils/deep-clone'

import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'BulkPriceEditorMassPricingSearch',
  components: {
    Input
  },
  props: {
    showSearch: {
      type: Boolean,
      default: false
    },
    search: {
      type: String,
      default: undefined
    },
    selected: {
      type: Array,
      default: () => []
    },
    items: {
      type: Object,
      default: () => ({})
    },
    placeholder: {
      type: String,
      default: 'Search item or model'
    },
    loading: {
      type: Boolean,
      default: false
    },
    searchCallBack: {
      type: Function,
      default: () => undefined
    }
  },
  emits: ['update:selected', 'update:search', 'update:loading', 'update:showSearch'],
  setup(props, ctx) {
    const showDropdown = computed({
      get: () => props.showSearch,
      set: (val) => ctx.emit('update:showSearch', val)
    })
    const searchQuery = computed({
      get: () => props.search,
      set: (val) => ctx.emit('update:search', val)
    })

    const selectedItemsModel = computed({
      get: () => Array.isArray(props.selected) ? props.selected : [],
      set: (val) => ctx.emit('update:selected', val)
    })
    const itemOptions = computed(() => {
      const keys = Object.keys(props.items).filter(key => key !== 'model')
      const results = []
      keys.forEach(key => {
        props.items[key].forEach(item => {
          results.push({
            name_parent: key,
            ...item
          })
        })
      })

      return results
    })
    const modelOptions = computed(() => {
      return deepClone(props.items['model'] || [])?.map(model => ({
        ...model
      })) || []
    })

    const handleSearch = debounce(async (_, expanded) => {
      if (searchQuery.value?.length >= 3 && !showDropdown?.value) {
        showDropdown.value = true
      }

      await props.searchCallBack(searchQuery.value)
    }, 300)
    const handleSelectItem = (e, callback = () => {}) => {
      let result = deepClone(selectedItemsModel.value)
      let index = result.findIndex(val => val.device_id === e.device_id);
      
      if (index >= 0) {
        // If it already exists, check if the category_id is also the same.
        let categoryMatch = result.find(val => val.device_id === e.device_id && val.category_id === e.category_id)

        // If there is an exact match (device_id & category_id), it does not need to be added
        if (categoryMatch) return

        // If there is the same device_id but without a category_id, replace it
        if (!result[index].category_id) result[index] = e
          // If the device_id is the same but the category_id is different, add it
          else result.push(e)
        // If there is no matching device_id, add it directly
      } else result.push(e)

      selectedItemsModel.value = result;
      showDropdown.value = false;
      // callback();
    }
    const handleRemoveItem = (i) => {
      selectedItemsModel.value.splice(i, 1)
      ctx.emit('update:selected', selectedItemsModel.value)
    }
    const closeDropdown = () => showDropdown.value = false

    return {
      searchQuery,
      itemOptions,
      modelOptions,
      selectedItemsModel,
      handleSearch,
      handleSelectItem,
      handleRemoveItem,
      showDropdown,
      closeDropdown
    }
  }
})
</script>
<style lang="scss" scoped>
.mass-price {
  @apply relative;
  .trigger {
    @apply relative;

    .icon {
      @apply absolute left-4 top-1/2 transform -translate-y-1/2 text-lg text-grey-01;
    }
    .input {
      @apply border rounded-lg px-11 py-3 border-grey-01 focus:border-teal-04 relative z-10 flex flex-wrap gap-x-1.5 gap-y-1.5;

      input {
        @apply flex-1;

        min-width: 128px;
      }
    }
  }
}
</style>
