<template>
  <div v-if="(!isChild && !companySource) || !sources?.length">
    <!-- Warning if multiple sources -->
    <div
      v-if="(sources?.length || 0) > 1"
      class="bg-grey-02 bg-opacity-10 rounded-md px-4 py-1 flex items-center gap-x-2 mb-4">
      <i class="material-icons">info</i>
      <p class="text-sm font-semibold">
        Manual mapping will be used for price sync due to the selection of multiple data sources
      </p>
    </div>

    <!-- Always show options even if sources is empty -->
    <div class="grid md:grid-cols-2 md:gap-x-4 gap-y-4 md:gap-y-0">
      <div
        v-for="(option, n) in options"
        :key="n"
        :class="[
          option.class,
          option.disabled && `${option.class}--disabled`,
          !sources?.length && `${option.class}--disabled-initiate`,
          method === option.value && `${option.class}--active`
        ]"
        @click="select(option)">
        <div class="flex justify-between gap-x-4">
          <img :src="option.icon" />
          <div class="info-mapping">
            <div class="flex justify-between items-center">
              <div class="flex items-center gap-x-4">
                <h2 class="text-base font-semibold">{{ option.title }}</h2>
                <div
                  v-if="option.extras"
                  class="bg-green-04 rounded-full text-white inline-flex px-4 py-0 items-center justify-center gap-1 text-sm">
                  <span class="material-icons">verified</span>
                  Recomended
                </div>
              </div>
              <div
                v-if="method === option.value"
                class="bg-grey-01 text-sm rounded-full px-3 py-1">
                Active
              </div>
            </div>
            <p
              :class="method === option.value
                ? 'text-grey-04'
                : 'text-grey-03'"
              class="info-mapping--desc">
              {{ option.desc }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'BulkPriceEditorMappingMethodMethods',
  props: {
    modelValue: {
      type: [Number, String],
      default: undefined
    },
    sources: {
      type: Array,
      default: () => []
    },
    isChild: {
      type: Boolean,
      default: false
    },
    companySource: {
      type: Boolean,
      default: false
    },
    editing: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'select'],
  setup(props, ctx) {
    const method = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const options = computed(() => {
      return [
        {
          value: 1,
          class: 'auto-mapping',
          title: 'Auto Mapping',
          extras: false,
          disabled: props.isChild || props.sources?.length > 1 && !props.isChild,
          icon: '/icon/settings/auto-mapping.svg',
          desc: 'This method clones Item conditions and terms automatically from the chosen price source, but be cautious: any existing conditions and terms will be overwritten.'
        },
        {
          value: 2,
          class: 'manual-mapping',
          title: 'Manual Mapping',
          extras: true,
          icon: '/icon/settings/manual-mapping.svg',
          disabled: false,
          desc: 'With this method, you can create conditions and terms from scratch or use our presets, then manually map them to any price source for better control, a highly recommended approach if you frequently change sources but want to maintain your conditions and terms.'
        }
      ]
    })
    const select = (val) => {
      if (val.disabled) return
      method.value = val.value
      ctx.emit('select', val.value)
    }

    return {
      method,
      options,
      select
    }
  }
})
</script>
