<template>
  <div
    class="border border-grey-02 border-opacity-70 rounded-md relative">
    <Tooltip
      placement="top">
      <span class="absolute -top-3.5 left-4 px-2 bg-white font-semibold">
        Pricing Rules for Manually Priced Items
      </span>
      <template #content>
        <p>This does not apply to items with active Price Sync.</p>
      </template>
    </Tooltip>
    <div class="px-4 pb-4 pt-8">
      <div class="overflow-x-auto">
        <table class="table-price-manual">
          <thead>
            <tr>
              <th
                v-for="(column, nCol) in columns"
                :key="nCol"
                :class="column.class">
                {{ column.name }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(row, nRow) in rows"
              :key="nRow">
              <td>{{ row.name }}</td>
              <td>
                <Select
                  v-model="row.parent_id"
                  by="id"
                  name="name"
                  class="w-full"
                  :items="filterFecthOption(options, row)"
                  :loading="row.parent_id == null && row.loading"
                  :disabled="row.disabled_select || disabledFromPrice"
                  dense
                  @input="handleChangeCondition(row)"
                />
              </td>
              <td>
                <RadioGroup
                  v-if="row.parent_id !== null"
                  v-model="row.set_percentage"
                  size="mini"
                  :disabled="disabledFromPrice"
                  @change="handleChangePercentage(row)">
                  <RadioButton label="min">- %</RadioButton>
                  <RadioButton label="plus">+ %</RadioButton>
                </RadioGroup>
              </td>
              <td>
                <Tooltip
                  v-model="row.show_tooltip"
                  placement="top"
                  manual>
                  <template #content>
                    <p class="max-w-xs text-sm">Items priced at {{ me.currency_prefix == 1 ? `${me.currency_symbol}0` : `0${me.currency_symbol}` }} are not subject to pricing rules.</p>
                  </template>
                  <div>
                    <Input
                      v-if="row.parent_id !== null"
                      v-model="row.condition_sub_value"
                      type="number"
                      icon
                      right
                      dense
                      :disabled="disabledFromPrice"
                      @focus-in="handleShowTooltip(row, true)"
                      @focus-out="handleShowTooltip(row, false)"
                      @input="handleCheckValue(row)">
                      <template #icon>
                        <span class="text-base">%</span>
                      </template>
                    </Input>
                  </div>
                </Tooltip>
              </td>
              <td>
                <Button
                  v-if="row.parent_id !== null"
                  :loading="row.loading"
                  :disabled="row.loading || disabledFromPrice"
                  color="teal"
                  dense
                  @click="handleApply(row)">
                  Apply
                </Button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue'
import debounce from 'utils/debounce'
import { useUser } from 'composable/user'

import Select from 'bbUi/components/select/select.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'BulkPriceEditorPricingManual',
  components: {
    Select,
    Button,
    Input
  },
  props: {
    conditions: {
      type: Array,
      default: () => []
    },
    options: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Array,
      default: () => []
    },
    item: {
      type: Object,
      default: () => ({})
    },
    keyComponent: {
      type: [String, Number],
      default: undefined
    },
    disabledFromPrice: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:conditions',
    'update:disabled',
    'update:options',
    'change:condition',
    'applyPricing'
  ],
  setup(props, ctx) {    
    const columns = ref([
      { class: 'md:w-44', name: 'Condition' },
      { class: 'md:w-56', name: 'Choose Pricing From' },
      { class: 'md:w-40', name: 'Set Percentage' },
      { class: 'md:w-36', name: 'Set Value' },
      { class: '', name: '' }
    ])
    const rows = computed({
      get: () => props.conditions.map(condition => ({
        ...condition,
        disabled_select: disabled.value.findIndex(item =>
          item.parent_id == condition.id
        ) != -1
          ? true
          : false,
        show_tooltip: condition?.show_tooltip !== undefined ? condition.show_tooltip : false
      })),
      set: (val) => {
        ctx.emit('update:conditions', val)
      }
    })
    const options = computed({
      get: () => props.options,
      set: (val) => ctx.emit('update:options', val)
    })
    const disabled = computed({
     get: () => props.disabled,
     set: (val) => ctx.emit('update:disabled', val)
    })
    const { me } = useUser()

    const filterFecthOption = (conditions, item) => {
      if (!!conditions && !!conditions.length) {
        return [...conditions].filter(condition =>
          condition.name !== item?.name && condition.id !== item?.id && !condition.parent_id
        )
      }
      return conditions
    }
    const filterDisabledOption = (id) => {
      const index = (!!disabled?.value?.length && disabled?.value.findIndex(item =>
        Number(item.parent_id) === Number(id)
      )) || -1
      console.log(index)

      return index !== -1 ? true : false
    }
    const handleChangeCondition = (row) => {
      const newRows = rows.value
      const index = rows.value.findIndex(item => item.id == row.id)
      newRows[index].parent_id = row.parent_id
      rows.value = newRows
      ctx.emit('change:condition', {
        row,
        item: props.item
      })
    }
    const handleChangePercentage = (row) => {
      const newRows = rows.value
      const index = rows.value.findIndex(item => item.id == row.id)
      row.set_percentage == 'plus' && index != -1
        ? newRows[index].condition_sub_value = Math.abs(Number(newRows[index].condition_sub_value))
        : newRows[index].condition_sub_value = -Math.abs(Number(newRows[index].condition_sub_value))

      rows.value = newRows
    }
    const handleCheckValue = debounce((row) => {
      const newRows = rows.value
      const index = rows.value.findIndex(item => item.id == row.id)
      
      if (newRows[index].set_percentage == 'min' && Number(newRows[index].condition_sub_value) > 0) {
        newRows[index].condition_sub_value = -Math.abs(Number(newRows[index].condition_sub_value))
      }
      rows.value = newRows

      return rows.value
    }, 300)
    const handleApply  = (row) => {
      const index = rows.value.findIndex(item => item.id == row.id)

      ctx.emit('applyPricing', {
        conditionIndex: index,
        condition: row,
        item: props.item,
        index: props.keyComponent
      })
    }
    const handleShowTooltip = (row, value = false) => {
      const newRows = rows.value
      const index = rows.value.findIndex(item => item.id == row.id)
      
      if (newRows[index]?.show_tooltip !== undefined) {
        newRows[index].show_tooltip = value
      }
      rows.value = newRows

      return rows.value
    }

    return {
      me,
      rows,
      columns,
      options,
      disabled,
      handleApply,
      handleShowTooltip,
      handleCheckValue,
      filterFecthOption,
      filterDisabledOption,
      handleChangeCondition,
      handleChangePercentage,
    }
  }
})
</script>
<style lang="scss" scoped>
.table-price-manual {
  border-spacing: 0;
  @apply w-full border-b border-grey-01 border-opacity-80;

  thead {
    @apply bg-grey-01 bg-opacity-50;

    tr th {
      @apply py-4 px-3 text-left whitespace-nowrap;
    }
  }

  tbody {
    tr td {
      @apply py-4 px-3 text-left border-b border-grey-01;
    }
  }
}
</style>