import { computed } from 'vue'
import { useStore } from 'vuex'

export const useAlert = (moduleName) => {
  const store = useStore();

  const isActive = (id) =>
    computed(() => store.getters['alert/isAlertActive'](moduleName, id))

  const trigger = (id) => {
    store.dispatch('alert/triggerAlert', { module: moduleName, id })
  }

  const dismiss = (id) => {
    store.dispatch('alert/dismissAlert', { module: moduleName, id })
  }

  return {
    isActive,
    trigger,
    dismiss
  }
}
