<template>
  <div class="lg:w-2/5 space-y-4">
    <div>
      <div class="flex items-center gap-x-1">
        <label for="CategoryName">
          Category Name
        </label>
        <Tooltip placement="top">
          <template #content>
            <div class="w-64 text-sm">
              The category names are internal only and not customer-facing.
            </div>
          </template>
          <template #default>
            <i class="material-icons text-teal-04">info</i>
          </template>
        </Tooltip>
      </div>
      <div class="flex gap-x-4 items-center">
        <div class="flex-1">
          <Input
            v-model="form.group_name"
            name="CategoryName"
            type="text"
            placeholder="Category name"
            :view="(validation.group_name.$invalid && 'danger') || 'default'"
            class="w-full"
            :disabled="main?.synced"
          />
          <span
            v-for="n in validation.group_name.$errors.length"
            :key="n"
            class="text-red-base mt-2 block">
            Category name is required
          </span>
        </div>
        <div v-if="!!main?.synced" whitelabel="remove">
          <Tooltip placement="right">
            <template #content>
              <div
                class="xl:max-w-lg max-w-sm"
                style="word-wrap: break-word">
                <p>
                  This item's category name is locked because you are
                  using price sync
                  <template v-if="!!main.companyName">
                    from
                    <b>{{ main.companyName }}</b>
                  </template>.
                  <b>
                    <a
                      href="https://help.reusely.com/en/articles/9176424-how-price-sync-works"
                      target="_blank"
                      noreferrer
                      noopener
                      class="mx-2 text-white underline">
                      Read More
                    </a>
                  </b>
                </p>
              </div>
            </template>
            <template #default>
              <i class="material-icons icon-md select-none">lock</i>
            </template>
          </Tooltip>
        </div>
      </div>
    </div>
    <div>
      <label>Assign Your Condition Item</label>
      <div class="flex gap-x-4 items-center">
        <div class="flex-1">
          <MultiSelect
            v-model="form.categories"
            placeholder="Search items"
            label="name"
            track-by="category_id"
            :options="devicesComputed"
            :multiple="true"
            :hide-selected="false"
            :show-labels="false"
            :show-no-results="true"
            :show-no-options="true"
            :class="multiSelectClass"
            :disabled="main?.synced || !!priceEditor || disabledUniqueAndMac"
            required>
            <template #option="props">
              <p class="text-sm text-grey-03 mb-1">
                {{ props.option.brand }}
              </p>
              <p>{{ props.option.name }}</p>
            </template>
          </MultiSelect>
          <span
            v-for="o in validation.categories.$errors.length"
            :key="o"
            class="text-red-base mt-2 block">
            Item is required
          </span>
        </div>
        <div v-if="!!main?.synced" whitelabel="remove">
          <Tooltip placement="right">
            <template #content>
              <div
                class="xl:max-w-lg max-w-sm"
                style="word-wrap: break-word">
                <p>
                  This item's category name is locked because you are
                  using price sync
                  <template v-if="!!main?.companyName">
                    from
                    <b>{{ main?.companyName }}</b>
                  </template>.
                  <b>
                    <a
                      href="https://help.reusely.com/en/articles/9176424-how-price-sync-works"
                      target="_blank"
                      noreferrer
                      noopener
                      class="mx-2 text-white underline">
                      Read More
                    </a>
                  </b>
                </p>
              </div>
            </template>
            <template #default>
              <i class="material-icons icon-md select-none">lock</i>
            </template>
          </Tooltip>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'

import Input from 'bbUi/components/input/input.vue'
import MultiSelect from 'vue-multiselect'

export default defineComponent({
  name: 'ConditionCategory',
  components: {
    Input,
    MultiSelect
  },
  props: {
    mixin: {
      type: Object,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    priceEditor: {
      type: Boolean,
      default: false
    },
    priceEditorCategory: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue'],
  async setup(props, ctx) {    
    const {
      form,
      loading,
      devices,
      getDevices
    } = props.mixin
    const rules = {
      group_name: { required },
      categories: { required }
    }
    const validation = useVuelidate(rules, form, {
      $lazy: true
    })
    const multiSelectClass = computed(() => ({
      'multiselect--invalid': validation.value.categories.$invalid
    }))
    const devicesComputed =computed(() => {
      if (form?.categories?.length) {
        const isMac = [...form.categories].findIndex(category => category.is_mac)
        return isMac >= 0
          ? [...devices.value].filter(device => device.is_mac === 1)
          : [...devices.value].filter(device => device.is_mac === 0)
      }
      return devices.value
    })
    const route = useRoute()
    const disabledUniqueAndMac = computed(() => {
      if (!form?.categories?.length) return false

      const isMac = [...form.categories].findIndex(category => category.is_mac)
      return !!route?.query?.unique_id && isMac >= 0
    })

    if (!!props.main?.id) {
      form.group_name = props.main.formEdit.groupName
      form.categories = [...props.main?.formEdit?.categories].filter(category => !!category.is_checked)
    }
    await getDevices()

    if (!!props.priceEditor) {
      form.categories = [...devices.value].filter(device =>
        (device.category_id == props.priceEditorCategory.id) &&
        device.name?.toLowerCase() == props.priceEditorCategory?.name?.toLowerCase()
      )
    }

    const submit = async () => {
      await validation.value.$validate()

      return validation.value.$error ? false : true
    }

    return {
      form,
      submit,
      loading,
      devices,
      validation,
      getDevices,
      disabledUniqueAndMac,
      devicesComputed,
      multiSelectClass
    }
  }
})
</script>
