<template>
  <div class="h-full">
    <Fieldset class="h-full">
      <template #label>
        <span class="text-grey-04">Preview Condition</span>
      </template>
      <template #default>
        <div class="mb-4 p-4">
          <div class="my-2">
            <h2 class="text-lg">{{ conditionName }}</h2>
            <div v-html="sanitizedConditionGuideline" class="my-4"></div>
          </div>

          <ul class="list">
            <template
              v-for="(condition, conditionKey) of sanitizedConditionTerms"
              :key="conditionKey"
            >
              <li v-if="condition" v-html="condition"></li>
            </template>
          </ul>
        </div>
      </template>
    </Fieldset>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'
import { sanitize } from 'utils/dom'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'

export default defineComponent({
  name: 'ConditionFormPreview',
  components: {
    Fieldset
  },
  props: {
    conditionName: {
      type: String,
      default: ''
    },
    conditionGuideline: {
      type: String,
      default: ''
    },
    conditionTerms: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const sanitizedConditionTerms = computed(() =>
      props.conditionTerms.map((term) => {
        return sanitize(term || '')
      })
    )
    const sanitizedConditionGuideline = computed(() =>
      sanitize(props.conditionGuideline || '')
    )
    return {
      sanitizedConditionTerms,
      sanitizedConditionGuideline
    }
  }
})
</script>
