<template>
  <Table
    v-if="!!list?.length && !loading"
    class="w-full"
    :data="list">
    <TableColumn
      prop="name"
      min-width="160">
      <template #header>
        <span
          v-if="method === 1"
          class="capitalize">
          {{ sources[0]?.name }} Conditions
        </span>
        <span
          v-else
          class="capitalize">
          {{ category?.name }} Condition
        </span>
      </template>
      <template #default="{ row }">
        <span class="capitalize">{{ row.name }}</span>
      </template>
    </TableColumn>
    <TableColumn
      prop="condition_term"
      label="Description"
      min-width="300">
      <template #default="{ row }">
        <div class="space-y-2">
          <p v-html="row.term" />
          <div class="pl-4">
            <ul class="list-disc">
              <li
                v-for="(textTerm, n) in row.condition_term"
                :key="n">
                <span v-html="textTerm" />
              </li>
            </ul>
          </div>
        </div>
      </template>
    </TableColumn>
    <TableColumn
      v-if="method === 2"
      min-width="300"
      class-name="cell-custom">
      <template #header>
        <div class="flex items-center gap-x-2">
          <span>Map Data Source Condition to yours</span>
          <Tooltip
            placement="top">
            <span class="text-teal-04 material-icons leading-4" style="font-size: 1.25rem !important;">info</span>
            <template #content>
              <div class="w-72">
                When you map your own conditions to a Data Source, your own conditions will be the ones visible to your customers.
              </div>
            </template>
          </Tooltip>
        </div>
      </template>
      <template #default="{ row, $index }">
        <Select
          ref="selectCondtion"
          v-model="row.source_condition_id"
          v-bind="{
            ...(sources?.length > 1 &&
              {
              child: 'condition',
              optgroup: true,
              optgroupBy: 'condition',
              optgroupCheckbox: true,
            }),
            items: sources?.length > 1 ? row.options : sourceConditions
          }"
          :error="validation?.source_condition_id?.$error"
          by="id"
          name="name"
          placeholder="Choose Condition"
          :multiple="sources?.length > 1"
          block
          no-border
          max-height="200px"
          @change="handleUpdateConditionIDS($event, row, $index)">
          <template
            v-if="sources?.length > 1"
            #selected="{ selectedOption }">
            <div v-if="!!row?.source_condition_id?.length">
              <div v-if="selectedOption.length > 1" class="flex items-center gap-x-2">
                <span>Mixed</span>
                <span class="bg-teal-02 bg-opacity-30 capitalize text-sm px-2 py-0.5 rounded-full">
                  {{ selectedOption.length }} Source Selected
                </span>
              </div>
              <div v-else class="flex items-center gap-x-2">
                <span>{{ selectedOption?.name }}</span>
                <span class="bg-teal-02 bg-opacity-30 capitalize text-sm px-2 py-0.5 rounded-full">
                  {{ row?.source[0]?.name }}'s
                </span>
              </div>
            </div>
          </template>
          <template #item="{ item }">
            <div class="flex items-center justify-start gap-x-2">
              <span
                :class="[
                  'capitalize',
                  sources?.length > 1 && 'font-semibold'
                ]">
                {{ sources?.length > 1 ? `${item.name}'s Conditions` : item.name }}
              </span>
              <Tooltip
                v-if="sources?.length === 1 && item?.condition_term?.length"
                :disabled="!item?.condition_term?.length"
                placement="top">
                <template #content>
                  <div
                    :style="{ width: '320px' }"
                    class="space-y-2">
                    <p v-html="item?.term || ''" />
                    <ul
                      v-if="item?.condition_term?.length"
                      class="list-disc list-outside mx-4 space-y-1">
                      <li
                        v-for="(term, nTerm) in  item?.condition_term"
                        :key="`${nTerm}_${$index}`"
                        class="text-sm">
                        {{ term }}
                      </li>
                    </ul>
                  </div>
                </template>
                <template #default>
                  <i :class="!item?.condition_term?.length && 'opacity-0'" class="material-icons">info</i>
                </template>
              </Tooltip>
              <div
                v-if="!item?.has_price && sources?.length <= 1"
                class="bg-orange-01 bg-opacity-50 text-orange-03 px-2 py-0.5 rounded-full">
                <div class="flex items-center text-xs justify-between gap-x-1">
                  No Price
                  <div class="relative w-6 h-6 flex items-center justify-center">
                    <span class="absolute z-0 left-1/2 transform -translate-x-1/2 -translate-y-1/2 top-1/2">{{ currencySymbol }}</span>
                    <span
                      class="material-icons z-10 absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 top-1/2"
                      style="font-size: 1.625rem !important; line-height: 1;">
                      block
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </template>
          <template
            v-if="sources?.length > 1 || isDifferentRules"
            #child="{ childItem, childIndex }">
            <Tooltip placement="top" :disabled="(!childItem.disabled && !childItem.already_selected_other)">
              <template #content>
                <div class="w- text-sm">
                  You can only choose one condition per one source
                </div>
              </template>
              <template #default>
                <div class="flex items-center justify-start gap-x-2">
                  <span class="capitalize">
                    {{ childItem?.name }}
                  </span>
                  <Tooltip
                    v-if="sources?.length > 1 && childItem?.condition_term?.length"
                    placement="top" :disabled="((childItem.disabled || childItem.already_selected_other)) || !childItem?.condition_term?.length">
                    <template #content>
                      <div
                        :style="{ width: '320px' }"
                        class="space-y-2">
                        <p v-html="childItem?.term || ''" />
                        <ul
                          v-if="childItem?.condition_term?.length"
                          class="list-disc list-outside mx-4 space-y-1">
                          <li
                            v-for="(term, nTermChild) in childItem?.condition_term"
                            :key="`${childIndex}_${nTermChild}`"
                            class="text-sm">
                            {{ term }}
                          </li>
                        </ul>
                      </div>
                    </template>
                    <template #default>
                      <i :class="!childItem?.condition_term?.length && 'opacity-0'" class="material-icons">info</i>
                    </template>
                  </Tooltip>
                  <div
                    v-if="!childItem?.has_price && sources.length > 1"
                    class="bg-orange-01 bg-opacity-50 text-orange-03 px-3 py-1 rounded-full">
                    <div class="flex items-center justify-between gap-x-1">
                      No Price
                      <div class="relative w-6 h-6 flex items-center justify-center">
                        <span class="absolute z-0 left-1/2 transform -translate-x-1/2 -translate-y-1/2 top-1/2">{{ currencySymbol }}</span>
                        <span
                          class="material-icons z-10 absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 top-1/2"
                          style="font-size: 1.625rem !important; line-height: 1;">
                          block
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </Tooltip>
          </template>
          <template #noChild="{ item }">
            <div class="">
              No Condition
              <p class="text-xs">
                {{ item?.name }}
                don't have any conditions set yet
              </p>
            </div>
          </template>
          <template v-if="Array.isArray(row?.source_condition_id) ? row?.source_condition_id?.length : !!row?.source_condition_id" #append="{ hide }">
            <button
              class="bg-grey-01 bg-opacity-20 px-4 py-3 mt-1 text-base text-center text-grey-02 hover:text-grey-03 w-full"
              @click="handleResetConditionIDS(hide, row)">
              Clear Selection
            </button>
          </template>
        </Select>
        <small
          v-if="validation?.source_condition_id?.$error"
          class="text-red-04">
          Please select at least one condition.
        </small>
      </template>
    </TableColumn>
    <TableColumn
      :label="showColumns ? 'Pricing Rules' : ''"
      :min-width="showColumns ? '360' : ''">
      <template #default="{ row }">
        <div v-if="(isSameRulesForOneSource && row.is_duplicate) || sources?.length > 1 || isDifferentRules || companySource">
          <div
            v-if="row.source_condition_id || row?.source_condition_id?.length"
            class="w-full flex items-center justify-end">
            <div class="flex-1 space-y-2">
              <div class="max-w-lg">
                <RadioGroup
                  v-model="row.pricingRuleType"
                  class="radio--full"
                  @change="handleChangeRuleType(row)">
                  <RadioButton label="3">Match</RadioButton>
                  <RadioButton label="2">Markup</RadioButton>
                  <RadioButton label="1">Markdown</RadioButton>
                </RadioGroup>
              </div>
              <div
                v-if="row.pricingRuleType != 3"
                class="grid grid-cols-3 max-w-lg gap-2">
                <RadioGroup
                  v-model="row.amountType"
                  dense
                  class="radio--full mb-4 col-span-2"
                  @change="handleChangeRuleAmountType(row)">
                  <RadioButton label="1">
                    % Percentage
                  </RadioButton>
                  <RadioButton label="0">
                    {{ currencySymbol }} Fixed
                  </RadioButton>
                </RadioGroup>
                <Tooltip
                  v-model="row.showTooltip"
                  placement="top"
                  manual>
                  <template #content>
                    <p class="max-w-xs text-sm">Items priced at {{ currencyPrefix == 1 ? `${currencySymbol}0` : `0${currencySymbol}` }} are not subject to pricing rules.</p>
                  </template>
                  <Input
                    v-model="row.amountPrice"
                    :right="row.amountPrice == '0' ? false : true"
                    :max="row.amountType == 1 ? (row.pricingRuleType == 2 ? undefined : 100) : 'auto'"
                    :min="0"
                    type="number"
                    class="w-full col-span-1"
                    icon-block
                    icon
                    @change="handleChangeRuleAmountPrice(row)"
                    @focus-in="row.showTooltip = true"
                    @focus-out="row.showTooltip = false">
                    <template #icon>
                      <span
                        v-if="row.amountType == '0'"
                        class="text-grey-03">
                        {{ currencySymbol }}
                      </span>
                      <span
                        v-else
                        class="text-grey-03">
                        %
                      </span>
                    </template>
                  </Input>
                </Tooltip>
              </div>
            </div>
          </div>
        </div>
      </template>
    </TableColumn>
  </Table>
</template>

<script>
import { defineComponent, computed, ref, nextTick, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'

import Select from 'components/Select.vue'
import Input from 'bbUi/components/input/input.vue'
import { required, helpers } from '@vuelidate/validators'

export default defineComponent({
  name: 'BulkPriceEditorMappingMethodConditions',
  components: {
    Select,
    Input
  },
  props: {
    conditions: {
      type: Array,
      default: () => []
    },
    sources: {
      type: Array,
      default: () => []
    },
    sourceConditions: {
      type: Array,
      default: () => []
    },
    category: {
      type: Object,
      default: () => ({})
    },
    companySource: {
      type: Boolean,
      default: false
    },
    method: {
      type: Number,
      default: 1
    },
    loading: {
      type: Boolean,
      default: false
    },
    differentRules: {
      type: Boolean,
      default: false
    },
    currencySymbol: {
      type: String,
      default: '$'
    },
    currencyPrefix: {
      type: [String, Number],
      default: 1
    }
  },
  emits: ['update:conditions', 'update:different', 'update:ids'],
  setup(props, ctx) {
    const showTooltip = ref(false)
    const list = computed({
      get: () => props.conditions.map(condition => ({
        ...condition,
        showTooltip: condition?.showTooltip !== undefined ? condition.showTooltip : false
      })),
      set: (val) => ctx.emit('update:conditions', val)
    })
    const isDifferentRules = computed(() => {
      if (!list.value?.length) return false

      const [firstEl, ...othersEl] = props.conditions
      const { amountPrice, amountType, pricingRuleType } = firstEl
      const isConsistent = othersEl.every(
        (row) =>
          row?.amountPrice === amountPrice &&
          row?.amountType === amountType &&
          row?.pricingRuleType === pricingRuleType
      )

      const result = !isConsistent

      return result
    })
    const isSameRulesForOneSource = computed(() => {
      if (!props.sources?.length || props.method === 1 || !list.value.length) return false
      const ids = list.value.map(item => item?.source_condition_id).filter(id => !!id)
      const duplicates = ids.filter((id, index) => ids.indexOf(id) !== index)

      if (duplicates.length > 0) return true

      return false
    })
    const showColumns = computed(() => {
      return isSameRulesForOneSource.value || props.sources?.length > 1 || isDifferentRules.value || props.companySource
    })

    const atLeastOneSelected = helpers.withMessage(
      'Please select at least one condition.',
      () => list.value.some(item => item.source_condition_id)
    )

    const rules = computed(() => props.method === 2 ? ({
      source_condition_id: { atLeastOneSelected }
    }) : { source_condition_id: {} })
    const validation = useVuelidate(rules, list, { $lazy: true })

    const handleChangeRuleType = async row => {
      // Always reset amountPrice to 0 when type is Match (3)
      if (row.pricingRuleType == 3) {
        row.amountPrice = 0
        return // No need to proceed with other validations
      }

      // Switching from Fixed to Percentage (Markdown only), normalize large value to 100
      if (row.amountType == 1 && row.pricingRuleType == 1 && Number(row.amountPrice) > 100) {
        row.amountPrice = 100
      }

      await nextTick()
      // row.amountPrice = 0
      // row.amountType = "1"
      ctx.emit('update:conditions', list.value)
      return row
    }
    const handleChangeRuleAmountType = async row => {
      handleChangeRuleType(row)
      await nextTick()
      ctx.emit('update:conditions', list.value)
      return row
    }
    const handleChangeRuleAmountPrice = row => {
      ctx.emit('update:conditions', list.value)
      return row
    }
    /*
      update from 'https://app.clickup.com/t/86cxr4kj2'

      const validationSelectionMultiple = ({row, index, indexChild}, selected) => {
        list.value[row].options[index].condition = list.value[row].options[index].condition.map(item => ({
          ...item,
          disabled: Boolean(selected?.length && !item.checked && selected?.find(i => i.sub_domain === item.sub_domain))
        }))

        // For validation if the condition was selected in another row
        list.value.forEach((item, key, self) => {
          item.options[index].condition[indexChild].already_selected_other = false
          if (key !== row) {
            item.options[index].condition[indexChild].disabled = !item?.options[index]?.condition[indexChild]?.disabled ? true : false
            if (item?.options[index]?.condition[indexChild]?.disabled) {
              item.options[index].condition[indexChild].already_selected_other = true
            }
          }
          return item
        })
      }
    */
    const handleUpdateConditionIDS = (e, value, index) => {
      list.value = [...list.value].map(item => {
        if (value.id === item.id) {
          item.source_condition_id = value?.source_condition_id
          if (props.sources.length > 1) {
            // (type 1 for add, type 0  for remove existing selected)
            if (e.type === 1) {
              value.source = [...value.source, {
                sub_domain: e?.head?.sub_domain,
                name: e?.head?.name,
                id: e?.option?.id
              }]
            } else {
              value.source = value.source.filter(
                item => !(item.id === e?.option?.id && item.sub_domain === e?.head?.sub_domain)
              )
            }
          }
        }

        return item
      })

      /*
        update from 'https://app.clickup.com/t/86cxr4kj2'

        if (props.companySource && props.sources.length > 1) {
          validationSelectionMultiple({
            row: index,
            index: e.index,
            indexChild: e.indexChild
          }, value.source)
        }
      */
    }
    const handleResetConditionIDS = (callback, value) => {
      list.value = [...list.value].map(item => {
        if (item.id === value.id) {
          item.source_condition_id = null
          if (!props?.sources || (props?.sources?.length === 1)) {
            item.source = {}
          } else if (props?.sources?.length > 1) {
            item.source = []
            item.source_condition_id = []
            item.options = [...item.options].map(option => ({
              ...option,
              condition: [...option?.condition].map(cond => ({
                ...cond,
                checked: false,
                disabled: false
              }))
            }))
          }
        }

        return item
      })

      typeof callback === 'function' && callback()
    }
    const onSubmit = async () => {
      return await validation.value.$validate()
    }

    watch(() => isDifferentRules.value, (val) => {
      ctx.emit('update:different', val)
    }, { immediate: true })

    return {
      list,
      showTooltip,
      isDifferentRules,
      showColumns,
      handleUpdateConditionIDS,
      handleResetConditionIDS,
      handleChangeRuleType,
      handleChangeRuleAmountType,
      handleChangeRuleAmountPrice,
      isSameRulesForOneSource,
      validation,
      onSubmit
    }
  }
})
</script>