<template>
  <div class="space-y-4 border border-grey-01 p-2 rounded-lg">
    <div class="flex items-center">
      <div class="mr-2">
        <svg
          width="80"
          height="80"
          viewBox="-18.5 -18.5 185 185"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          style="transform: rotate(-90deg)"
        >
          {{ calculatedOffset }}
          <circle
            r="64"
            cx="74"
            cy="74"
            fill="transparent"
            stroke="#e0e0e0"
            stroke-width="12"
            :stroke-dasharray="`${baseOffset}px`"
            stroke-dashoffset="0"
          ></circle>
          <circle
            r="64"
            cx="74"
            cy="74"
            stroke="#3b82f6"
            stroke-width="12"
            stroke-linecap="round"
            :stroke-dashoffset="`${calculatedOffset}px`"
            fill="transparent"
            :stroke-dasharray="`${baseOffset}px`"
          ></circle>
          <text
            :x="
              progressPercentage < 100
                ? progressPercentage < 25
                  ? '50px'
                  : '42px'
                : '40px'
            "
            y="87px"
            fill="#262626"
            font-size="32px"
            font-weight="bold"
            style="
              transform: rotate(90deg) translate(0, -144px);
              text-align: center;
            "
          >
            {{ progressPercentage || 0 }}%
          </text>
        </svg>
      </div>
      <div class="mr-4">
        <p>Status: Updating</p>
        <h3 class="text-xl font-medium">Price Editor</h3>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'

import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'PriceUploadInProgress',
  components: {
    Button
  },
  setup(props, ctx) {
    // const { processingStatus } = useISBN()
    const baseOffset = ref(401.92)
    const progressPercentage = computed(
      // processingStatus.value.progress
      () => 80 || 0
    )
    const calculatedOffset = computed(
      () =>
        // baseOffset.value - (baseOffset.value * progressPercentage.value) / 100
        baseOffset.value - (baseOffset.value * progressPercentage.value) / 100
    )
    return {
      progressPercentage,
      baseOffset,
      calculatedOffset
    }
  }
})
</script>
