<template>
  <div class="max-w-lg">
    <RadioGroup
      v-model="form.type"
      class="radio--full"
      :disabled="form?.selectedConditions?.length <= 0"
      @change="handleChangeType">
      <RadioButton label="2">Markup</RadioButton>
      <RadioButton label="1">Markdown</RadioButton>
    </RadioGroup>
  </div>
  <div
    v-if="['1', '2'].includes(form?.type)"
    class="grid grid-cols-2 max-w-lg gap-2">
    <RadioGroup
      v-model="form.amountType"
      class="radio--full mb-4"
      @change="handleChangeType">
      <RadioButton label="1">
        % Percentage
      </RadioButton>
      <RadioButton label="0">
        {{ currencySymbol }} Fixed
      </RadioButton>
    </RadioGroup>
    <Tooltip
      v-model="showTooltip"
      placement="top"
      manual>
      <template #content>
        <p class="max-w-xs text-sm">Items priced at {{ currencyPrefix == 1 ? `${currencySymbol}0` : `0${currencySymbol}` }} are not subject to pricing rules.</p>
      </template>
      <Input
        v-model="form.amountPrice"
        :right="form.amountType == '0' ? false : true"
        :min="0"
        :max="form.amountType == 1 ? (form.type == 2 ? undefined : 100) : 'auto'"
        type="number"
        class="w-full"
        icon-block
        icon
        @focus-in="showTooltip = true"
        @focus-out="showTooltip = false">
        <template #icon>
          <span
            v-if="form.amountType == '0'"
            class="text-grey-03">
            {{ currencySymbol }}
          </span>
          <span
            v-else
            class="text-grey-03">
            %
          </span>
        </template>
      </Input>
    </Tooltip>
  </div>
</template>
<script>
import { defineComponent, computed, ref } from 'vue'

import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'BulkPriceEditorMassPricingRules',
  components: {
    Input
  },
  props: {
    currencySymbol: {
      type: String,
      default: () => '-'
    },
    currencyPrefix: {
      type: [String, Number],
      default: () => 1
    },
    modelValue: {
      type: Object,
      default: () => ({
        type: null,
        amountType: null,
        amountPrice: null,
        conditions: []
      })
    }
  },
  emits: ['update:modelValue'],
  setup(props, ctx) {
    const showTooltip = ref(false)
    const form = computed({
      get: () => props.modelValue,
      set: (val) => {
        ctx.emit('update:modelValue', val)
      }
    })

    const handleChangeType = (e) => {
      // Switching from Fixed to Percentage (Markdown only), normalize large value to 100
      if (form.value.amountType == 1 && form.value.type == 1 && Number(form.value.amountPrice) > 100) {
        form.value.amountPrice = 100
      }
    }

    return {
      form,
      showTooltip,
      handleChangeType
    }
  }
})
</script>
