<template>
  <div class="space-y-4">
    <div class="p-4 pb-6 rounded-xl space-y-6 border border-grey-01">
      <div class="border border-grey-01 rounded-lg">
        <div
          class="flex items-center justify-between p-4 cursor-pointer"
          @click="showInstructions = !showInstructions">
          <p class="text-sm font-semibold">Ensure no price cells are left blank. Price upload won’t work with items under Price Sync.</p>
          <div class="flex items-center justify-between gap-x-1">
            <p class="text-sm text-grey-03">
              Show {{ !showInstructions ? 'More' : 'Less' }}
            </p>
            <span
              :class="[
                'material-icons',
                'transform transition-all',
                'ease-out duration-300',
                showInstructions && '-rotate-180'
              ]"
              class="material-icons">expand_more</span>
          </div>
        </div>
        <Transition name="slide-fade">
          <div v-if="showInstructions" class="px-4 pb-4 space-y-4 text-sm text-grey-03">
            <p>In the template, items or conditions priced at $0 won't appear in the Widget after upload, and any blank price cells will trigger a validation error. Your upload will continue, and you will see a validation report with a list of errors, if any, once it is complete.</p>
            <p>Price upload won't work for items under price sync; it only applies to manually priced items. During price uploads, you won't be able to make changes to prices, the catalog, item conditions, or the price editor, and the settings for these will not be accessible. Also, do not alter anything other than prices in the template, as modifying or deleting any other data will cause the affected items to not appear on your Widget.</p>
          </div>
        </Transition>
      </div>
      <ul class="list-disc text-sm mx-4 text-grey-04">
        <li>$0 items or conditions won’t appear in the Widget.</li>
        <li>Blank price cells will cause a validation error.</li>
        <li>Price upload works only for manually priced items, not those under price sync.</li>
        <li>During price uploads, edits and settings for prices, the catalog, and conditions will be inaccessible.</li>
        <li>Only edit prices; changing other data may remove items from the Widget.</li>
      </ul>
      <div class="bg-blue-01 bg-opacity-50 rounded-md flex gap-4 p-4 text-blue-03">
        <span class="material-icons">tips_and_updates</span>
        <p class="text-sm">Pro Tip: If you don’t want to buy an item or a condition in the template, set its price to $0, then upload it, and it won’t be visible to your customers.</p>
      </div>
      <div class="space-y-2">
        <div class="flex flex-wrap gap-4">
          <Tooltip
            v-for="templateFormat in templates"
            placement="top"
            :key="templateFormat.id">
            <Button
              :disabled="disabled || loadingDownload"
              color="teal"
              :view="disabled || loadingDownload ? '' : 'outlined'"
              dense
              @click="handleDownload(templateFormat.id)">
              <div class="flex items-center gap-x-2">
                <span class="material-icons">download</span>
                {{ templateFormat.name }}
              </div>
            </Button>
            <template #content>
              <p class="w-80 text-sm">{{ templateFormat.tooltip }}</p>
            </template>
          </Tooltip>
        </div>
        <Transition name="fade">
          <div v-if="loadingDownload">
            <p class="text-left text-xs text-grey-03">Downloading {{ templateSelected }} template...</p>
            <div class="w-full bg-grey-01 bg-opacity-80 rounded-full h-1">
              <div
                class="bg-teal-04 h-1 rounded-full transition-all ease-out duration-300"
                :style="{ width: `${state?.downloadProgress || 0}%` }">
              </div>
            </div>
          </div>
        </Transition>
      </div>
    </div>
  </div>
  <div v-if="loadingDownload">
    <Teleport to="body">
      <Modal
        v-model="modalCancel"
        size="xs"
        hide-header
        hide-footer>
        <template #default>
          <div class="py-10 space-y-8">
            <div class="space-y-4">
              <div class="flex items-center justify-center flex-col gap-y-4">
                <img
                  src="@/assets/icon/warning.svg"
                  alt="Warning icon"
                />
                <h6>Download in Progress</h6>
                <p class="text-center text-grey-02">A template file is currently downloading {{ templateSelected || 'CSV/XLSX' }}. If you close this window, your download will be interrupted.</p>
              </div>
            </div>
            <div>
              <p class="text-left text-sm">Downloading Progress</p>
              <div class="w-full bg-grey-01 bg-opacity-80 rounded-full h-1">
                <div
                  class="bg-teal-04 h-1 rounded-full transition-all ease-out duration-300"
                  :style="{ width: `${state?.downloadProgress}%` }">
                </div>
              </div>
            </div>
            <div class="flex items-center justify-center gap-x-6">
              <a href="#" @click.prevent="handleContinue">Continue Download</a>
              <Button color="orange" @click="handleAbort">Close Anyway</Button>
            </div>
          </div>
        </template>
      </Modal>
    </Teleport>
  </div>
</template>

<script>
import { computed, defineComponent, nextTick, ref } from 'vue'
import { usePriceUploadDownloadTemplate } from 'composable/pricing'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'

export default defineComponent({
  name: 'PriceUploadDownloadTemplate',
  components: {
    Button,
    Modal
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    modalCancel: {
      type: Boolean,
      default: false
    }
  },
  emits: ['download', 'continueDownload', 'confirmClose'],
  setup(props, ctx) {
    const pricingState = computed(() => state.pricing?.processing)
    const  {
      state,
      progress,
      templates,
      templateSelected,
      showInstructions,
      action,
      cancelDownload,
      saveStateDownload
    } = usePriceUploadDownloadTemplate()

    const loadingDownload = computed(() => {
      const temp = [...templates.value].find(i => i.loading)
      
      return temp?.loading || state.value?.downloadProgress > 0 || false
    })

    const handleDownload = async (e) => {
      ctx.emit('download', true)
      await action(e)
      ctx.emit('download', false)
    }
    const handleContinue = () => ctx.emit('continueDownload')
    const handleAbort = async () => {
      saveStateDownload()
      await nextTick()
      cancelDownload()
      await nextTick()
      ctx.emit('confirmClose')
    }

    if (state.pricing?.processing?.downloadProgress > 0) {
      templateSelected.value = state.value?.typeDownload
      progress.value = state.value?.downloadProgress
    }

    return {
      state,
      progress,
      templates,
      pricingState,
      loadingDownload,
      showInstructions,
      templateSelected,
      handleDownload,
      handleContinue,
      handleAbort
    }
  }
})
</script>