<template>
  <Modal
    v-model="show"
    title="Delete Condition"
    @close="cancelCondition">
    <h6 class="font-normal mb-5 text-lg">
      Are you sure want to delete this condition?
    </h6>
    <template #footer>
      <Button
        color="red"
        :loading="loading.delete"
        @click="deleteCondition">
        Delete
      </Button>
    </template>
  </Modal>
</template>
<script>
import { computed, defineComponent, ref } from 'vue'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'ConditionModalDelete',
  components: {
    Modal,
    Button
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'delete', 'cancel'],
  setup(props, ctx) {
    const show = computed({
      get: () => props.modelValue,
      set: (val) => {
        ctx.emit('update:modelValue', val)
      }
    })
    const loading = ref(false)

    const deleteCondition = () => ctx.emit('delete')
    const cancelCondition = () => ctx.emit('cancel')

    return {
      show,
      loading,
      deleteCondition,
      cancelCondition
    }
  }
})
</script>
