<template>
  <BDropdown block>
    <template #default="{ isExpanded }">
      <button class="border rounded-lg block w-full px-5 py-2.5 border-grey-01">
        <div class="flex items-center justify-between">
          <p
            v-if="!selectedOptions.length"
            class="text-base text-grey-02">
            Select Condition
          </p>
          <template v-if="selectedOptions.length && selectedOptions.length === optionsFlat?.length && !hasEmptyConditions">
            <div>All Condition</div>
          </template>
          <template v-else-if="selectedOptions.length > 0">
            <div>{{ selectedOptions.length }} Condition Selected</div>
          </template>
          <span
            :class="[
              'transform material-icons',
              'text-grey-03',
              isExpanded && 'rotate-180'
            ]">
            arrow_drop_down
          </span>
        </div>
      </button>
    </template>
    <template #dropdown>
      <div class="max-h-72 py-4">
        <template v-if="multipleItems">
          <ul
            v-for="(option, index) in options"
            :key="index"
            class="space-y-2">
            <li class="space-y-2">
              <span class="font-semibold px-4">{{ option[groupLabel] }}</span>
              <ul>
                <template v-if="option[childKey]?.length > 0">
                  <li
                    v-for="(childOption, childIndex) in option[childKey]"
                    :key="`${index}_${childIndex}`"
                    class="hover:bg-teal-01 hover:bg-opacity-30 px-4 py-3">
                    <Checkbox
                      v-model="selectedOptions"
                      :value="childOption"
                      :id="childIndex">
                      <span class="font-normal">
                        {{ childOption[label] }}
                      </span>
                    </Checkbox>
                  </li>
                </template>
                <template v-else>
                  <li class="px-4 py-3">
                    <p class="text-grey-03 text-sm">
                      {{ option[groupLabel] || 'The item or model' }}  do not have a condition yet.
                    </p>
                  </li>
                </template>
              </ul>
            </li>
          </ul>
        </template>
        <template v-else>
          <ul>
            <template v-if="singleOptions?.length > 0">
              <li
                v-for="(option, index) in singleOptions"
                :key="index"
                class="hover:bg-teal-01 hover:bg-opacity-30 px-4 py-3">
                <Checkbox
                  v-model="selectedOptions"
                  :value="option"
                  :id="index">
                  <span class="font-normal">
                    {{ option[label] }}
                  </span>
                </Checkbox>
              </li>
            </template>
            <template v-else>
              <li class="px-4 py-3">
                <p class="text-grey-03 text-sm text-center">
                  {{ options[0]?.name || 'The item or model' }} do not have a condition yet.
                </p>
              </li>
            </template>
          </ul>
        </template>
      </div>
    </template>
  </BDropdown>
</template>

<script>
import { defineComponent, computed, onMounted, watch } from 'vue'

import BDropdown from 'components/BDropdown/BDropdown.vue'
import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  name: 'BulkPriceEditorMassPricingCondition',
  components: {
    BDropdown,
    Checkbox,
    Input
  },
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    label: {
      type: String,
      default: 'label'
    },
    groupLabel: {
      type: String,
      default: 'name'
    },
    childKey: {
      type: String,
      default: 'conditions'
    },
    value: {
      type: String,
      default: undefined
    },
    products: {
      type: Array,
      default: () => []
    },
    conditions: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update-conditions', 'update:modelValue'],
  setup(props, ctx) {
    const selectedOptions = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const options = computed(() => props.conditions)
    const optionsFlat = computed(() => props.conditions.map(i => i.conditions)?.flat())
    const hasEmptyConditions = computed(() => props.conditions.some(item => item.conditions.length === 0))
    const multipleItems = computed(() => props?.products?.length > 1)
    const singleOptions = computed(() => {
      if (!multipleItems.value) {
        return options.value[0]?.conditions || []
      }
      return []
    })

    onMounted(() => {
      selectedOptions.value = optionsFlat.value || []

      watch(() => optionsFlat?.value, () => {
        selectedOptions.value = optionsFlat.value || []
      }, { deep: true })
    })

    return {
      selectedOptions,
      multipleItems,
      options,
      singleOptions,
      optionsFlat,
      hasEmptyConditions
    }
  }
})
</script>
