<template>
  <div class="space-y-6">
    <ExpandablePanel
      v-for="(item, n) in items"
      v-model="item.collapse"
      :key="`${item.parent_id}_${item.id}`"
      :ref="setItemRefs(n)">
      <template #header="{ toggle }">
        <div class="grid grid-cols-2 action-child">
          <div
            class="flex items-center gap-x-2 cursor-pointer"
            @click="handleExpandItem(item, toggle)">
            <div
              :class="{
                'el-table__expand-icon el-table__expand-icon--expanded': item.collapse,
                'el-table__expand-icon': !item.collapse
              }">
              <i v-loading="item.loading" class="el-icon-arrow-right" />
            </div>
            <div class="flex items-center gap-2">
              <p class="mb-0">{{item.name}}</p>
              <Tooltip
                v-if="item?.missing_price_flags"
                placement="top">
                <span class="text-red-04 material-icons leading-4" style="font-size: 1.25rem !important;">flag</span>
                <template #content>
                  <div class="max-w-sm">
                    This item is flagged due to unavailable prices for certain conditions. Expand the submenu to manually price them.
                  </div>
                </template>
              </Tooltip>
            </div>
            <div
              v-if="!!item?.child"
              class="ml-auto flex z-10"
              :class="{ 'hidden': !item.collapse }"
              tabindex="0">
            </div>
          </div>
          <div
            v-if="sycnablePrice == 1 && !priceScraper"
            :class="[
              'flex items-center gap-x-2',
              item.is_price_sync != 0 && sycnablePrice ? 'ml-11' : 'ml-12'
            ]">
            <SyncPricing
              v-if="sycnablePrice"
              v-model="item.is_price_sync"
              :is-syncable="sycnablePrice"
              :loading="item.loading"
              :item="item"
              :disabled-action="disabledAction"
              @loaded-price="handleLoadedPrice"
              @update:modelValue="changeAutomatedPricing(item, n, { toggle, isExpanded })"
            />
            <SyncPricing
              v-else
              v-model="item.is_price_sync"
              :item="item"
              :disabled-action="disabledAction"
            />
            <div class="action-btn-child">
              <Button
                v-if="item.is_price_sync != 0 && sycnablePrice"
                :loading="item.loading"
                color="teal"
                dense
                :disabled="disabledAction.priceUploadByProgress"
                @click="editAutomate(item, n)">
                <span class="material-icons text-sm">edit</span>
              </Button>
            </div>
          </div>
        </div>
      </template>
      <template #default>
        <div>
          <div v-show="item.collapse">
            <template v-if="Array.isArray(item.devices?.data)">
              <div class="space-y-8">
                <div
                  :id="`device-list-${item.id}`"
                  class="grid grid-cols-8">
                  <div class="col-span-8">
                    <Input
                      v-if="item.devices.filters"
                      :value="item.devices.filters.search"
                      @update:modelValue="val => handleFetchDevice(item, val, 'search')"
                      placeholder="Search product"
                      block
                      icon>
                      <template #icon>
                        <span class="material-icons">search</span>
                      </template>
                    </Input>
                    <Alert v-if="item?.missing_price_flags" color="red" view="smooth" class="mt-4" dense>
                      <div class="flex items-center">
                        <i class="material-icons mr-2">info</i> <p>Conditions priced at $0 must be manually priced to visible to your customers.</p>
                      </div>
                    </Alert>
                    <div
                      v-if="item.is_price_sync && !disabledAction.priceUploadByProgress"
                      class="flex justify-end mt-4">
                      <Button
                        dense
                        :loading="loadingCustomPrice.resetBulk.has(item.id) || item.loading || isLoading"
                        color="teal"
                        @click="handleResetPriceSyncCustomPriceBulk(item)">
                        <span class="text-sm mx-2">
                          <i class="material-icons mr-2">history</i>
                          Reset Manually Priced Products
                        </span>
                      </Button>
                    </div>
                  </div>
                </div>
                <div
                  v-if="!item?.conditions?.length && !disabledAction.priceUploadByProgress"
                  class="text-center mb-8">
                  <span class="text-grey-04">
                    Please setup your
                    <router-link
                      :to="{
                        name: 'settingCondition'
                      }">
                      conditions
                    </router-link>
                    first
                  </span>
                </div>
                <Table
                  v-else
                  class="w-full"
                  :data="item?.devices?.data || []" v-loading="item.loading || isLoading">
                  <TableColumn
                    v-if="route?.params?.tab === 'mac'"
                    label="MPN"
                    min-width="120">
                    <template #default="scope">
                      <span v-if="scope.row.mpn">
                        <span v-for="(item, index) in scope.row.mpn.split(',')" :key="index">
                          {{ item }}<span v-if="index < scope.row.mpn.split(',')?.length - 1">,</span><br />
                        </span>
                      </span>
                      <span v-else>
                        -
                      </span>
                    </template>
                  </TableColumn>
                  <TableColumn
                    prop="product_name"
                    label="Item"
                    :min-width="route?.params?.tab === 'mac' ? '350' : '150'"
                  />
                  <template
                    v-for="(condition, nCondition) in item.conditions_sources"
                    :key="`${nCondition}_${condition.id}`">
                    <TableColumn
                      :label="condition.name"
                      :min-width="route?.params?.tab === 'mac' && item.is_price_sync ? '220' : (route?.params?.tab === 'mac' ? '150' : '230')"
                      >
                      <template #header>
                        <div class="flex flex-col">
                          <div class="flex justify-start items-center gap-x-2">
                            <div class="flex flex-col gap-0">
                              <div>{{ condition.name }}</div>
                            </div>
                            <Popover
                              :width="250"
                              placement="bottom-start"
                              trigger="hover">
                              <h6 class="font-semibold text-base text-black mb-3">
                                Condition: {{ condition.name }}
                              </h6>
                              <p
                                v-html="condition.title_rule"
                                class="text-dark mb-2"
                              />
                              <ul class="list w-full">
                                <template
                                  v-for="(rule, n) in condition.rules"
                                  :key="n">
                                  <li class="break-normal">
                                    {{ rule }}
                                  </li>
                                </template>
                              </ul>
                              <template #reference>
                                <div class="text-teal-04 text-xs">
                                  <span class="material-icons text-lg">info</span>
                                </div>
                              </template>
                            </Popover>
                          </div>
                          <span
                            v-if="condition.is_mapping && condition.source && !priceSharing && !hasMultipleSources"
                            class="text-xs text-grey-02 capitalize">
                            {{ condition.source_name }}
                          </span>
                        </div>
                      </template>
                      <template #default="scope">
                        <div v-if="!!scope.row?.price?.length">
                          <DeviceInput
                            :data="{
                              condition,
                              row:
                              scope.row,
                              items,
                              index: nCondition
                            }"
                            :is-automated-price="item.is_price_sync"
                            :has-pricing-source="!!item.pricing_source"
                            :disabled="disabledAction.priceUploadByProgress || (
                              ((!!item.is_price_sync &&
                              !scope.row.price[nCondition].is_custom_price) || (
                                !item.is_price_sync &&
                                !!scope.row.price[nCondition].is_subcondition
                              )) &&
                              !scope.row.price[nCondition].is_editable_custom_price
                            )"
                            @focus-out="handleDeviceUpdate"
                            @set-custom-price="handleSetPriceSyncCustomPrice(scope.row, nCondition)"
                            @unset-custom-price="handleUnsetPriceSyncCustomPrice(scope.row, nCondition)"
                            @reset-custom-price="handleResetCustomPrice(scope.row, nCondition, item)"
                            @set-original-price="handleSetOriginalPrice(scope.row, nCondition)"
                            @save-custom-price="handleSaveCustomPrice(scope.row, nCondition, item, actionUpdate)"
                          />
                          <div
                            v-if="!item.is_price_sync"
                            class="text-right">
                            <small class="text-right">
                              {{
                                scope.row.price[nCondition].last_update ?
                                  scope.row.price[nCondition].last_update
                                  : '-'
                              }}
                            </small>
                          </div>
                        </div>
                        <div v-else>
                          <DeviceInput />
                        </div>
                      </template>
                    </TableColumn>
                  </template>
                </Table>
                <Pagination
                  v-if="item.devices.filters"
                  :data="item.devices.filters"
                  :page="item.devices.filters.page"
                  :limit="item.devices.filters.limit"
                  @update:page="val => handleFetchDevice(item, val, 'page')"
                  @update:limit="val => handleFetchDevice(item, val, 'limit')"
                />
              </div>
            </template>
            <template v-else>
              <div v-if="loading.all" v-loading="true" class="p-8"></div>
              <div v-if="!loading.all && item?.children?.length == 0" class="p-8">
                <span>No data to show</span>
              </div>
            </template>
          </div>
          <BulkPriceEditorList
            v-if="item.children && item.children?.length > 0"
            :id="id"
            :items="item"
            :automated-price="item.is_price_sync"
            :level-names="item.level_names"
            :round-up="item.rounde_up"
            :sycnable-price="sycnablePrice"
            :price-sharing="priceSharing"
            :price-scraper="priceScraper"
            :search="search"
            :disabled-action="disabledAction"
            :refetch="refetch"
            @refetch-device="refetchDevice"
            @update:refetch="handleSetRefetch"
            @pricing-flag-updated="handleChildPricingFlagUpdated"
          />
        </div>
      </template>
    </ExpandablePanel>

    <!-- v-if="sycnablePrice == 1" -->
    <BulkPriceEditorSources
      v-model:modal="sources.modal"
      v-model:item="sources.item"
      v-model:editing="sources.isEdit"
      v-model:priceSharing="priceSharing"
      :loading="loading?.rule"
      @cancel="cancelAutomatedPricing"
      @apply="applyRule"
    />
  </div>
</template>
<script>
import { defineComponent, watch, ref, computed, nextTick } from 'vue'
import { useRoute } from 'vue-router'
import { useDevice, usePriceSyncCustomPrice } from 'composable/pricing'

import debounce from 'utils/debounce'

import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import DeviceInput from 'components/BulkPriceEditor/Device/Input.vue'
import SyncPricing from 'components/BulkPriceEditor/SyncPricing.vue'
import Pagination from 'components/Pagination.vue'
import BulkPriceEditorSources from 'components/BulkPriceEditor/Device/Sources.vue'
import { useCategories } from 'composable/pricing'
import Alert from 'bbUi/components/alert/alert.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'BulkPriceEditorList',
  components: {
    Input,
    Button,
    ExpandablePanel,
    DeviceInput,
    SyncPricing,
    Pagination,
    BulkPriceEditorSources,
    Alert
  },
  props: {
    id: {
      type: [String, Number],
      default: undefined
    },
    refetch: {
      type: Boolean,
      default: false
    },
    items: {
      type: Object,
      default: () => ({})
    },
    automatedPrice: {
      type: [Boolean, Number, String],
      default: false
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    roundUp: {
      type: [String, Number],
      default: undefined
    },
    priceSharing: {
      type: Boolean,
      default: false
    },
    priceScraper: {
      type: Boolean,
      default: false
    },
    availablePrice: {
      type: [String, Number],
      default: undefined
    },
    sycnablePrice: {
      type: [String, Number],
      default: undefined
    },
    search: {
      type: String,
      default: null
    },
    disabledAction: {
      type: Object,
      default: undefined
    }
  },
  emits: ['refetch-device', 'pricing-flag-updated', 'show-prices', 'update:refetch'],
  setup(props, ctx) {
    const route = useRoute()
    const {
      loading,
      actionAll,
      actionUpdate,
      actionSetRule,
      actionUnsetRule,
      actionSetLevel
    } = useDevice(route.params.tab)
    const {
      items,
      sources,
      loading: loadingCustomPrice,
      resetBulkCustomPriceV2,
      handleTimeOut,
      handleSetPriceSyncCustomPrice,
      handleUnsetPriceSyncCustomPrice,
      handleResetPriceSyncCustomPrice,
      handleSavePriceSyncCustomPrice,
      handleSetItems,
      handleSetDevices
    } = usePriceSyncCustomPrice()
    const { getRule, mergeChildrenPreserveInPlaceChunked } = useCategories()

    const sourceList = ref([])
    const timerScroll = ref()

    const hasMultipleSources = computed(() => sourceList.value?.length > 1)

    const setItemRefs = (index) => {
      return (el) => {
        items.value[index].refDom = el?.$el ?? el
      }
    }
    const updateItemDevices = (res, item) => {
      if (!res || !res.data) {
        // Init empty but safe, without refetch
        item.devices = {
          data: [],
          filters: {
            page: 1,
            limit: 50,
            search: '',
          },
          isFetched: true
        }
        return
      }

      const conditions = handleSetDevices(res) || {}
      item.conditions_sources = conditions?.data[0]?.price || []
      const {
        current_page,
        first_page_url,
        from,
        last_page,
        last_page_url,
        next_page_url,
        path,
        per_page,
        prev_page_url,
        to,
        total
      } = res

      const { filters } = item?.devices || {}
      item.devices = {
        filters: {
          page: res?.current_page || 1,
          limit: res?.per_page || 50,
          search: filters?.search || item?.search || '',
          current_page,
          first_page_url,
          from,
          last_page,
          last_page_url,
          next_page_url,
          path,
          per_page,
          prev_page_url,
          to,
          total
        },
        ...conditions,
        isFetched: true
      }
    }
    const scrollIntoRefDom = (item) => {
      if (!item.children?.length) {
        clearTimeout(timerScroll.value)
        timerScroll.value = setTimeout(() => {
          item?.refDom?.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
          })
        }, 300)
      }
      // if (item.refDom) {
      // }
    }
    const handleGetDevice = async (item, refetch = false) => {
      const isAlreadyFetched = item.devices?.isFetched || item.children?.length > 0

      item.loading = true

      if (isAlreadyFetched && !refetch) {
        return true
      }

      try {
        const payload = { id: item.id }
        const filters = item.devices?.filters || {}

        const promises = []

        if (item.is_price_sync) {
          const rulePromise = getRule({
            category_id: item.id,
            level: handleSetLevel(item)
          }).then(response => {
            sourceList.value = response.selected_sources
            return response
          })
          promises.push(rulePromise)
        }

        const devicesPromise = actionAll(payload, { ...filters }, response => {
          updateItemDevices(response, item)
        })
        promises.push(devicesPromise)

        await Promise.all(promises)
        // scrollIntoRefDom(item)

        return true
      } catch (error) {
        console.log('Fetching device Error', error)
        return false
      } finally {
        item.loading = false
      }
    }
    const handleExpandItem = async (item, callback = () => {}) => {
      const isAlreadyFetched = item.devices?.isFetched || item.children?.length > 0

      if (isAlreadyFetched) {
        callback()
        return
      }

      if (item.collapse) {
        if (props.search) {
          if (!!item?.devices?.filters) {
            item.devices.filters.search = props.search
          } else {
            item.devices = { filters: { search: props.search } }
          }
        }
      }

      const isSucceed = handleSetLevel(item) === 'last_child' ? await handleGetDevice(item) : true
      if (isSucceed) {
        callback()
        ctx.emit('show-prices', item)
        await nextTick()
        scrollIntoRefDom(item)
      }
    }
    const handleResetPriceSyncCustomPriceBulk = async (item) => {
      const payload = { last_child_id: item.id }

      try {
        const response = await resetBulkCustomPriceV2(payload);
        
        if (response && response.pricing_status) {
          const level = handleSetLevel(item)
          
          item.missing_price_flags = level == "model" ? response.pricing_status.model_id_has_no_price : response.pricing_status.last_child_id_has_no_price
          
          const itemIndex = items.value.findIndex(valueItem => valueItem.id === item.id)
          if (itemIndex !== -1) {
            items.value[itemIndex].missing_price_flags = level == "model" ? response.pricing_status.model_id_has_no_price : response.pricing_status.last_child_id_has_no_price
          }

          const itemsWithCollapseReset = items.value.map(item => ({
            ...item,
            collapse: false
          }))

          ctx.emit('pricing-flag-updated', {
            itemId: item.parent_id != item.top_category_id ? item.name.toLowerCase().includes('mac') ? item.parent_id : item.top_category_id : item.parent_id,
            modelIdHasNoPrice: response.pricing_status.model_id_has_no_price,
            deviceIdHasNoPrice: response.pricing_status.device_id_has_no_price,
            lastChildHasNoPrice: response.pricing_status.last_child_id_has_no_price,
            children: itemsWithCollapseReset,
            modelId: item.parent_id,
            lastChildId: item.id
          })
        }
        
        await handleGetDevice(item, true);
      } catch (error) {
        console.error('Error resetting price:', error);
      }
    }
    const handleDeviceUpdate = async (event) => {
      let { row, index, item, isSync } = event;
      const payload = {
        product_id: row.id,
        condition_id: row.price[index].id,
        price: row.price[index].price,
        is_sync: isSync ? 1 : 0
      }
      await actionUpdate(payload, (err, response) => {
        if (err) return

        row = handleTimeOut(row, index, 1000)
        response.data.pricing.forEach((itemPrice, indexPricing) => {
          if (
            row.price[indexPricing].is_subcondition == 1 ||
            indexPricing === index
          ) {
            row.price[indexPricing].price = itemPrice.price
            row.price[indexPricing].last_update = itemPrice.last_update
            row.price[indexPricing].original_price = itemPrice.price
          }
        })
      })
      return row
    }
    const handleSetOriginalPrice = (item, index) => {
      item.price[index].original_price = item.price[index].price

      return item
    }
    const handleFetchDevice = debounce(async (item, val, type) => {
      // // type values is ['search', 'page', 'limit']
      if (!item?.devices?.filters || !Object.keys(item?.devices?.filters).includes(type)) return undefined

      item.devices.filters[type] = val

      if (type === 'limit') item.devices.filters.page = 1

      await handleGetDevice(item, true)
    }, 500)
    const handleSetLevel = (item) => actionSetLevel(item)
    const changeAutomatedPricing = async (item, key, { toggle, isExpanded }) => {
      const updateLoading = (isLoading) => {
        item.loading = isLoading
      }

      try {
        if (item.is_price_sync) {
          updateLoading(true)
          editAutomate(item, key, false)
        } else {
          updateLoading(true)
          await actionUnsetRule({
            category_id: item.id,
            level: handleSetLevel(item)
          })

          ctx.emit('refetch-device', {
            item
          })
        }
      } catch (error) {
        console.error('Error changing automated pricing:', error)
      } finally {
        updateLoading(false)
      }
    }
    const editAutomate = async (item, key, edit = true) => {
      sources.value = {
        modal: true,
        item,
        isEdit: edit,
        key
      }
    }
    const cancelAutomatedPricing = () => {
      items.value[sources.value.key].loading = false

      if (!sources.value.isEdit) {
        items.value[sources.value.key].is_price_sync = 0
      }

      sources.value = {
        modal: false,
        item: undefined,
        isEdit: false,
        key: undefined
      }
    }
    const validateData = ({ mapping_type, mapping }) => {
      return mapping.map((item, index) => {
        let errors = []

        // Validation by mapping_type
        if (mapping_type === "manual" && item.member_condition_id == null) {
          const msg = 'Member condition required'
          if (!errors.includes(msg)) errors.push(msg)
        }
        if (!item.source_condition_id) {
          const msg = 'Source condition required'
          if (!errors.includes(msg)) errors.push(msg)
        }

        // validation by type
        if (item.type !== 3 && (!item.amount_price || item.amount_price.trim() === "")) {
          const msg = 'Amount price required'
          if (!errors.includes(msg)) errors.push(msg)
        }

        return errors.length ? { index, errors } : null
      }).filter(Boolean)
    }
    const applyRule = async (payload, callback = () => {}) => {
      const item = items.value[sources.value.key]

      if (!item || !payload.isNew) return callback()

      loading.rule = true

      const result = validateData({
        mapping_type: payload.data.mapping_type,
        mapping: payload.data.mapping
      })

      if (result.length) {
        loading.rule = false
        return ElMessage({
          message: 'Please fill in the required information before proceeding',
          type: 'warning'
        })
      }

      const response = await actionSetRule(payload.data)

      if (response) {
        const callBackFetch = async () => {
          callback()
          sources.value = {
            modal: false,
            item: undefined,
            isEdit: false,
            key: undefined
          }
          loading.rule = false
        }

        const level = handleSetLevel(item)
        if (level === 'last_child') await handleGetDevice(item, true)
        ctx.emit('refetch-device', {
          item,
          callback: callBackFetch
        })
        return item
      } else loading.rule = false
    }
    const refetchDevice = (e) => {
      const { item, callback } = e
      ctx.emit('refetch-device', {
        item,
        callback
      })
    }
    /* Remove This If API give parameter if prices exist or more than Zero */
    const handleLoadedPrice = (res, item) => {
      if (item?.children?.length) {
        for (const child of item.children) {
          handleLoadedPrice(res, child)
        }
      } else {
        updateItemDevices(res, item)
      }
    }
    /* End Of Remove This If API give parameter if prices exist or more than Zero */

    const handleChildPricingFlagUpdated = (payload) => {
      const itemIndex = items.value.findIndex(item => item.id === payload.modelId)

      const item = items.value[itemIndex]
      if (itemIndex != -1) {
        item.missing_price_flags = payload.modelIdHasNoPrice
      }

      const itemsWithCollapseAndFlagUpdate = items.value.map(item => ({
        ...item,
        collapse: false
      }))

      const childIndex = itemsWithCollapseAndFlagUpdate[itemIndex].children.findIndex(child => child.id === payload.lastChildId)
      if (childIndex != -1) {
        itemsWithCollapseAndFlagUpdate[itemIndex].children[childIndex].missing_price_flags = payload.lastChildHasNoPrice
      }

      ctx.emit('pricing-flag-updated', {
        ...payload,
        children: itemsWithCollapseAndFlagUpdate,
      });
    };


    const handleSaveCustomPrice = async (row, nCondition, item, actionUpdate) => {
      const response = await handleSavePriceSyncCustomPrice(row, nCondition, item, actionUpdate)
      
      if (response && response.pricing_status) {
        const level = handleSetLevel(item)
        
        const newMissingPriceFlag = level == "model" ? 
          response.pricing_status.model_id_has_no_price : 
          response.pricing_status.last_child_id_has_no_price
        
        const itemIndex = items.value?.findIndex(valueItem => valueItem.id === item.id) ?? -1
        if (itemIndex !== -1) {
          items.value[itemIndex] = {
            ...items.value[itemIndex],
            missing_price_flags: newMissingPriceFlag
          }
        }
        
        Object.assign(item, { missing_price_flags: newMissingPriceFlag })

        const itemsWithCollapseReset = items.value.map(item => ({
          ...item,
          collapse: false
        }))

        ctx.emit('pricing-flag-updated', {
          itemId: item.parent_id != item.top_category_id ? item.name.toLowerCase().includes('mac') ? item.parent_id : item.top_category_id : item.parent_id,
          modelIdHasNoPrice: response.pricing_status.model_id_has_no_price,
          deviceIdHasNoPrice: response.pricing_status.device_id_has_no_price,
          lastChildHasNoPrice: response.pricing_status.last_child_id_has_no_price,
          children: itemsWithCollapseReset,
          modelId: item.parent_id,
          lastChildId: item.id
        })
      }
    }

    const handleResetCustomPrice = async (row, nCondition, item) => {
      const response = await handleResetPriceSyncCustomPrice(row, nCondition, item)
      
      if (response && response.data.pricing_status) {
        const level = handleSetLevel(item)
        
        item.missing_price_flags = level == "model" ? response.data.pricing_status.model_id_has_no_price : response.data.pricing_status.last_child_id_has_no_price
        
        const itemIndex = items.value.findIndex(valueItem => valueItem.id === item.id)
        if (itemIndex !== -1) {
          items.value[itemIndex].missing_price_flags = level == "model" ? response.data.pricing_status.model_id_has_no_price : response.data.pricing_status.last_child_id_has_no_price
        }
        const itemsWithCollapseReset = items.value.map(item => ({
          ...item,
          collapse: false
        }))

        ctx.emit('pricing-flag-updated', {
          itemId: item.parent_id != item.top_category_id ? item.name.toLowerCase().includes('mac') ? item.parent_id : item.top_category_id : item.parent_id,
          deviceIdHasNoPrice: response.data.pricing_status.device_id_has_no_price,
          modelIdHasNoPrice: response.data.pricing_status.model_id_has_no_price,
          lastChildHasNoPrice: response.data.pricing_status.last_child_id_has_no_price,
          children: itemsWithCollapseReset,
          modelId: item.parent_id,
          lastChildId: item.id
        })
      }
    }
    const handleRefetchFromParent = async (existingChildren, newChildren) => {
      return mergeChildrenPreserveInPlaceChunked(existingChildren, newChildren)
    }
    const handleSetRefetch = (val) => {
      ctx.emit('update:refetch', val)
    }

    watch(
      () => ({
        items: props.items,
        roundUp: props.roundUp
      }),
      (val, oldVal) => {
        if (JSON.stringify(val?.items) !== JSON.stringify(oldVal?.items)) {
          handleSetItems(val.items, val.roundUp)
        }
      },
      { immediate: true, deep: true }
    )

    watch(() => props.refetch, (val) => {
      if (val) {
        handleRefetchFromParent(items.value, props.items?.children).finally(() => {
          ctx.emit('update:refetch', false)
        })
      }
    })

    return {
      items,
      route,
      loading,
      sources,
      setItemRefs,
      handleGetDevice,
      handleExpandItem,
      handleFetchDevice,
      loadingCustomPrice,
      changeAutomatedPricing,
      handleDeviceUpdate,
      handleLoadedPrice,
      handleSetOriginalPrice,
      handleSetPriceSyncCustomPrice,
      handleSavePriceSyncCustomPrice,
      handleUnsetPriceSyncCustomPrice,
      handleResetPriceSyncCustomPrice,
      handleResetPriceSyncCustomPriceBulk,
      cancelAutomatedPricing,
      applyRule,
      editAutomate,
      handleSetLevel,
      refetchDevice,
      actionUpdate,
      sourceList,
      hasMultipleSources,
      handleSaveCustomPrice,
      handleResetCustomPrice,
      handleSetRefetch,
      handleChildPricingFlagUpdated
    }
  },
})
</script>
