<template>
  <div class="flex flex-col gap-y-8 py-4 px-2">
    <div class="space-y-2">
      <div>
        <div class="bg-green-04 rounded-full text-white inline-flex px-4 items-center justify-center gap-1 text-sm">
          <span class="material-icons">
            verified
          </span>
          Recomended
        </div>
      </div>
      <p>
        Save time by using these presets to define your conditions without having to start from scratch.
      </p>
      <Select
        v-model="form.source"
        :items="form.sources"
        :style="{
          width: '100%',
          boxShadow: 'unset'
        }"
        :disabled="disabledSource"
        :loading="loading"
        placeholder="Choose Preset Condition"
        max-height="200px"
        by="sub_domain"
        name="name"
        no-border
        @update:modelValue="handleSelectSource">
        <template #item="{ item }">
          <div class="flex items-center gap-x-4">
            <div
              :style="{backgroundImage: `url(${item.logo})`}"
              class="w-28 h-7 flex items-center justify-center bg-center bg-contain bg-no-repeat"
            />
            <div class="flex-1 flex items-center gap-x-1">
              {{ item.name }}
              <template v-if="form?.source">
                <i v-if="item?.sub_domain === form?.source?.sub_domain" class="material-icons text-green-04">check_circle</i>
              </template>
            </div>
          </div>
        </template>
      </Select>
    </div>

    <template v-if="form.source">
      <div v-loading="loading" class="space-y-6">
        <div
          v-for="(condition, n) in form.conditions"
          :key="n"
          :class="[
            'condition-source-outline',
            !condition.added && !!condition.exist && 'condition-source-outline--warning',
            !!condition.added && 'condition-source-outline--success'
          ]">
          <div class="space-y-4">
            <div class="font-semibold title">
              {{ condition.name }}
              <template v-if="!!condition.added || !!condition.exist">
                <Tooltip placement="top">
                  <template #content>
                    <p class="w-80 text-sm">
                      A condition with this name is already exists. To use this condition please rename the existing one or delete it first.
                    </p>
                  </template>
                  <template #default>
                    <span class="material-icons">
                      {{ (!!condition.added && 'check_circle') || 'info' }}
                    </span>
                  </template>
                </Tooltip>
              </template>
            </div>
            <div>
             <div v-html="condition.term" />
              <ul class="list-outside list-disc ml-6">
                <template
                  v-for="(term, indexTerm) in (condition.terms || [])"
                  :key="indexTerm">
                  <li
                    v-if="!!term && !!term?.length"  
                    class="text-grey-04"
                    v-html="sanitize(term)"
                  />
                </template>
              </ul>
            </div>
            <Button
              v-if="!condition.added"
              view="outlined"
              :color="condition.exist? 'orange' : 'teal'"
              block
              dense
              @click="handleAddCondition(condition, n)">
              <div class="flex items-center justify-center gap-2">
                <span>Add to Active Conditions</span>
                <span class="material-icons">
                  arrow_forward
                </span>
              </div>
            </Button>
          </div>
        </div>
  
        <Button
          v-if="!!form.conditions.length && form.conditions.length >= 2 && form.conditions.length !== form.condition.length"
          color="teal"
          block
          dense
          @click="handleAddAllCondition">
          <div class="flex items-center justify-center gap-2">
            <span>Add All To Active Conditions</span>
            <span class="material-icons">
              arrow_forward
            </span>
          </div>
        </Button>
      </div>
    </template>
  </div>
  <ConditionModalCautionSource
    v-model="modal"
    @continue="handleContinueCaution"
    @cancel="handleCancelCaution"
  />
</template>

<script>
import { ref, defineComponent, computed, watch } from 'vue'
import { sanitize } from 'utils/dom'

import Select from 'components/Select.vue'
import Button from 'bbUi/components/button/button.vue'
import ConditionModalCautionSource from 'components/Condition/Modal/CautionSource.vue';

export default defineComponent({
  name: 'ConditionMappingSource',
  components: {
    Select,
    Button,
    ConditionModalCautionSource
  },
  props: {
    mixin: {
      type: Object,
      default: undefined
    },
    category: {
      type: Object,
      default: undefined
    },
    active: {
      type: Object,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    refList: {
      type: Object,
      default: undefined
    },
    priceEditor: {
      type: Boolean,
      default: false
    },
    priceEditorCategory: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue', 'selectSource', 'selectCondition'],
  async setup(props, ctx) {
    const main = props.main
    const { form, getConditions, getSources, loading, modal, isValidJSON } = props.mixin
    const category = computed(() => props.category)
    const { form: formActive } = props.active
    const replacer = ref(null)
    const isAddAll = ref(false)

    const categoriesSelected = computed(() => {
      if (!category.value?.form?.categories) return undefined;

      if (!category.value?.form?.categories.length) return undefined;

      return category.value?.form?.categories
    })
    const disabledSource = computed(() => {
      if (!category.value.form.group_name) return true

      if (!categoriesSelected.value) return true

      if (categoriesSelected.value.length > 1) return true

      if (loading.value) return true

      if (main?.synced) return true

      return false
    })
    const handleShowCondition = async () => {
      const data = form.value.source
      const category = (Array.isArray(categoriesSelected.value) && [...categoriesSelected.value].shift()) || []
      if (!!data?.sub_domain && !!category) {
        await getConditions({
          sub_domain: data.sub_domain,
          category_id: category.category_id,
          is_mac: category.is_mac
        })
        handleInitCondition()
      }
    }
    const handleSelectSource = async (subDomain) => {
      if (Array.isArray(form.value.sources)) {
        form.value.source = [...form.value.sources].find(source => source.sub_domain === subDomain)

        return await handleShowCondition()
      }

      form.value.source = {}
    }
    const handleCheckExist = (condition) => {
      if (!!formActive.value?.conditions?.length) {
        const results = []
        formActive.value?.conditions.map(item => {
          const sourceName = condition.name?.split(' ').join('')
          const activeName = item.name?.split(' ').join('')
          activeName.toLowerCase() === sourceName.toLowerCase() && results.push(item.name)
        })

        return !!results.length ? true : false
      }

      return false
    }
    const handleAddCondition = (condition = undefined, index = -1) => {
      if (!condition || index === -1) return

      if (condition.exist) {
        replacer.value = { ...condition, index }
        modal.value = true

        return
      }

      form.value.condition.push(condition)
      form.value.conditions[index].added = true

      props.refList.isEdit = false
      const payload = { ...condition }
      delete payload.id
      if (main.id) {
        props.refList.saveCondition(payload, true)
      } else formActive.value.conditions.push(payload)
      // formActive.value.conditions = (
      //   Array.isArray(formActive.value.conditions) &&
      //   formActive.value.conditions.length &&
      //   [...new Set([...formActive.value.conditions, ...form.value.condition])]
      // ) ||
      //   [...new Set(form.value.condition)]
    }
    const handleAddAllCondition = () => {
      const cautions = form.value?.conditions?.length && [...form.value?.conditions].filter(item => !!item.exist) || []

      if (!!cautions.length) {
        replacer.value = cautions
        isAddAll.value = true
        modal.value = true

        return
      }

      form.value?.conditions?.forEach((item, key) => {
        if (!item.added) handleAddCondition(item, key)
      })
    }
    const handleReplaceCondition = () => {
      const indexToReplace = [...formActive.value.conditions].findIndex((item) => {
        const sourceName = replacer.value.name.split(' ').join('')
        const activeName = item.name.split(' ').join('')
        return sourceName.toLowerCase() === activeName.toLowerCase()
      })

      if (indexToReplace >= 0) {
        const condition = form.value.conditions[replacer.value.index]
        form.value.condition = [...new Set([...form.value.condition, ...[condition]])]
        form.value.conditions[replacer.value.index].added = true
      
        formActive.value.conditions[indexToReplace] = { ...condition }
      }

      handleCancelCaution()
    }
    const handleReplaceAllCondition = () => {
      form.value.conditions = form.value.conditions?.map(item => ({
        ...item,
        added: true
      }))
      form.value.condition = [...form.value.conditions]
      form.value.condition?.forEach(async (item) => {
        const indexToReplace = [...formActive.value.conditions].findIndex((active) => {
          const sourceName = item.name.split(' ').join('')
          const activeName = active.name.split(' ').join('')
          return sourceName.toLowerCase() === activeName.toLowerCase()
        })

        if (indexToReplace >= 0) {
          props.refList.isEdit = true
          if (main.id && formActive.value.conditions[indexToReplace].id) {
            await props.refList.saveCondition({
              id: formActive.value.conditions[indexToReplace].id,
              name: item.name,
              term: item.term,
              terms: item.terms
            }, true)
          } else {
            formActive.value.conditions[indexToReplace] = {
              ...formActive.value.conditions[indexToReplace],
              ...item
            }
          }
        } else {
          props.refList.isEdit = false
          if (!!main.id) {
            await props.refList.saveCondition({ name: item.name, term: item.term, terms: item.terms }, true)
          } else formActive.value.conditions.push(item)
        }
      })
    }
    const handleInitCondition = () => {
      form.value.conditions = [...form.value.conditions].map(item => {
        let terms = []
        try {
          if (isValidJSON(item.condition_term)) {
            const parsedTerms = JSON.parse(item.condition_term)
            terms = Array.isArray(parsedTerms) ? parsedTerms : []
          }
        } catch (error) {
          console.error('Error parsing condition terms:', error)
          terms = []
        }

        return {
          ...item,
          terms: !!terms ? terms.splice(1, terms.length - 1) : [''],
          term: !!terms ? terms[0] : item.condition_term,
          added: false,
          exist: handleCheckExist(item) // false
        }
      })
    }
    const handleContinueCaution = () => {
      if (!!isAddAll.value) return handleReplaceAllCondition()

      return handleReplaceCondition()
    }
    const handleCancelCaution = () => {
      modal.value = false
      isAddAll.value = false
      replacer.value = null
    }
    const handleRelabeling = () => {
      form.value.conditions = form.value.conditions.map((item) => {
        if (!!item.added) return {...item};

        return {
          ...item,
          exist: handleCheckExist(item)
        }
      })
    }
    const deleteActiveCondtion = (condition) => {
      const key = form.value.conditions.findIndex(item => {
        const sourceName = item.name?.split(' ').join('')
        const activeName = condition.name?.split(' ').join('')
        return activeName.toLowerCase() === sourceName.toLowerCase()
      })

      if (!!form.value.conditions[key]) {
        form.value.conditions[key].added = false
        form.value.conditions[key].exist = false
      }
    }

    watch(() => [
      categoriesSelected.value,
      main?.synced,
      getSources
    ],
    () => {
      if (!!categoriesSelected.value && !!getSources && !main?.synced) {
        getSources(categoriesSelected.value)
      } else {
        form.value.source = null,
        form.value.conditions = [],
        form.value.sources = [],
        form.value.condition = []
      }
    }, {
      deep: true,
      immediate: true
    })

    return {
      form,
      main,
      formActive,
      getConditions,
      getSources,
      loading,
      modal,
      sanitize,
      categoriesSelected,
      disabledSource,
      replacer,
      isAddAll,
      deleteActiveCondtion,
      handleRelabeling,
      handleSelectSource,
      handleAddCondition,
      handleInitCondition,
      handleCancelCaution,
      handleContinueCaution,
      handleAddAllCondition,
      handleReplaceCondition
    }
  }
})
</script>

<style lang="scss" scoped>
.condition-source-outline {
  @apply rounded-lg bg-teal-01 bg-opacity-25 p-4;

  box-shadow: 0 0 2px 1px rgba(66, 66, 66, 0.20);

  .title {
    @apply text-teal-04 flex items-center gap-x-2;
  }

  &--warning,
  &--success {
    box-shadow: unset
  }
  &--warning {
    @apply border border-orange-04;
    
    .title {
      @apply text-orange-04;
    }
  }
  &--success {
    @apply border border-green-04;
    
    .title {
      @apply text-green-04;
    }
  }
}
</style>
