<template>
  <div class="inline-block">
    <Tooltip
      trigger="hover"
      placement="top-end"
      :disabled="disabled">
      <template #content>
        <div class="max-w-xs text-sm">Items priced at {{ me.currency_prefix ? `${me.currency_symbol}0` : `0${me.currency_symbol}` }} are not subject to pricing rules.</div>
      </template>
      <Button
        v-if="!$slots.trigger"
        :disabled="disabled"
        color="white"
        @click="triggerClick">
        <div
          :class="[
            'transition-all duration-300 ease-in-out',
            'text-grey-03 hover:text-grey-04 flex items-center gap-x-3'
          ]">
          <span class="text-xl">{{ me.currency_symbol }}</span>
          <span>Mass Pricing</span>
        </div>
      </Button>
      <slot
        v-if="$slots.trigger"
        name="trigger"
        v-bind="{
          click: triggerClick
        }">
      </slot>
    </Tooltip>
  </div>

  <Modal
    v-model="modal"
    size="sm"
    :hide-header="loading.all"
    :body-scroll="!showSearch"
    @close="handleClose">
    <template #header>
      <span class="font-medium">
        Category Mass Price Adjustments
      </span>
    </template>
    <template #default>
      <div v-if="modal" class="space-y-8">
        <div class="flex gap-x-2">
          <i class="material-icons">info</i>
          <div class="text-sm space-y-4">
            <p>
              Mass pricing adjustments apply only to manually priced items and allow you to adjust prices for up to five items or models at a time. You can increase or decrease prices by a set amount or percentage for selected items, models, or specific conditions within those categories.
            </p>
            <p>
              For example, you can raise all iPhone prices by $1 in one click, applying the change to every model and condition, or you can set rules for specific conditions or specific models.
            </p>
          </div>
        </div>
        <div
          v-loading="loading.all"
          class="flex flex-col gap-y-8">
          <div>
            <Search
              v-model:selected="data.selectedProducts"
              v-model:search="data.search"
              v-model:loading="loading.search"
              v-model:showSearch="showSearch"
              :items="data.products"
              :searchCallBack="getModelItems"
              @update:selected="selectingProducts"
            />
          </div>
          <template v-if="data?.selectedProducts?.length > 0">
            <div v-loading="loading.conditions" class="space-y-2">
              <span class="font-medium">Condtion</span>
              <Conditions
                v-model="data.selectedConditions"
                :products="data.selectedProducts"
                :conditions="data.conditions"
                label="name"
                value="id"
                group-label="name"
                child-key="conditions"
              />
            </div>
            <div
              v-loading="loading.conditions"
              :class="[
                'space-y-2',
                data?.selectedConditions?.length <= 0 && 'opacity-30'
              ]">
              <span class="font-medium">Adjustment Rules</span>
              <Rules
                v-model="data"
                :currency-symbol="me.currency_symbol"
                :currency-prefix="me.currency_prefix"
              />
            </div>
          </template>
        </div>
      </div>
    </template>
    <template #footer>
      <Button
        color="teal"
        :loading="loading.all"
        :disabled="readyToSave"
        @click="handleSubmit">
        Save Changes
      </Button>
    </template>
  </Modal>
</template>
<script>
import { defineComponent, ref } from 'vue'
import { useUser } from 'composable/user'
import { useMassPricing } from 'composable/pricing'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Search from './Search.vue'
import Rules from './Rules.vue'
import Conditions from './Conditions.vue'

export default defineComponent({
  name: 'BulkPriceEditorMassPricing',
  components: {
    Modal,
    Button,
    Search,
    Rules,
    Conditions
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['submit'],
  setup(props, ctx) {
    const { me } = useUser()
    const {
      data,
      modal,
      loading,
      getConditions,
      getModelItems,
      canceling,
      readyToSave,
      selectingProducts,
      setRules
    } = useMassPricing()
    const showSearch = ref(false)
    
    const triggerClick = async () => {
      if (props.disabled) return
      modal.value = !modal.value
      if (modal.value) {
        await getConditions()
      }
    }
    const handleSubmit = async () => {
      await setRules((res) => {
        if (res.response === 200 || res === 'success') {
          modal.value = false
          handleClose()
          ctx.emit('submit')
        }
      })
    }
    const handleClose = () => {
      canceling()
      showSearch.value = false
    }

    return {
      me,
      data,
      modal,
      loading,
      readyToSave,
      triggerClick,
      canceling,
      showSearch,
      selectingProducts,
      getModelItems,
      getConditions,
      handleSubmit,
      handleClose
    }
  }
})
</script>
