<template>
  <div v-loading="main.loading">
    <div class="flex flex-col gap-6">
      <div class="space-y-4">
        <div class="flex items-center gap-6">
          <div class="flex items-center justify-center bg-teal-04 rounded-full text-white font-semibold w-8 h-8">
            <span>1</span>
          </div>
          <h6 class="text-lg text-teal-04">Set Category</h6>
        </div>
        <div class="lg:ml-14">
          <div class="flex items-center text-left border-l-4 border-orange-02 px-4 py-2 gap-2 mb-2 info-warning">
            <i class="material-icons">info_outline</i>
            <p>To assign a Preset Condition to more than one item in a group, first select one item, apply a preset, and then add more items as needed.</p>
          </div>
          <ConditionCategory
            ref="refCategory"
            :mixin="category"
            :main="{
              ...main,
              id
            }"
            :price-editor="priceEditor"
            :price-editor-category="priceEditorCategory"
          />
        </div>
      </div>
      <div class="space-y-4">
        <div class="flex items-center gap-6">
          <div class="flex items-center justify-center bg-teal-04 rounded-full text-white font-semibold w-8 h-8">
            <span>2</span>
          </div>
          <h6 class="text-lg text-teal-04">Set Condition</h6>
        </div>
        <div class="lg:ml-14">
          <ConditionMapping
            ref="refMapping"
            :mixin="condition"
            :category="category"
            :main="{
              ...main,
              id
            }"
            :price-editor="priceEditor"
            :price-editor-category="priceEditorCategory"
          />
        </div>
      </div>
    </div>
    <div
      v-if="!main?.synced && !priceEditor"
      :class="[
        !!id || !noSave ? 'border-t' : ''
      ]"
      class="mt-10 -mx-6 -mb-6 px-10 py-6 border-grey-01">
      <div
        :class="[
          'flex items-center',
          !!id && !noSave ? 'justify-between' : 'justify-end'
        ]">
        <a
          v-if="!!id"
          v-loading="main.loading"
          href="#"
          class="text-red-04 hover:text-red-02 focus:text-red-02"
          @click.prevent="modalDelete = true">
          Delete
        </a>
        <Button
          v-if="!noSave"
          color="teal"
          :loading="main.loading"
          @click="saveCondition">
          Save
        </Button>
      </div>
    </div>
    <template v-if="!!id && !main.synced">
      <Modal
        v-model="modalDelete"
        title="Delete Category"
        size="xs">
        <h6 class="text-lg font-normal mb-5">
          Are you sure want to delete this category?
        </h6>
        <template #footer>
          <Button
            view="outlined"
            color="teal"
            class="mr-5"
            :disabled="main.loading"
            @click="modalDelete = false">
            Cancel
          </Button>
          <Button
            view="smooth"
            color="red"
            :loading="main.loading"
            @click="deleteCondition">
            Delete
          </Button>
        </template>
      </Modal>
    </template>
  </div>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'
import useConditionFormNew from 'composable/setting/conditions'
import { useRouter, useRoute } from 'vue-router'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import ConditionCategory from './Category/index.vue'
import ConditionMapping from './Mapping/index.vue'
import { numeric } from '@vuelidate/validators'

export default defineComponent({
  components: {
    Button,
    Modal,
    ConditionCategory,
    ConditionMapping
  },
  props: {
    id: {
      type: [Number, String],
      default: undefined
    },
    isPriceSync: {
      type: Boolean,
      default: false
    },
    companySource: {
      type: String,
      default: ''
    },
    priceEditor: {
      type: Boolean,
      default: false
    },
    priceEditorCategory: {
      type: Object,
      default: undefined
    },
    noSave: {
      type: Boolean,
      default: false
    }
  },
  async setup(props, { emit }) {
    const {
      main: _main,
      category,
      condition
    } = useConditionFormNew()
    const route = useRoute()
    const router = useRouter()
    const { unique_id, price_sync } = route.query
    const sync = computed(() => price_sync)
    const synced = computed(() => unique_id && price_sync == 1)
    const uniqueId = computed(() => unique_id ? unique_id : null)
    const main = ref({
      ..._main,
      sync,
      synced,
      uniqueId
    })
    const refMapping = ref()
    const refCategory = ref()
    const modalDelete = ref(false)

    const resetData = () => {
      main.value.loading = false
      modalDelete.value = false
    }
    const saveCondition = async () => {
      const validCategory = await refCategory.value.submit()
      const validCondition = await refMapping.value.mappingList.submit()

      if (!validCategory || !validCondition) return

      main.value.loading = true
      if (!!props.id) {
        await main.value.action.update({
          id: props.id,
          group_name: refCategory.value.form.group_name,
          categories: refCategory.value.form.categories,
        }).then(() => {
          resetData()
          if (!props.priceEditor) router.push({ name: 'settingCondition' })
        }).catch(() => {
          resetData()
        })
      } else {
        await main.value.action.add({
          group_name: refCategory.value.form.group_name,
          categories: refCategory.value.form.categories,
          conditions: refMapping.value.mappingList.form.conditions
        }).then(() => {
          resetData()
          if (!props.priceEditor) router.push({ name: 'settingCondition' })
        }).catch(() => {
          resetData()
        })
      }
    }
    const deleteCondition = async () => {
      if (!props.id) return
      modalDelete.value = false

      main.value.loading = true
      await main.value.action.delete(props.id).then(() => {
        resetData()
        router.push({ name: 'settingCondition' })
      }).catch(() => {
        resetData()
      })
    }

    if (!!props.id) await main.value.getDetail({
      id: props.id,
      priceSync: {
        sync: sync.value,
        uniqueId: uniqueId.value
      }
    })

    return {
      main,
      category,
      condition,
      refCategory,
      refMapping,
      resetData,
      modalDelete,
      saveCondition,
      deleteCondition,
			router,
			route
    }
  }
})
</script>

<style scoped>
  .info-warning {
    background: #fef9c2;
    color: #b07929
  }
</style>