<template>
  <div>
    <div class="md:flex items-center justify-between gap-x-8 md:space-y-0 space-y-4 mt-6 mb-5">
      <Input
        icon
        v-model="data.search"
        :disabled="disableActions.priceUploadByProgress"
        placeholder="Search"
        class="w-full md:w-2/5"
        @input="actionSearch">
        <template #icon>
          <span class="material-icons">search</span>
        </template>
      </Input>
      <div v-loading="loading.all" class="flex items-center justify-end gap-3">
        <Tooltip
          trigger="hover"
          placement="top-end"
          :disabled="!disableActions.priceUpload">
          <template #content>
            <p class="max-w-xs whitespace-normal text-sm" style="word-break: keep-all;">
              You must have at least one active item with one or more conditions applied in offer to download the template.
            </p>
          </template>
          <template #default>
            <div>
              <BulkPriceEditorPriceUpload
                :pro="typeOfPackage == 17"
                :disabled="disableActions.priceUpload"
                @submit="handleSubmitPriceUpload">
                <template #trigger="{ click: clickTriggerUpload }">
                  <Button
                    :disabled="disableActions.priceUpload"
                    color="white"
                    @click="clickTriggerUpload">
                    <div
                      :class="[
                        'transition-all duration-300 ease-in-out',
                        'text-grey-03 hover:text-grey-04 flex items-center gap-x-2'
                      ]">
                      <span class="material-icons">table_view</span>
                      <span>Price Upload</span>
                      <span
                        v-if="disableActions.priceUpload"
                        class="material-icons text-orange-04">
                        warning
                      </span>
                    </div>
                  </Button>
                </template>
              </BulkPriceEditorPriceUpload>
            </div>
          </template>
        </Tooltip>
        <Tooltip
          v-if="disableActions.massPricing"
          trigger="hover"
          placement="top-end">
          <template #content>
            <p class="max-w-sm whitespace-normal text-sm" style="word-break: keep-all;">
              You must have at least one active item with one or more conditions applied in offer to update mass pricing.
            </p>
          </template>
          <template #default>
            <div>
              <Button disabled color="white">
                <div
                  :class="[
                    'transition-all duration-300 ease-in-out',
                    'text-grey-03 hover:text-grey-04 flex items-center gap-x-2'
                  ]">
                  <span>{{ me.currency_symbol }}</span>
                  <span>Mass Pricing</span>
                  <span
                    v-if="disableActions.massPricing"
                    class="material-icons text-orange-04">
                    warning
                  </span>
                </div>
              </Button>
            </div>
          </template>
        </Tooltip>
        <BulkPriceEditorMassPricing v-else @submit="refetchPricing" />
      </div>
    </div>
    <div class="mb-5">
      <Checkbox type="toggle" v-model="roundupPayload" :disabled="disableActions.priceUpload" color="teal" value="1" value-off="0" @input="updateRoundup" />
      <span class="my-auto">Round-up item pricing to the nearest dollar</span>
    </div>
    <Transition name="fade">
      <div v-if="processingUpload.invalid && !!showAlert" class="mb-5">
        <div class="bg-green-02 bg-opacity-20 p-4 rounded-md">
          <div class="grid lg:grid-cols-2 gap-4 text-green-04">
            <div class="flex items-center gap-x-2">
              <span class="material-icons">info</span>
              <span>
                {{`Total successfully uploaded and updated items: `}}
                <b>{{ processingUpload.totalSuccess }}</b>
              </span>
            </div>
            <div class="flex flex-wrap items-center justify-end gap-4">
              <span>Last Update: <b>{{ processingUpload.dateInvalid }}</b></span>
              <div class="flex items-center justify-between gap-x-4">
                <BulkPriceEditorPriceUploadInvalid>
                  <template #trigger="{ click: clickTrigger }">
                    <Button
                      color="red"
                      dense
                      @click="clickTrigger">
                      View Invalid Data
                    </Button>
                  </template>
                </BulkPriceEditorPriceUploadInvalid>
                <Button
                  color="teal"
                  view="outlined"
                  dense
                  @click="handleClearAlert">
                  <div class="flex items-center justify-center gap-x-2">
                    <span class="material-icons">close</span>
                    Clear Notification
                  </div>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Transition>
    <BulkPriceEditor
      :search="data.search"
      :items="state"
      :loadingTable="loading.all"
      :price-sharing="isPriceSharing"
      :price-scraper="isScraper"
      :round-up="roundupPayload"
      @refetch-pricing="refetchPricing"
      :disallowSync="disallowSync"
      :disabled-action="disableActions"
      :subDomainLinked="subDomainLinked"
    />
    <div class="mt-4">
      <Pagination
        v-model:page="data.page"
        v-model:limit="data.perPage"
        :data="data.pagination"
        @update:page="handleChangePage"
        @update:limit="handleChangeLimit"
      />
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, watch, ref } from 'vue'
import { useRoute } from 'vue-router'
import { useCategories, globals, useRoundup, processing as processingUpload } from 'composable/pricing'
import { useNotification } from 'composable/notification'
import { useUser } from 'composable/user'
import { useDevice } from 'composable/dashboard'
import { useAlert } from 'composable/alerts'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import BulkPriceEditor from 'components/BulkPriceEditor/Device.vue'
import BulkPriceEditorComputer from 'components/BulkPriceEditor/Computer.vue'
import BulkPriceEditorMassPricing from 'components/BulkPriceEditor/MassPricing/index.vue'
import BulkPriceEditorPriceUpload from 'components/BulkPriceEditor/PriceUpload/index.vue'
import BulkPriceEditorPriceUploadInvalid from 'components/BulkPriceEditor/PriceUploadInvalid/index.vue'
import Pagination from 'components/Pagination.vue'
import debounce from 'utils/debounce'
import { ALERT_ID } from 'constants/alerts'

export default defineComponent({
  components: {
    Checkbox,
    BulkPriceEditor,
    BulkPriceEditorComputer,
    BulkPriceEditorMassPricing,
    BulkPriceEditorPriceUpload,
    BulkPriceEditorPriceUploadInvalid,
    Input,
    Button,
    Pagination
  },
  async setup() {
    const route = useRoute()
    const { isActive, dismiss } = useAlert('pricing')
    const showAlert = computed(() => isActive(ALERT_ID.PRICING.INVALID_PRICE_UPLOAD)?.value)
    const { data, actionAll, loading, state } = useCategories()
    const {
      state: stateRoundup,
      actionGet: actionGetRoundup,
      actionUpdate: actionUpdateRoundup
    } = useRoundup()
    const { me } = useUser()
    const { state: stateNotifcation, openNotification } = useNotification()
    const { data: dataDevice, actionConditionDeviceConditionAvailability } = useDevice()

    const roundupPayload = ref(0)

    const actionSearch = debounce(async() => {
        await actionAll(route.params)
    }, 500)

    const updateRoundup = debounce(async () => {
      await actionUpdateRoundup({
        is_round_up_price_enable: roundupPayload.value
      })
      state.value = []
      actionAll(route.params)
    }, 500)

    watch(() => route.params, async () => {
      data.search = ''
      if(route.params.tab) {
        await Promise.all([
						actionAll(route.params),
          ]
        )
        roundupPayload.value = stateRoundup.value.status.is_round_up_price_enable
      }
    }, {
      immediate: true
    })

    const isPriceSharing = computed(() => me.value.is_price_sharing == 1)
    const isScraper = computed(() => data.isScraper == 1)
    const disallowSync = computed(() => me.value.is_price_sharing == 3)
    const subDomainLinked = computed(() => me.value.sub_domain_linked ? true : false)
    const disableActions = computed(() => {
      const isUploading = processingUpload.value.uploadProgress > 0 && processingUpload.value.uploadProgress < 100
      const noAvailability = !dataDevice?.availabilityCondition

      const priceUpload =
        isUploading || noAvailability

      const massPricing =
        isUploading || noAvailability

      return { priceUpload, priceUploadByProgress: isUploading, massPricing, availabilityCondition: noAvailability }
    })
    const typeOfPackage = computed(() => me.value.package_id)

    const handleChangePage = async (e) => {
      data.page = Number(data.page)
      await actionAll(route.params)
    }
    const handleChangeLimit = async () => {
      data.page = 1
      data.perPage = Number(data.perPage)
      await actionAll(route.params)
    }
    const refetchPricing = async (callback = () => {}) => {
      try {
        await actionAll(route.params)
        typeof callback === 'function' && callback()
      } catch (error) {
        console.log('Failed To Set Rule')
      }
    }
    const handleClearAlert = () => {
      dismiss(ALERT_ID.PRICING.INVALID_PRICE_UPLOAD)
    }
    const handleSubmitPriceUpload = async () => {
      await refetchPricing()
      openNotification('NotificationPriceUpload')
    }
    await Promise.all([
			actionGetRoundup(),
			actionConditionDeviceConditionAvailability()
		])

    return {
      me,
      globals,
      stateRoundup,
      processingUpload,
      roundupPayload,
      data,
      state,
      dataDevice,
      showAlert,
      actionSearch,
      loading,
      isPriceSharing,
      isScraper,
      disallowSync,
      subDomainLinked,
      updateRoundup,
      disableActions,
      stateNotifcation,
      typeOfPackage,
      refetchPricing,
      handleChangePage,
      handleChangeLimit,
      handleClearAlert,
      handleSubmitPriceUpload
    }
  }
})
</script>
