<template>
  <div class="flex flex-col lg:flex-row gap-4">
    <div class="lg:w-1/4">
      <Fieldset class="rounded-lg">
        <template #label>Preset Condition</template>
        <ConditionMappingSource
          ref="mappingSource"
          :mixin="preset"
          :category="category"
          :active="active"
          :main="main"
          :refList="mappingList"
          :price-editor="priceEditor"
          :price-editor-category="priceEditorCategory"
        />
      </Fieldset>
    </div>
    <div class="flex-1">
      <Fieldset class="rounded-lg">
        <template #label>Active Conditions</template>
        <ConditionMappingList
          ref="mappingList"
          :mixin="active"
          :preset="preset"
          :category="category"
          :main="main"
          :refSource="mappingSource"
          :price-editor="priceEditor"
          :price-editor-category="priceEditorCategory"
        />
      </Fieldset>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import ConditionMappingSource from './Source.vue'
import ConditionMappingList from './List.vue'

export default defineComponent({
  name: 'ConditionMapping',
  components: {
    ConditionMappingSource,
    ConditionMappingList,
    Fieldset
  },
  props: {
    mixin: {
      type: Object,
      default: undefined
    },
    category: {
      type: Object,
      default: undefined
    },
    main: {
      type: Object,
      default: undefined
    },
    priceEditor: {
      type: Boolean,
      default: false
    },
    priceEditorCategory: {
      type: Object,
      default: undefined
    }
  },
  setup(props, ctx) {
    const { preset, active } = props.mixin
    const category = props.category
    const main = props.main
    const mappingList = ref()
    const mappingSource = ref()
    const isMac = computed(() => !!category?.form?.categories?.filter(category => !!Number(category.is_mac))?.length)

    return {
      main,
      isMac,
      preset,
      active,
      category,
      mappingList,
      mappingSource
    }
  }
})
</script>
