<template>
  <div>
    <Table ref="tableElement" row-key="id" :expand-row-keys="expands" :data="items" class="mb-4" v-loading="loadingTable">
      <TableColumn type="expand">
        <template #default="scope">
          <div v-if="scope.row.conditions_group">
            <Alert v-if="scope.row.conditions_group.length !== 0" color="orange" view="smooth" class="mb-5" dense>
              <div class="flex space-x-4">
                <div>
                  <i class="material-icons">error</i>
                </div>
                <div>
                  Warning! Changing the pricing rules on this window will affect all of the following items:
                  <ul class="list-disc ml-5">
                    <li v-for="condition in scope.row.conditions_group" class="font-semibold" :key="condition">
                      {{ condition }}
                    </li>
                  </ul>
                  <router-link :to="{ name: 'settingConditionEdit', params: { conditionId: scope.row.conditions_group_id } }">
                    Conditions
                  </router-link>
                </div>
              </div>
            </Alert>
          </div>
          <template v-for="(item, index) in scope.row.child" :key="index">
            <ExpandablePanel v-model="item.collapse" class="mb-4" @click-header="deviceAll(item)">
              <div class="px-5">
                <div v-if="item.devices?.condition_price && item.devices?.condition_price?.length === 0" class="text-center my-4">
                  <span class="text-grey-04">Please setup your <router-link :to="{name: 'settingCondition'}">conditions</router-link> first</span>
                </div>
                <div class="flex items-center mb-8">
                  <div v-if="item?.devices" class="flex items-center mr-5">
                      <p v-if="scope.row.is_automated_price == 1" class="mr-4"><span class="font-semibold">Source of Pricing:</span> {{item.pricing_source.source || '-'}}</p>
                      <label class="font-semibold mr-4 ml-0 text-dark">Base Price: </label>
                      <Input v-model="item.devices.base_price" type="number" :disabled="scope.row.is_automated_price == 1 || item.devices.condition_price.length === 0 || item.loading || listLoading || !scope.row.is_condition_exist" min="0" @focus-out="deviceUpdate(item)" @click.stop icon icon-block>
                        <template #icon>
                          <span class="text-dark text-lg">{{ me.currency_symbol }}</span>
                        </template>
                      </Input>
                  </div>
                  <div v-if="item?.pricing_source && scope.row.is_automated_price == 1">
                    <img v-if="item.pricing_source.source_logo" :src="item.pricing_source.source_logo" alt="company-logo" class="h-10">
                  </div>
                </div>
                <ComputerTable :product-parent="{ priceSync: scope.row.is_automated_price, brand: scope.row.brand, category: scope.row.name}" :base-price="item?.devices?.base_price" :item="item" :disabled="scope.row.is_automated_price == 1" :loading="item.loading || listLoading" @getDevice="getDevice" />
              </div>
              <template #header="{toggle, isExpanded}">
                <div class="flex items-center justify-between">
                  <div @click="toggle" class="flex items-center cursor-pointer py-2">
                      <span class="material-icons mr-2 text-2xl transform transition-all" :class="{ 'rotate-90': item.collapse }">chevron_right</span>
                      <p class="mb-0">{{item.name}}</p>
                      <span class="mx-10">EMC {{ Array.isArray(item.emc_number) ?  item.emc_number.map(item => `#${item}`).join(', ') : '' }}</span>
                  </div>
                  <div v-if="!isExpanded">
                    <label class="font-semibold mr-4 ml-0 text-dark">Base Price: </label>
                    <Input v-model="item.base_price" type="number" min="0" @focus-out="handleSetBasePrice(item)" @click.stop icon icon-block :disabled="loading.set_base_price.has(item.id) || scope.row.is_automated_price == 1 || !scope.row.is_condition_exist">
                      <template #icon>
                        <span class="text-dark text-lg">{{ me.currency_symbol }}</span>
                      </template>
                    </Input>
                  </div>
                </div>
              </template>
            </ExpandablePanel>
          </template>
        </template>
      </TableColumn>
      <TableColumn prop="brand" label="Brand" />
      <TableColumn prop="name" label="Category" />
      <TableColumn>
        <template #header>
          <span>Price Sync</span>
          <!-- <div class="flex items-center">
            <a href="https://help.reusely.com/en/articles/9176424-how-price-sync-works" target="_blank" noreferrer noopener class="material-icons mx-2" whitelabel="remove">help</a>
          </div> -->
        </template>
        <template #default="scope">
          <div class="py-1">
             <Popover trigger="hover" :disabled="!priceSharing" width="400">
              <span class="font-semibold">When price sharing is enabled, prices must be manually entered.</span>
              <template #reference>
                <Checkbox v-if="scope.row.is_syncable" v-model="scope.row.is_automated_price" type="toggle" color="teal" :disabled="priceSharing" :loading="loading.rules.includes(scope.row.id)" value="1" value-off="0" @input="changeAutomatedPricing(scope.row)"/>
                <Tooltip v-else placement="top">
                  <Checkbox class="py-3" type="toggle" color="teal" value="1" value-off="0" disabled :loading="loading.rules.includes(scope.row.id)" />
                  <template #content>
                    <div class="w-60 text-xs">There are currently no pricing sources available for this item. You can price it manually by using the dropdown menu or contact support to add a custom data source.</div>
                  </template>
                </Tooltip>
              </template>
            </Popover>
          </div>
        </template>
      </TableColumn>
      <TableColumn>
        <template #default="scope">
            <Button
              color="teal"
              dense
              class="lg:opacity-0 group-hover:opacity-100 transition-all"
              v-if="scope.row.is_automated_price != 0"
              :loading="scope.row.loading"
              @click="editAutomate(scope.row)"
            >
              <span class="material-icons text-sm">edit</span>
            </Button>
        </template>
      </TableColumn>
    </Table>
    <Modal class="modal" size="lg" v-model="modal" hide-header>
      <div class="border-b border-grey-01 px-10 pb-5 mb-5 -mx-12">
        <h6 class="font-semibold">Price Sync</h6>
        <!-- <div class="flex items-center">
          <a href="https://help.reusely.com/en/articles/9176424-how-price-sync-works" target="_blank" noreferrer noopener whitelabel="remove" class="material-icons mx-2">help</a>
        </div> -->
      </div>
      <div class="space-y-10">
        <!--
          <Alert color="blue" view="smooth" class="mb-5" dense>
            <div class="flex items-center h-full">
              <span>
                Price sync is a tool that allows you to sync buyback pricing
                with a specific data source in real-time automatically. You can
                set rules to match, markup, or markdown the buyback price based
                on a percentage or fixed amount. Price sync will also
                automatically convert the price into your local currency.
              </span>
            </div>
            <template #left>
              <span class="material-icons text-2xl my-auto">info</span>
            </template>
          </Alert>
        -->
        <div v-loading="loadingPricingSource.check" class="space-y-8">
          <div class="flex items-center gap-x-4">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">1</div>
            <span class="font-semibold text-sm">Select your data source</span>
          </div>
          <div class="grid lg:grid-cols-4 md:grid-cols-2 grid-cols-1 gap-4">
            <div
              v-for="(source, n) in pricingSources"
              :key="n"
              v-bind="{
                ...(n === (pricingSources.length - 1) && { whiteLabel: 'remove' })
              }"
              :class="[
                'border',
                source.sub_domain
                  ? 'hover:border-teal-04 cursor-pointer'
                  : '',
                source.sub_domain === pricing_source.name
                  ? 'border-teal-04'
                  : 'border-grey-01',
                !pricingSources.length || pricingSources.length === 1 && source.sub_domain
                  ? 'opacity-50 cursor-not-allowed'
                  : 'opacity-100',
                'rounded-lg p-4'
              ]"
              @click="handleUpdatePricingSource(source.sub_domain)">
              <template v-if="source.sub_domain">
                <div class="flex flex-col items-center justify-center gap-6 h-full">
                  <img :src="source.logo" class="pricing-source-logo" />
                  <div class="flex justify-center items-center gap-x-2 max-w-max">
                    <i v-if="pricing_source.name === source.sub_domain" class="material-icons text-teal-04">check_circle</i>
                    <span class="text-sm font-semibold inline-block w-full text-center break-words">{{ source.name }}</span>
                  </div>
                  <div v-if="me" class="bg-grey-01 bg-opacity-50 p-2 px-4 rounded-md flex items-center justify-start gap-x-4">
                    <template v-if="me.currency !== source.currency">
                      <div class="flex items-center gap-x-2">
                        <img v-if="source.country_code" :src="`/flag/${source.country_code.toLowerCase()}.svg`" width="28" height="24" />
                        <span class="text-sm">{{ source.currency }}</span>
                      </div>
                      <span class="bg-teal-04 rounded-full w-6 h-6 flex justify-center items-center text-white text-lg">
                        <i class="material-icons">arrow_right_alt</i>
                      </span>
                      <div class="flex items-center gap-x-2">
                        <img :src="`/flag/${currencyFlagDefault}.svg`" width="28" height="24" />
                        <span class="text-sm">{{ me.currency }}</span>
                      </div>
                    </template>
                    <div v-else class="flex items-center gap-x-2">
                      <img :src="`/flag/${source.country_code.toLowerCase()}.svg`" width="28" height="24" />
                      <span class="text-sm">{{ source.currency }}</span>
                    </div>
                  </div>
                  <div v-if="source.is_support_sell_instantly">
                    <Tooltip placement="top">
                      <button class="text-orange-04 bg-orange-01 px-2 py-1 rounded-full flex justify-center items-center">
                        <svg width="17" height="16" viewBox="0 0 17 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M6.25 3V8.5H7.75V13L11.25 7H9.25L11.25 3H6.25Z" fill="currentColor"/>
                        </svg>
                        <span class="text-xs">Supports Sell Instantly</span>
                      </button>
                      <template #content>
                        <div class="w-60 text-xs">
                          You can sell excess inventory directly from the inventory page to this pricing source.
                        </div>
                      </template>
                    </Tooltip>
                  </div>
                </div>
              </template>
              <template v-else>
                <div class="flex flex-col items-center justify-center gap-6 h-full">
                  <img src="/icon/settings/pricing-source-custom.svg" alt="custom" class="w-48 mx-auto" />
                  <span class="text-sm font-semibold">Connect Any Data</span>
                  <button class="bg-teal-04 text-white px-4 py-2 rounded-lg hover:text-white text-sm" @click="handleIntercomeMessage">Learn More</button>
                </div>
              </template>
            </div>
          </div>
        </div>
        <div class="space-y-2">
          <div class="flex items-center gap-x-4 mb-5">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">2</div>
            <span class="font-semibold text-sm">Set your pricing rules</span>
          </div>
          <div v-if="!!pricing_source.name && !isPricingSourceUnavailable && !loadingPricingSource.check" class="max-w-lg">
            <RadioGroup v-model="dataModal.type" class="radio--full">
              <RadioButton label="3">Match</RadioButton>
              <RadioButton label="2">Markup</RadioButton>
              <RadioButton label="1">Markdown</RadioButton>
            </RadioGroup>
          </div>
          <div v-if="dataModal.type != 3" class="grid grid-cols-2 max-w-lg gap-2">
            <RadioGroup v-model="dataModal.amount_type" class="radio--full">
              <RadioButton label="1">Percentage</RadioButton>
              <RadioButton label="0">Fixed</RadioButton>
            </RadioGroup>
            <Input v-model="dataModal.amount_price" icon icon-block :currency="true" :min="0" :right="dataModal.amount_type == '0' ? false : true" type="number" class="w-full">
              <template #icon>
                <span class="text-grey-03" v-if="dataModal.amount_type == '0'">{{ me.currency_symbol }}</span>
                <span class="text-grey-03" v-else>%</span>
              </template>
            </Input>
          </div>  
        </div>
        <div v-if="!!pricing_source.name && !isPricingSourceUnavailable && !loadingPricingSource.check">
          <div class="flex items-center gap-x-4 mb-5">
            <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">3</div>
            <span class="font-semibold text-sm">Preview Price from Model</span>
          </div>
          <Select v-model="dataModal.selected_model" placeholder="Please Select" name="name" class="w-full max-w-lg" :items="selected?.child" @input="selectModel" />
        </div>
      </div>
      <div v-if="dataModal.selected_model" class="border border-grey-02 rounded-md p-5 mt-8" >
        <div class="flex items-center mb-5">
          <p class="text-dark font-semibold mr-8">Source of Pricing: <span class="font-normal ml-4">{{selectedCompanyPricingSourceName}}</span></p>
          <div class="flex items-center mr-8">
              <label class="font-semibold mr-4">Base Price: </label>
              <Input :modelValue="dataModal.selected_model.devices?.base_price" type="number" :disabled="true" icon dense>
                <template #icon>
                  {{ me.currency_symbol }}
                </template>
              </Input>
          </div>
          <img v-if="!!pricing_source.logo" v-loading="loadingPricingSource.check" :src="pricing_source.logo" alt="company-logo" class="h-12 mx-4">
        </div>
        <ComputerTable :item="dataModal.selected_model" disabled :loading="dataModal.selected_model.loading" />
      </div>
      <template #footer>
        <div class="flex justify-end">
          <a href="javascript:void(0)" class="text-red-04 hover:text-red-02 mr-8" @click="cancelAutomatedPricing">Cancel</a>
          <Button class="ml-auto small" color="teal" :loading="loading.rule || loadingPricingSource.check || loadingPricing.all" :disabled="isPricingSourceUnavailable || !pricing_source.name" @click="applyRule">Apply</Button>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { defineComponent, ref, watch, reactive, computed, onBeforeMount, onUnmounted } from 'vue'
import { useRoute } from 'vue-router'
import { useUser } from 'composable/user'
import { useDevice, useCategories, useCompanyPricingSources } from 'composable/pricing'
import { useFeatureFlag } from 'composable/whitelabel'

import Button from 'bbUi/components/button/button.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import ComputerTable from 'components/BulkPriceEditor/Computer/Table.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'

export default defineComponent({
  name: 'Device',
  components: {
    Button,
    Checkbox,
    Input,
    Select,
    ExpandablePanel,
    ComputerTable,
    Modal,
    Alert
  },
  props: {
    items: {
      type: Array,
      default: () => ([])
    },
    pathId: {
      type: String,
      default: null
    },
    loadingTable: {
      type: Boolean
    },
    priceSharing: {
      type: Boolean,
      default: false
    },
    pricingSources: {
      type: Array,
      default: () => ([])
    }
  },
  setup(props) {
    const isEditAutomate = ref(false)
    const dataModal = reactive({
      type: '3',
      amount_type: '1',
      amount_price: 0,
      selected_model: null,
    })
    const { me } = useUser()
    const modal = ref(false)
    const selected = ref(null)
    const route = useRoute()
    const tableElement = ref(null)
    const { loading, actionAll, actionUpdate, actionUnsetRule, actionSetRule, actionPreview, actionSetBasePrice } = useDevice(route.params.tab)
    const { loading: loadingPricing, actionAll: fetchPricing, getRule} = useCategories()
    const { actionCheckDevicePricingSource, loading: loadingPricingSource, action: actionPricingSources } = useCompanyPricingSources()
    const { features } = useFeatureFlag()

    const items = ref(props.items)
    const pricing_source = reactive({
      name: null,
      logo: null,
      status: null
    })
    const listLoading = ref(false)
    const isProcessing = ref(false)

    const pricingSources = computed(() => props.pricingSources.filter((pricingSource) => {
      if(pricingSource.id === null && !features.value.priceEditor.customRequest) return false
      return true
    }))
    const isPricingSourceUnavailable = computed(() => pricing_source.status === 'not available')
    const selectedCompanyPricingSourceName = computed(() => !!pricing_source.name ? props.pricingSources.find(item => item.sub_domain === pricing_source.name)?.name : '')
    const expands = computed(() => {
      const filtered = []
      if (route.query.ref === 'true' && route.query.model) {
        items.value.map(item => {
          if (item.brand === route.params.manufacturer && item.name === route.query.model) {
            filtered.push(item.id)
          }
        })
      }
      return filtered
    })
    const currencyFlagDefault = computed(() => {
      const flag = me.value.currency.split('')
      flag.pop()
 
     return flag.join('').toLowerCase()
    })

    const deviceAll = async (item, load = false, modal = false) => {
      if(!item.devices || load) {
        item.loading = true
        let response
        if(modal){
          response = await actionPreview({
            product_id: dataModal.selected_model.id,
            amount_price: dataModal.amount_price,
            amount_type: dataModal.amount_type,
            source: pricing_source.name,
            type: dataModal.type
          })
        }else{
          response = await actionAll(item.id)
        }
        response.devices.product.specs = Object.entries(response.devices.product.specs).reduce((acc, curr) => {
          if(curr[1].length > 0) curr[1].forEach(item => {
            acc.push({
              name: curr[0],
              spec: item
            })
          })
          return acc
        }, [])
        item.pricing_source = {
          source: response.source,
          source_logo: response.source_logo
        }
        item.base_price = response.devices.product.base_price
        modal ? dataModal.selected_model.devices = response.devices.product : item.devices = response.devices.product
        item.loading = false
      }
    }
    const deviceUpdate = async (item, index = null) => {
      const specsData = item.devices.specs.map(item => ({spec_id: item.spec.id, percentage: item.spec.price}))
      const pricingData = item.devices.condition_price.map(item => ({condition_id: item.condition_id, percentage: item.percentage}))

      const pricing = {
        product_id: item.id,
        category_id: item.devices.mac_category_id,
        base_price: item.devices.base_price,
        pricing: pricingData,
        specs: specsData
      }
      if(index !== null) {
        pricing.pricing.push({
          condition_id: item.devices.condition_price[index].condition_id,
          percentage: item.devices.condition_price[index].percentage
        })
      }
      await actionUpdate({
        pricing: JSON.stringify([pricing])
      })
      deviceAll(item, true)
    }

    const getDevice = (item) => {
      deviceAll(item, true)
    }

    const changeAutomatedPricing = async(item) => {
      isProcessing.value = true
      loading.rules.push(item.id)
      listLoading.value = true
      if(item.is_automated_price === '1') {
        await actionPricingSources({
          syncable: 1,
          is_mac: route.params.tab == 'mac' ? 1 : 0,
          category_id: item.id
        }, () => {
          modal.value = true
          selected.value = {...item, devices: null}
        })
      } else await actionUnsetRule({category_id: item.id, is_computer: 1})
      listLoading.value = false
      const matchedIndex = loading.rules.findIndex(loadingItem => loadingItem === item.id)
      if(matchedIndex > -1) loading.rules.splice(matchedIndex, 1)
      handleResetDeviceInfo()
      isProcessing.value = false

      if(Array.isArray(props.pricingSources)){
        if(props.pricingSources.length === 1) handleUpdatePricingSource(props.pricingSources[0]?.sub_domain || null)
      }
    }

    const cancelAutomatedPricing = () => {
      if(selected.value){
        if(!isEditAutomate.value){
          let indexItem = items.value.findIndex(item => item.id === selected.value.id)
          items.value[indexItem].is_automated_price = 0
        }
        resetModal()
        modal.value = false
      }
    }

    const applyRule = async() => {
      loading.rule = true
      const response = await actionSetRule({
        category_id: selected.value.id,
        type: Number(dataModal.type),
        source: pricing_source.name,
        amount_type: dataModal.amount_type,
        amount_price: dataModal.amount_price,
        is_computer: 1
      })

      if (response ) {
        await fetchPricing(route.params)
        resetCollapse(selected.value)
        loading.rule = false
        modal.value = false
        selected.value = null
        resetModal()
      }
    }

    const resetModal = () => {
      dataModal.type = '3'
      dataModal.amount_type = '1'
      dataModal.amount_price =  0
      dataModal.selected_model = null
      pricing_source.name = null
      pricing_source.logo = null
      pricing_source.status = null
    }

    const resetCollapse = (row) => {
      let index = items.value.findIndex(item => item.id == row.id)
      items.value[index].child.forEach(child => {
        child.collapse = false
        child.devices = null
      })
      tableElement.value.toggleRowExpansion(items.value[index], false)
    }

    const selectModel = async() => {
      deviceAll(dataModal.selected_model, true, true)
    }

    const editAutomate = async (item) => {
      item.loading = true
      await actionPricingSources({
        syncable: 1,
        is_mac: route.params.tab == 'mac' ? 1 : 0,
        category_id: item.id
      })
      await getRule({category_id: item.id, is_computer: true})
      .then(response => {
        isEditAutomate.value = true
        let index = items.value.findIndex(item => item.id == response.category_id)
        selected.value = {
          ...response,
          id: response.category_id,
          child: items.value[index].child,
          devices: null
        }
        dataModal.amount_type = response.amount_type,
        dataModal.amount_price = response.amount_price
        dataModal.type = response.type
        pricing_source.name = response.source
        modal.value = true
        item.loading = false
      })
    }

    const handlePricingSourceChange = async (source) => {
      if(!source) return
      const data = {
        category_id: selected.value.id,
        is_mac: 1,
        sub_domain: source
      }
      await actionCheckDevicePricingSource(data, (response) => {
        pricing_source.logo = response.logo
        pricing_source.status = response.status
      })
    }

    const handleResetDeviceInfo = () => {
      items.value = items.value.map(item => ({...item, child: item.child.map(childItem => ({...childItem, collapse: false, devices: null}))}))
    }
    const handleSetBasePrice = async (item) => {
      await actionSetBasePrice(item, async (err, result) => {
        if(err) return
        item.base_price = result.base_price
        if(item.devices){
          item.devices.base_price = result.base_price
        }
        await deviceAll(item, true)
      })
    }
    const handleUpdatePricingSource = (source=null) => {
      if (!source) return
      pricing_source.name = source
    }
    const handleIntercomeMessage = () => {
      if (!isWhiteLabel.value) return window.open(
        'https://forms.clickup.com/8427959/f/816dq-50256/3ZVVXIRACGV7NVG7QD',
        '_blank'
      )

      window.CustomerSupport.use('showNewMessage', 'Hi, I would like to learn more about your custom data services')
    }

    watch(() => props.items, newValue => {
      items.value = newValue
    })

    watch(() => dataModal.type, newValue => {
      if(newValue == 3){
        dataModal.amount_type = 0
        dataModal.amount_price = 0
      }
      dataModal.selected_model = null
    })
    watch(() => pricing_source.name, handlePricingSourceChange)

    onBeforeMount(() => {
      window.addEventListener('beforeunload', (ev) => {
        if(!isProcessing.value) return
        ev.preventDefault()
        ev.returnValue = ''
      })
    })

    onUnmounted(() => {
      window.removeEventListener('beforeunload', ()=>{})
    })

