<template v-if="!disabledAction.priceUploadByProgress">
  <ElCheckbox
    v-if="disabledAction.priceUploadByProgress"
    v-model="synced" 
    disabled
    color="teal" 
    :true-label="1" 
    :false-label="0" 
    v-loading="isLoading" 
  />
  <Tooltip v-else-if="available" placement="top" :disabled="!computedTooltip && !isNotSyncable">
    <div>
      <ElCheckbox
        v-model="synced" 
        :disabled="isNotSyncable || (disallowSync && !synced)" 
        color="teal" 
        :indeterminate="isIndeterminateState ? true : false"
        :true-label="1" 
        :false-label="0" 
        v-loading="isLoading" 
        @change="handleChange" 
      />
    </div>
    <template #content>
      <div class="text-sm">
        <p v-if="!isNotSyncable && item.total_device_manual && !item.total_device_sync">
          All Items Manually Priced
        </p>
        <p v-else-if="!isNotSyncable && !item.total_device_manual && item.total_device_sync">
          All items in this category are price-synced.
        </p>
        <p v-else-if="!isNotSyncable && item.total_device_manual && item.total_device_sync">
          <!-- {{ item.total_device_sync }}
          {{ item.total_device_sync > 1 ? 'Items' : 'Item' }} price synced,
          {{ item.total_device_manual }}
          {{ item.total_device_manual > 1 ? 'Items' : 'Item' }} manually priced -->
          Not all items in this category are price-synced.
        </p>
        <p class="w-60 text-xs" v-else>
          There are currently no pricing sources available for this item. You can price it manually by using the dropdown menu.
        </p>
      </div>
    </template>
  </Tooltip>
  <Tooltip v-else placement="top" :disabled="priceSharing">
    <ElCheckbox color="teal" true-label="1" false-label="0" disabled v-loading="isLoading" />
    <template #content>
      <div v-if="!priceScraper" class="w-60 text-xs">There are currently no pricing sources available for this item. You can price it manually by using the dropdown menu.</div>
      <div v-else class="w-60 text-xs">Accounts designated as scrapers cannot sync with the pricing source.</div>
    </template>
  </Tooltip>
  <div v-if="!priceSharing && !disabledAction.priceUploadByProgress">
    <Modal
      v-model="modal"
      hide-header
      hide-footer
      size="xs"
      no-close>
      <div class="flex items-center justify-center flex-col gap-y-8 my-6 -mx-8">
        <img src="@/assets/icon/attention-orange.svg" />
        <div class="space-y-2 text-center max-w-lg">
          <p class="font-semibold text-xl">{{ computedWording.title }}</p>
          <p class="text-sm text-grey-03">
            {{ computedWording.desc }}
          </p>
          <p
            v-if="!item.is_child && item.total_device_manual && item.total_device_sync"
            class="text-sm text-grey-03">
            Currently
            <b>
              {{ item.total_device_sync }}
              {{ item.total_device_sync > 1 ? 'Items' : 'Item' }} price synced,
              {{ item.total_device_manual }}
              {{ item.total_device_manual > 1 ? 'Items' : 'Item' }} manually priced
            </b>
          </p>
        </div>
        <div class="flex items-center justify-center gap-x-4">
          <Button
            color="teal"
            @click="handleConfirm">
            Yes, {{ synced == 1 ? 'Enable' : 'Disable' }}
          </Button>
          <Button
            color="teal"
            view="smooth"
            @click="handleCancel">
            Cancel
          </Button>
        </div>
      </div>
    </Modal>
  </div>
</template>

<script setup>
import { computed, ref, watch, onMounted } from 'vue'
import { useUser } from 'composable/user'
import { useRoute } from 'vue-router'
import { useDevice } from 'composable/pricing'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import { ElCheckbox } from 'element-plus'

const props = defineProps({
  is_available: Number | String,
  loading: {
    type: Boolean,
    default: false
  },
  isSyncable: Number | String,
  modelValue: {
    type: [Number, Boolean, String],
    default: 0
  },
  tooltip: {
    type: Boolean,
    default: false
  },
  item: {
    type: Object,
    default: () => undefined
  },
  disabled: {
    type: Boolean,
    default: false
  },
  priceSharing: {
    type: Boolean,
    default: false
  },
  priceScraper: {
    type: Boolean,
    default: false
  },
  disabledAction: {
    type: Object,
    default: undefined
  }
})

const emit = defineEmits(['update:modelValue', 'loaded-price'])

/* Remove This If API give parameter if prices exist or more than Zero */
const route = useRoute()
const {
  loading: loadingPrice,
  actionAll,
  actionSetLevel,
  actionManualPrice
} = useDevice(route.params.tab)
/* End Of Remove This If API give parameter if prices exist or more than Zero */

const wordings = ref({
  title: {
    disable_category: 'Disable Price Sync for all Items in this Category?',
    disable_child: 'Disable Price Sync for this Items?',
    enable_category: 'Enable Price Sync for all Items in this Category?',
    enable_child: 'Enable Price Sync for this Items'
  },
  description: {
    disable_category: 'Warning! Proceeding will require manual price updates for all items in this category. You will retain the most recently synced prices until then.',
    disable_child: 'Warning! Proceeding will require manual price updates for this items. You will retain the most recently synced prices until then.',
    enable_category: 'Warning! If you proceed, any manually set prices for items in this category will be overridden.',
    enable_child: 'Warning! If you proceed, any manually set prices for this items will be overridden.'
  }
})

const computedWording = computed(() => {
  if (synced.value == 1) {
    return {
      desc: props.item?.is_child ? wordings.value.description.enable_child : wordings.value.description.enable_category,
      title: props.item?.is_child ? wordings.value.title.enable_child : wordings.value.title.enable_category
    }
  }

  return {
    desc: props.item?.is_child ? wordings.value.description.disable_child : wordings.value.description.disable_category,
    title: props.item?.is_child ? wordings.value.title.disable_child : wordings.value.title.disable_category
  }
})

const computedTooltip = computed(() => {
  props.tooltip && (props.item.total_device_manual > 0 || props.item.total_device_sync > 0)
})

const available = computed(() => props.modelValue ? 1 : props.isSyncable)
const { me } = useUser()
const disallowSync = computed(() => me.value.is_price_sharing == 3)
const subDomainLinked = computed(() => me.value.sub_domain_linked)
const modal = ref(false)
const synced = ref(props.modelValue)
const priceExist = ref([]) // Remove This If API give parameter if prices exist or more than Zero

const isIndeterminateState = computed(() => {
  if (!props.item) return false
  const level = actionSetLevel(props.item)
  return (level === 'device' || props.item.model_as_device) &&
    props.item.total_device_sync > 0 &&
    props.item.total_device_manual > 0
})

const isLoading = computed(() =>
  props.loading || loadingPrice.all || loadingPrice.manual_price
)

const isNotSyncable = computed(() => {
  if (subDomainLinked.value !== null) {
    return props.item?.is_syncable == 0
  }
  return !subDomainLinked.value && (props.item?.is_syncable == 0 && !props.item?.is_price_sync)
})

const handleChange = async (value) => {
  let isManualExist = false
  if (value == 1 && (props.item?.sync_with_multiple_source || (!isIndeterminateState.value && !props.item?.sync_with_multiple_source))) {
    await actionManualPrice({ category_id: props.item.id }, (err, res) => {
      if (!err) isManualExist = res?.is_price_exist ?? false
    })
  }
  synced.value = value

  const conditionSubValue = !props.item?.is_price_sync && props.item?.conditions?.length
    ? [...props.item?.conditions]?.filter(
        (condition) =>
          !isNaN(condition.condition_sub_value) &&
          parseFloat(condition.condition_sub_value) > 0
      )
    : []

  if (
    (
      props.item.is_price_sync ||
      conditionSubValue?.length ||
      (
        !props.item.is_child &&
        props.item.total_device_manual &&
        props.item.total_device_sync
      ) ||
      isManualExist
    ) &&
    !props.priceSharing
  ) {
    modal.value = true
  } else {

    handleConfirm()
  }
}

const handleCancel = () => {
  modal.value = false
  synced.value = Number(!synced.value)
}

const handleConfirm = () => {
  modal.value = false
  emit('update:modelValue', Number(synced.value))
}

watch(
  () => props.modelValue,
  (val) => synced.value = val
)
</script>

<style scoped>
::v-deep(.el-checkbox__inner) {
  width: 16px !important;
  height: 16px !important;
}

/* Checked (✔) - only show when not disabled or when specifically set as checked */
::v-deep(.el-checkbox__input.is-checked .el-checkbox__inner::after) {
  top: 1.5px !important;
  left: 5px !important;
  transform: rotate(45deg) scaleY(1) !important;
}

/* Hide checkmark when disabled unless specifically checked */
::v-deep(.el-checkbox__input.is-disabled:not(.is-checked) .el-checkbox__inner::after) {
  opacity: 0 !important;
}

/* Indeterminate (-) */
::v-deep(.el-checkbox__input.is-indeterminate .el-checkbox__inner::before) {
  top: 6px !important;
}

/* Change border color when disabled */
::v-deep(.el-checkbox__input.is-disabled .el-checkbox__inner) {
  border-color: #dcdfe6 !important;
  background-color: #f5f7fa !important;
}
</style>