<template>
  <Modal
    v-model="show"
    size="lg"
    :title="!!Object.keys(form)?.length ? 'Edit Condition' : 'Add Condition'"
    @close="cancelCondition">
    <div>
      <div v-if="errorExist" class="mb-4 bg-red-01 rounded-lg px-6 py-4">
        Condition
      </div>
      <div class="grid md:grid-cols-5 grid-cols-1 gap-6">
        <div class="col-span-3 space-y-3">
          <div>
            <Input
              v-model="form.name"
              type="text"
              label="Condition Name"
              placeholder="e.g., Broken, Normal, New, etc."
              class="w-full"
              :view="(validation.name.$invalid && 'danger') || 'default'"
            />
            <span
              class="text-red-base mt-2 block"
              v-for="(error, n) of validation.name.$errors"
              :key="n">
              {{ error.$message }}.
            </span>
          </div>
          <div>
            <Input
              v-model="form.term"
              :disabled="!form.name"
              type="text"
              label="Condition Guideline"
              placeholder="e.g., Choose if ALL of the following are true"
              class="w-full"
              :view="(validation.term.$invalid && 'danger') || 'default'"
            />
            <span class="text-xs text-grey-04 mt-2 block">
              Maximum 200 character
            </span>
            <p
              v-for="(error, n) of validation.term.$errors"
              :key="n">
              <span
                v-if="error && error.$validator === 'minLength'"
                class="text-red-base mt-2 block">
                Title is required
              </span>
              <span
                v-else
                class="text-red-base mt-2 block">
                {{ error.$message }}
              </span>
            </p>
          </div>
          <div class="block">
            <label class="mb-2">Terms</label>
            <Draggable
              :list="form.terms"
              handle=".dragable__icon">
              <div
                v-for="(term, index) in form.terms"
                :key="index"
                class="condition__draggable py-2">
                <div class="flex justify-between items-center -mx-2">
                  <div class="pb-2">
                    <span class="material-icons text-grey-04 text-2xl mr-3 cursor-move dragable__icon">
                      drag_handle
                    </span>
                  </div>
                  <div class="px-2 flex-1 font-semibold">
                    <Input
                      v-model="form.terms[index]"
                      :disabled="!form.name"
                      placeholder="e.g., Activation Locked, Item Power On, etc.."
                      type="text"
                      class="mb-2"
                      block
                      :view="(validation.terms.$model[index].length < 1 && submitting && 'danger') || 'default'"
                    />
                    <div
                      v-if="validation.terms.$model[index].length < 1 && submitting">
                      <span class="text-red-base text-xs mt-2 block">
                        It must be at least one characters in length
                      </span>
                    </div>
                  </div>
                  <div class="pb-2 flex items-center -mx-1">
                    <div class="px-1 mx-6">
                      <Button
                        :disabled="form.terms.length === 1"
                        color="red"
                        dense
                        @click="deleteTerm(index)">
                        <span class="material-icons text-sm">delete</span>
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </Draggable>
            <span class="text-xs text-grey-04 block">
              ( Examples: Activation Locked, Item Power On, etc... )
            </span>
            <Button
              class="mt-4"
              view="outlined"
              color="teal"
              dense
              @click="createTerm">
              <span class="mx-2">Add Terms</span>
            </Button>
          </div>
        </div>
        <div class="col-span-2">
          <FormPreview
            :condition-name="form.name"
            :condition-guideline="form.term"
            :condition-terms="form.terms"
          />
        </div>
      </div>
    </div>
    <template #footer>
      <Button
        :loading="loading"
        :disabled="loading || form.terms?.length === 0"
        color="teal"
        @click="saveCondition">
        Save
      </Button>
    </template>
  </Modal>
</template>
<script>
import { ref, computed, defineComponent, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import { minLength } from '@vuelidate/validators'
import { isEmpty } from 'utils/is.js'
import { ElMessage } from 'element-plus'

import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { VueDraggableNext as Draggable } from 'vue-draggable-next'
import FormPreview from '../FormPreview.vue'

export default defineComponent({
  name: 'ConditionModalForm',
  components: {
    Modal,
    Input,
    Button,
    Draggable,
    FormPreview
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    conditions: {
      type: Array,
      default: undefined
    },
    condition: {
      type: Object,
      default: undefined
    },
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:modelValue',
    'save',
    'cancel'
  ],
  setup(props, ctx) {
    const show = computed({
      get: () => props.modelValue,
      set: (val) => {
        ctx.emit('update:modelValue', val)
      }
    })
    const form = ref({})
    const loading = ref(false)
    const errorExist = ref(false)
    const rules = {
      name: { required },
      term: { required },
      terms: {
        required,
        minLength: minLength(1),
        $each: {
          required,
          minLength: minLength(1)
        }
      }
    }
    const validation = useVuelidate(
      rules,
      form,
      { $lazy: true }
    )
    const submitting = ref(false)

    const createTerm = () => {
      submitting.value = false
      form.value.terms.push('')
    }
    const deleteTerm = (index) => {
      submitting.value = false
      form.value.terms.splice(index, 1)
    }
    const validateCondition = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const checkTermsValue = () => {
      const model = validation.value.terms.$model

      if (model.length <= 0) {
        return false
      }

      for (let i = 0; i < model.length; i++) {
        if (isEmpty(model[i]) || model[i].length < 1) {
          return false
        }
      }

      return true
    }
    const saveCondition = () => {
      submitting.value = true
      if (validateCondition() && checkTermsValue()) {
        const exists = !!props.conditions?.length ? [...props.conditions].filter(condition =>
          (condition.name.split(' ').join('').toLowerCase() === form.value.name.split(' ').join('').toLowerCase())
        ) : []

        if (!!exists.length && !form.value.id) {
          return ElMessage({
            type: 'warning',
            message: 'Condition name is already taken!'
          })
        }

        errorExist.value = false
        submitting.value = false
        ctx.emit('save', form.value)
      }
    }
    const cancelCondition = () => {
      submitting.value = false
      validation.value.$reset()
      ctx.emit('cancel')
    }

    watch(() => [show.value, props.isEdit], () => {
      if (!!show.value) {
        submitting.value = false
        validation.value.$reset()
      }

      if (!!show.value && !!props.isEdit) {
        form.value = {...props.condition}
      } else form.value = {
        name: '',
        term: '',
        terms: ['']
      }
    })

    return {
      show,
      form,
      loading,
      validation,
      submitting,
      errorExist,
      createTerm,
      deleteTerm,
      saveCondition,
      cancelCondition
    }
  }
})
</script>
