<template>
    <div>
        <div class="mb-8">
            <p class="font-semibold mb-4 text-dark">Set Condition Pricing</p>
            <Table :data="item.devices ? item.devices.condition_price : []" v-loading="loading">
                <TableColumn prop="name" label="Condition Name" width="150" />
                <TableColumn label="Set Percentage from Base Price" width="250">
                    <template #default="scope">
                        <Input v-model="scope.row.percentage" type="number" :disabled="disableInput" @focus-out="deviceUpdate(item, 'condition', scope.$index)" icon right block dense>
                            <template #icon>
                                <span class="text-base">%</span>
                            </template>
                        </Input>
                    </template>
                </TableColumn>
                <TableColumn prop="last_update" label="Last Update">
                    <template #default="scope">
                        <span>{{scope.row.last_update ? scope.row.last_update : '-'}}</span>
                    </template>
                </TableColumn>
            </Table>
        </div>
        <p class="font-semibold mb-4 text-dark">Price Preview</p>
        <Table :data="item.devices ? item.devices.specs : []" v-loading="loading">
            <TableColumn prop="name" label="Spec Category" width="150"/>
            <TableColumn prop="name" label="Spec name" width="150">
                <template #default="scope">
                    {{ scope.row.spec.spec_name }}
                </template>
            </TableColumn>
          <TableColumn prop="name" label="Spec (%)" width="100">
            <template #default="scope">
                <Input v-model="scope.row.spec.price" v-loading="loadingUpdate.specs.includes(`${item.id}-${scope.row.spec.id}`)" type="number" :disabled="disableInput || item.devices?.condition_price?.length === 0" @focus-out="deviceUpdate(item, 'spec', scope.$index)" icon right block dense>
                    <template #icon>
                        <span class="text-base">%</span>
                    </template>
                </Input>
            </template>
          </TableColumn>
            <template v-if="item.devices">
                <template v-for="(condition, index) in item.devices.condition_price" :key="index">
                    <TableColumn min-width="170">
                        <template #header>
                            <div class="flex justify-start items-center">
                                <div>{{ condition.name }}</div>
                                <div class="ml-2">
                                    <Popover :width="250" placement="bottom-start" trigger="hover">
                                        <h6 class="font-semibold text-base text-black mb-3">
                                            Condition: {{ condition.name }}
                                        </h6>
                                        <div v-if="condition?.terms">
                                            <p class="text-dark mb-2" v-html="JSON.parse(condition.terms)?.length > 1 ? sanitize(JSON.parse(condition.terms)[0]) : ''" />
                                            <ul class="list w-full">
                                                <template v-for="(rule, n) in JSON.parse(condition.terms)" :key="n">
                                                <li v-if="n !== 0" class="break-normal">{{ sanitize(rule) }}</li>
                                                </template>
                                            </ul>
                                        </div>
                                        <template #reference>
                                            <div class="text-teal-04 text-xs">
                                            <span class="material-icons text-lg">info</span>
                                            </div>
                                        </template>
                                    </Popover>
                                </div>
                            </div>
                        </template>
                        <template #default="scope">
                            <Currency :value="scope.row.spec.conditions[index].condition_value" />
                        </template>
                    </TableColumn>
                </template>
            </template>
        </Table>
    </div>
</template>
<script>
import { defineComponent, ref, getCurrentInstance, reactive, watch, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useDevice } from 'composable/pricing'
import { sanitize } from 'utils/dom'
import Input from 'bbUi/components/input/input.vue'
import Currency from 'components/Currency.vue'
export default defineComponent ({
    components: {
        Input,
        Currency
    },
    props: {
        productParent: {
            type: Object
        },
        item: {
            type: Object
        },
        disabled: {
            type: Boolean,
            default: false
        },
        loading: {
            type: Boolean,
        },
        basePrice: {
            type: [String, Number],
            default: 0
        }
    },
    setup(props) {
        const {emit} = getCurrentInstance()
        const route = useRoute()
        const item = ref(props.item)
        const disableInput = computed(() => props.disabled)
        const { actionUpdate } = useDevice(route.params.tab)
        const loadingUpdate = reactive({
            specs: []
        })

        const deviceUpdate = async (selectedItem, type, index) => {
            const specsData = selectedItem.devices.specs.map(item => ({spec_id: item.spec.id, percentage: item.spec.price}))
            const pricingData = selectedItem.devices.condition_price.map(item => ({condition_id: item.condition_id, percentage: item.percentage}))
            const pricing = {
                product_id: selectedItem.id,
                category_id: selectedItem.devices.mac_category_id,
                base_price: props.basePrice,
                pricing: pricingData,
                specs: specsData,
            }
            if(type === 'spec'){
                const loadingId = `${selectedItem.id}-${selectedItem.devices.specs[index].spec.id}`
                loadingUpdate.specs.push(loadingId)
                await actionUpdate({
                    pricing: JSON.stringify([pricing]),
                    products: props.productParent
                }, (response) => {
                    item.value.devices.specs[index].spec.price = response.data[index].price
                    item.value.devices.specs[index].spec.conditions = response.data[index].conditions
                })
                const loadingIndex = loadingUpdate.specs.findIndex(id => id === loadingId)
                if(loadingIndex > -1) loadingUpdate.specs.splice(loadingIndex, 1)
            }   
            else{
                await actionUpdate({
                    pricing: JSON.stringify([pricing]),
                    products: props.productParent
                })
                emit('getDevice', selectedItem)
            }
            
        }

        watch(() => props.item, (newValue) => {
            item.value = newValue
        })
        
        return {
            item,
            loadingUpdate,
            deviceUpdate,
            disableInput,
            sanitize
        }
    },
})
</script>