<template>
  <div
    v-if="!conditions && !loading"
    class="text-center bg-grey-01 bg-opacity-20 py-10 w-full">
    <div v-if="available" class="max-w-lg mx-auto mt-10 mb-6 space-y-6 text-grey-03">
      <img class="mx-auto" alt="not-found" src="/icon/settings-icon/catalog-not-found.svg" />
      <p class="leading-5 text-2xl">No Condition</p>
      <p class="leading-5 text-base">You don't have any conditions set yet. Before proceeding to pricing, please add conditions according to your needs.</p>
      <Button
        color="teal"
        view="outlined"
        @click="addConditionGroup">
        <div class="flex">
          Add Condition Group
          <span class="material-icons">arrow_right</span>
        </div>
      </Button>
    </div>
    <div v-else class="mx-auto mt-10 mb-6 space-y-6">
      <img class="mx-auto" alt="not-found" src="/icon/settings-icon/catalog-not-found.svg" />
      <p class="leading-5 text-2xl">No Items Found</p>
      <p class="leading-5 text-base">
        To set items condition, you first need to enable items in the Catalog.
        Click the <b>Go to Catalog Page</b> button below, switch on the toggle for the items you want to display, and then proceed to the Price Editor to set their conditions and prices
      </p>
      <Button
        link
        color="teal"
        view="outlined"
        :to="{ name: 'settingBuybackDevice' }">
        <div class="flex">
          Go to Catalog Page
          <span class="material-icons">arrow_right</span>
        </div>
      </Button>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'

import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'BulkPriceEditorMappingMethodEmpty',
  components: {
    Button
  },
  props: {
    conditions: {
      type: Number,
      default: 0
    },
    loading: {
      type: Boolean,
      default: false
    },
    available: {
      type: Boolean,
      default: false
    }
  },
  emits: ['add'],
  setup(props, ctx) {
    const addConditionGroup = () => ctx.emit('add')

    return {
      addConditionGroup
    }
  }
})
</script>