<template>
  <Modal
    v-model="show"
    title="Preview Condition"
    size="md"
    hide-footer>
    <PreviewCondition
      :conditions="conditions"
      class="mb-4"
    />
  </Modal>
</template>
<script>
import { computed, defineComponent } from 'vue'

import Modal from 'bbUi/components/modal/modal.vue'
import PreviewCondition from '../Preview.vue'

export default defineComponent({
  name: 'ConditionModalPreview',
  components: {
    Modal,
    PreviewCondition
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    condition: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue', 'update:conditions'],
  setup(props, ctx) {
    const show = computed({
      get: () => props.modelValue,
      set: (val) => {
        ctx.emit('update:modelValue', val)
      }
    })
    const conditions = computed(() => props.conditions || {})

    return {
      show,
      conditions
    }
  }
})
</script>