<template>
  <div class="space-y-4">
    <div class="relative">
      <FileUpload
        :accept="computedAccept"
        :config="fileUploadConfig"
        :files="files"
        :single-upload="computedAccept.length <= 1 && computedAccept.includes('xlsx')"
        offline
        @completed="handleCompleteUpload"
        @error="handleErrorUpload"
        @uploading="handleUploading"
        @remove="handleRemovedFile">
        <template #default="{ openDialog, acceptedFileTypes: accepteds }">
          <div class="bg-grey-01 bg-opacity-10 rounded-md p-8 flex flex-col items-center gap-4">
            <div
              class="flex items-center gap-1 flex-col text-grey-03"
              @click="openDialog">
              <svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 24 24" height="32px" viewBox="0 0 24 24" width="32px" fill="currentColor"><g><rect fill="none" height="24" width="24"/></g><g><path d="M18,15v3H6v-3H4v3c0,1.1,0.9,2,2,2h12c1.1,0,2-0.9,2-2v-3H18z M7,9l1.41,1.41L11,7.83V16h2V7.83l2.59,2.58L17,9l-5-5L7,9z"/></g></svg>
              <p class="text-grey-04 font-medium text-xl">Choose files or drag them here</p>
              <p v-if="!files?.length" class="text-xs">Upload multiple {{ accepteds }}</p>
              <p v-else class="text-xs">Upload {{ accepteds }}</p>
            </div>
            <Button
              color="teal"
              view="outlined"
              @click="openDialog">
              Browse Files
            </Button>
          </div>
        </template>
        <template #types="{ acceptedFileTypes }">
          <small class="block mt-4">
            <span class="font-semibold">File:</span> {{ acceptedFileTypes }}
          </small>
        </template>
        <template #file-list="{ files: fileList, removeFile, isEmptyFiles }">
          <div class="rounded-b-md bg-grey-01 bg-opacity-10">
            <div v-if="isEmptyFiles && files?.length > 0">
              <div class="px-7 text-grey-01">
                <hr />
              </div>
              <p class="text-sm font-medium">
                {{ files?.length }}
                {{ files?.length > 1 ? 'files' : 'file' }}
                selected
              </p>
              <div
                v-for="file in files"
                :key="file.name"
                :class="['file-list__item']"
              >
                <div class="file-list__file-icon pr-6">
                  <i class="material-icons">insert_drive_file</i>
                </div>
                <div class="relative w-full">
                  <div class="flex items-center">
                    <div class="file-list__file-name text-sm">{{ file.name }}</div>
                    <div class="file-list__file-size text-sm">
                      {{ fileSizeFormat(file.size) }}
                    </div>
                  </div>
                </div>
                <div class="file-list__file-remove pl-6">
                  <a @click.prevent="handleRemovedFile(null)"
                    ><i class="material-icons">cancel</i></a
                  >
                </div>
              </div>
            </div>
            <div v-else-if="!isEmptyFiles" class="px-4 pt-4 pb-6">
              <div class="px-2 text-grey-01 mb-8">
                <hr />
              </div>
              <p class="text-sm font-medium">
                {{ fileList?.length }}
                {{ fileList?.length > 1 ? 'files' : 'file' }}
                selected
              </p>
              <div
                v-for="file in fileList"
                :key="file.name"
                :class="[
                  'file-list__item',
                  'bg-opacity-50 rounded-lg',
                  !file.status.error ? 'bg-grey-01' : 'file-list__item--error',,
                  file.status.progress < 100 ? 'file-list__item--onprogress' : ''
                ]"
                :style="{
                  padding: file.status.progress < 100 ? `0.5rem 1.5rem 0.725rem 1.5rem` : `0.5rem 1.5rem`,
                }"
              >
                <div class="file-list__file-icon pr-2">
                  <i class="material-icons" style="font-size: 1.725rem !important;">insert_drive_file</i>
                </div>
                <div class="relative w-full">
                  <div class="flex items-center">
                    <div>
                      <div class="file-list__file-name text-sm">{{ file.name }}</div>
                    </div>
                    <div class="file-list__file-size text-sm text-grey-02">{{ file.size }}</div>
                  </div>
                  <transition name="fade">
                    <div
                      v-if="file.status.progress < 100"
                      class="file-list__progress-bar"
                      :style="{ width: `${file.status.progress}%` }"
                    ></div
                  ></transition>
                </div>
                <div class="file-list__file-remove pl-6">
                  <a @click.prevent="removeFile(file.id)"
                    ><i :class="!file.status.error && 'text-grey-04'" class="material-icons">cancel</i></a
                  >
                </div>
              </div>
            </div>
          </div>
        </template>
      </FileUpload>
      <div v-if="loading" class="absolute top-0 left-0 right-0 bottom-0 bg-white opacity-50 cursor-not-allowed" />
    </div>
    <div class="border border-grey-01 rounded-lg">
      <div
        class="p-4 flex items-center justify-between gap-4 cursor-pointer"
        @click="handlCollapse('alternates')">
        <span class="font-medium">Alternates</span>
        <span class="material-icons">expand_more</span>
      </div>
      <Transition name="slide-fade">
        <div v-if="collapses.alternates" class="p-4 space-y-4">
          <div class="bg-blue-01 bg-opacity-50 rounded-md flex gap-4 p-3 py-2 text-blue-03">
            <p class="text-sm">
              Update prices for items in your active catalog through
              <a
                href="https://docs.reusely.com/reference/reusely-api-overview"
                class="text-blue-03 hover:text-blue-03 underline"
                target="_blank">
                our API
              </a>
              <span
                class="ml-1 material-icons"
                style="line-height: 1;">
                open_in_new
              </span>
            </p>
          </div>
          <p>This is an example of how to integrate the API</p>
          <div
            v-for="(alternateTutorial, n) in alternateTutorials"
            :key="n"
            :class="[
              'bg-grey-01',
              'rounded-lg',
              !alternateTutorial.collapse ? 'bg-opacity-50' : 'bg-opacity-20',
            ]">
           <div
              :class="[
                'p-4 flex items-center',
                'justify-between gap-4',
                !alternateTutorial.collapse && 'cursor-pointer'
              ]"
              @click="alternateTutorial.collapse = !alternateTutorial.collapse">
              <span class="font-medium">{{ alternateTutorial?.title }}</span>
              <span class="material-icons">expand_more</span>
            </div>
            <Transition name="slide-fade">
              <div
                v-if="alternateTutorial.collapse"
                class="px-4 pb-4">
                <div>
                  <ol class="space-y-4">
                    <li
                      v-for="(howTo, o) in alternateTutorial?.howTo"
                      :key="`${n}_${o}`"
                      class="flex items-start gap-x-4">
                      <span class="bg-teal-04 w-8 h-8 flex items-center justify-center rounded-full text-white">{{ o + 1 }}</span>
                      <div class="flex-1 space-y-4">
                        <div v-if="howTo?.codes" v-html="howTo?.codes" />
                        <p v-if="howTo?.text" class="mt-0.5" v-html="howTo.text" />
                        <div v-if="howTo?.images?.length" class="flex">
                          <div class="flex-1 flex gap-4 items-start">
                            <img
                              v-for="image in howTo?.images"
                              :key="image"
                              :src="`/images/price-external-source/${image}`"
                              :alt="image"
                              class="w-auto h-auto"
                              style="max-width: 45%;"
                            >
                          </div>
                        </div>
                      </div>
                    </li>
                  </ol>
                </div>
              </div>
            </Transition>
          </div>
        </div>
      </Transition>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, ref } from 'vue'
import { PriceUploadTutorials } from 'constants/setting'

import FileUpload from 'components/FileUpload.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'PriceUploadImportData',
  components: {
    FileUpload,
    Button
  },
  props: {
    files: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['completed', 'error', 'remove', 'uploading'],
  setup(props, ctx) {
    const fileUploadConfig = reactive({
      url: '/v1/pos/pricing/upload-pricing',
      method: 'post',
      field: 'file'
    })
    const computedAccept = computed(() => {
      if (!!props.files.length) {
        const typeFile = props.files[0].name.split('.').pop()

        return [typeFile]
      }
      return ['csv', 'xlsx']
    })
    const collapses = ref({
      alternates: false
    })
    const alternateTutorials = ref(PriceUploadTutorials)
    const handleCompleteUpload = (files = []) => {
      ctx.emit('completed', files)
    }
    const handleErrorUpload = (errors = []) => {
      ctx.emit('error', errors)
    }
    const handleRemovedFile = (file = null) => {
      ctx.emit('remove', file)
    }
    const handleUploading = (e) => {
      ctx.emit('uploading', e)
    }
    const handlCollapse = (e) => {
      collapses.value[e] = !collapses.value[e]
    }

    return {
      computedAccept,
      fileUploadConfig,
      collapses,
      alternateTutorials,
      handlCollapse,
      handleUploading,
      handleCompleteUpload,
      handleErrorUpload,
      handleRemovedFile
    }
  }
})
</script>
