            class="my-4"
          >
            <div v-html="condition.condition_term[0]"></div>
            <ul class="my-4">
              <template
                v-for="(term, termIndex) of condition.condition_term"
                :key="termIndex"
              >
                <li v-if="termIndex !== 0" class="flex my-2">
                  <i class="material-icons text-green-04">check_circle</i>
                  <div v-html="term" class="mx-4"></div>
                </li>
              </template>
            </ul>
          </div>
          <div v-if="conditionIndex === 0" class="flex justify-center my-4">
            <button
              class="py-4 px-6 rounded-lg text-white font-semibold bg-blue-04"
              @click.prevent
            >
              Confirm Condition
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'
export default defineComponent({
  name: 'ConditionPreview',
  props: {
    conditions: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const conditions = computed(() => {
      try {
        return props.conditions.map((condition) => ({
          ...condition,
          condition_term: JSON.parse(condition.condition_term)
        }))
      } catch (err) {
        console.error(`Error parsing item condition: ${err}`)
        return []
      }
    })
    return {
      conditions
    }
  }
})
</script>
